<template>
    <div>
        <b-breadcrumb class="testTop">
            <b-breadcrumb-item>{{$t('common.youAreHere').toUpperCase()}}</b-breadcrumb-item>
            <b-breadcrumb-item active>
                {{$t('menuSalesInvoiceList')}}
            </b-breadcrumb-item>
        </b-breadcrumb>
        <p v-if="$route.params.id">{{ $route.params.id }}</p>
        <b-row>
            <b-col lg="12" xs="12">
                <Widget title="" collapse close customHeader>
                    <action-buttons
                        :addActive="commonShit.addAvailable"
                        :deleteActive="false"
                        :saveActive="false"
                        :nextActive="commonShit.activeRowIndex < (this.filteredList.length - 1)"
                        :previousActive="commonShit.activeRowIndex > 0"
                        showTooltips="invoice"
                        @actionAddNew="goToInvoices"
                        
                        @actionNext="nextRowCheck(mainList)"
                        @actionPrevious="previousRowCheck(mainList)"
                    />
                    <!--    @actionDelete="showDelConfirm(commonShit.activeRow)"
                            @actionSave="saveData" -->
                    <span v-if="commonShit.editMode">
                        <em>{{this.$t('common.selected') }}: <span class="text-info">{{ commonShit.activeRowMessage }}</span></em>
                    </span>
                    <b-tabs class="mb-lg" v-model="activeTab" no-key-nav>
                        <b-tab :title="$t(this.commonShit.pageMainApi + '.list')">
                            <div class="widget-middle-overflow">
                                <span @click="exportFilteredList">
                                    <ExportToExcelButton  :theList="exportedList" :columns="columns" :columnNames="options.headings" :listName="commonShit.pageMainApi">
                                    </ExportToExcelButton>
                                </span>
                                <v-client-table 
                                    :data="mainList" 
                                    :columns="columns" 
                                    :options="options"
                                    @row-click="editRow"
                                    ref="mainTable"
                                    :key="tableKey">
                                    <!-- <span slot="actions" slot-scope="{row}">
                                        <b-button
                                        @click="showDelConfirm(row)"
                                        type="button"
                                        class="btn btn-square btn-danger"
                                        >{{ $t('common.deleteSomething') }}</b-button>
                                    </span> -->
                                </v-client-table>
                            </div>
                        </b-tab>
                        <b-tab :title="$t(this.commonShit.pageMainApi + '.details')" :disabled="!commonShit.editMode">
<!-- ============================================================= MAIN FORM ======================================================================== -->
                            <b-form>
                                <fieldset>
                                    <b-form-group id="input-group-1" :label="$t('tableHeadings.invOfficeId')" label-for="officeId">
                                        <b-form-input id="officeId" name="officeId" placeholder=""
                                                    :class="{ 'form-control': true}"
                                                    v-model="form.invOfficeId"
                                                    :readonly="true"
                                        >
                                        </b-form-input>
                                    </b-form-group>
                                    <b-form-group id="input-group-2" :label="$t('tableHeadings.invCode')" label-for="invoiceCode">
                                        <b-form-input id="invoiceCode" name="invoiceCode" placeholder=""
                                                    :class="{ 'form-control': true}"
                                                    v-model="form.invCode"
                                                    :readonly="true"
                                        >
                                        </b-form-input>
                                    </b-form-group>
                                    <b-form-group id="input-group-3" :label="$t('tableHeadings.invStatus')" label-for="invoiceStatus">
                                        <b-form-input id="invoiceStatus" name="invoiceStatus" placeholder=""
                                                    :class="{ 'form-control': true}"
                                                    v-model="form.invStatus"
                                                    :readonly="true"
                                        >
                                        </b-form-input>
                                    </b-form-group>
                                    <b-form-group id="input-group-4" :label="$t('tableHeadings.invCost')" label-for="invoiceCost">
                                        <b-form-input id="invoiceCost" name="invoiceCost" placeholder=""
                                                    :class="{ 'form-control': true}"
                                                    v-model="form.invCost"
                                                    :readonly="true"
                                        >
                                        </b-form-input>
                                    </b-form-group>
                                    <b-form-group id="input-group-5" :label="$t('tableHeadings.invEmpIdDesc')" label-for="employeeName">
                                        <b-form-input id="employeeName" name="employeeName" placeholder=""
                                                    :class="{ 'form-control': true}"
                                                    v-model="form.invEmpIdDesc"
                                                    :readonly="true"
                                        >
                                        </b-form-input>
                                    </b-form-group>
                                    <b-form-group id="input-group-6" :label="$t('tableHeadings.invCustomerAnimalNameCustomer')" label-for="customerName">
                                        <b-form-input id="customerName" name="customerName" placeholder=""
                                                    :class="{ 'form-control': true}"
                                                    v-model="form.invCustomerAnimalNameCustomer"
                                                    :readonly="true"
                                        >
                                        </b-form-input>
                                    </b-form-group>
                                    <b-form-group id="input-group-7" :label="$t('tableHeadings.invCustomerAnimalNameAnimal')" label-for="animalName">
                                        <b-form-input id="animalName" name="animalName" placeholder=""
                                                    :class="{ 'form-control': true}"
                                                    v-model="form.invCustomerAnimalNameAnimal"
                                                    :readonly="true"
                                        >
                                        </b-form-input>
                                    </b-form-group>
                                    <b-form-group id="input-group-8" :label="$t('tableHeadings.invMethodPayDesc')" label-for="paymentMethod">
                                        <b-form-input id="paymentMethod" name="paymentMethod" placeholder=""
                                                    :class="{ 'form-control': true}"
                                                    v-model="form.invMethodPayDesc"
                                                    :readonly="true"
                                        >
                                        </b-form-input>
                                    </b-form-group>
                                    <b-form-group id="input-group-9" :label="$t('tableHeadings.invMethodPayDay')" label-for="paymentDay">
                                        <b-form-input id="paymentDay" name="paymentDay" placeholder=""
                                                    :class="{ 'form-control': true}"
                                                    v-model="form.invMethodPayDay"
                                                    :readonly="true"
                                        >
                                        </b-form-input>
                                    </b-form-group>
                                </fieldset>
                            </b-form>
                        </b-tab>
                        <b-tab :title="$t(this.commonShit.pageMainApi + '.services')" :disabled="!commonShit.editMode">
                            <v-client-table 
                                :data="invoiceServicesList" 
                                :columns="invoiceServicesColumns" 
                                :options="invoiceServicesOptions"
                                ref="invoiceServicesTable"
                                >
                            </v-client-table>
                        </b-tab>
                        <b-tab :title="$t(this.commonShit.pageMainApi + '.assortment')" :disabled="!commonShit.editMode">
                            <v-client-table 
                                :data="invoiceAssortmentList" 
                                :columns="invoiceAssortmentColumns" 
                                :options="invoiceAssortmentOptions"
                                ref="invoiceAssortmentTable"
                                >
                            </v-client-table>
                        </b-tab>
                    </b-tabs>
                </Widget>
            </b-col>
        </b-row>

    </div>
</template>

<script>
  import Widget from '@/components/Widget/Widget';
  // import { vueTableData, vueDatatableData } from './data';
  import ExportToExcelButton from "@/components/Custom/ExportToExcelButton.vue";
  import ActionButtons from "@/components/Custom/ActionButtons.vue";
  import axios from 'axios';
  import { commonMixin } from "@/core/commonMixin";
  import { axiosMixin } from "@/core/axiosMixin";
  import { doubleClickMixin } from "@/core/doubleClickMixin";
  import { saveChangesMixin } from "@/core/saveChangesMixin";
  import { mapGetters, mapActions } from 'vuex';

  export default {
    name: 'SalesHistory',
    components: { Widget, ExportToExcelButton, ActionButtons },
    mixins: [commonMixin, axiosMixin, doubleClickMixin, saveChangesMixin],
      data() {
        return {
          commonShit: {
            pageMainApi: 'invoice',
            // rest in commonMixin
          },
          form: {
            // rowId:'',
            invOfficeId:'',
            invCode:'',
            invStatus:'',
            invCost:'',
            invEmpId:'',
            invEmpIdDesc:'',
            invCustomerAnimalId:'',
            invCustomerAnimalNameCustomer:'',
            invCustomerAnimalNameAnimal:'',
            invMethodPayId:'',
            invMethodPayDesc:'',
            invMethodPayDay:'',
          },
            tableKey: '',
                
            columns: ['invOfficeId', 'invCode', 'invStatus', 'invCost','invEmpIdDesc', 'invCustomerAnimalNameCustomer', 'invCustomerAnimalNameAnimal', 'invMethodPayDesc', 'invMethodPayDay'],
            options: {
              filterByColumn: true,
              perPage: 10,
              perPageValues: [],
              pagination: { chunk: 10, dropdown: false },
              skin: 'table table-striped table-hover',
              headings: {
                invOfficeId: 'Office ID',
                invCode: 'Code',
                invCost: 'Cost',
                invStatus: 'Status',
                invEmpIdDesc: 'Employee',
                invCustomerAnimalNameCustomer: 'Customer',
                invCustomerAnimalNameAnimal: 'Animal',
                invMethodPayDesc: 'Payment Method',
                invMethodPayDay: 'Payment Day',
                actions: ''

              },
              filterable: ['invOfficeId', 'invCode', 'invStatus', 'invCost', 'invEmpIdDesc', 'invCustomerAnimalNameCustomer', 'invCustomerAnimalNameAnimal', 'invMethodPayDesc', 'invMethodPayDay'],
              sortable: ['invOfficeId', 'invCode', 'invStatus', 'invCost', 'invEmpIdDesc', 'invCustomerAnimalNameCustomer', 'invCustomerAnimalNameAnimal', 'invMethodPayDesc', 'invMethodPayDay'],
            },
            invoiceServicesList: [],
            invoiceServicesColumns: ['invserviceDesc', 'invserviceQty', 'invservicePriceBrutto', 'invserviceDiscount'],
            invoiceServicesOptions: {
              rowClassCallback(row) { return  (row.rowActive == true) ? 'activeRow' : 'hoverhighlight'},
              texts: {},
              filterByColumn: true,
              perPage: 10,
              perPageValues: [],
              pagination: { chunk: 10, dropdown: false },
              skin: 'table table-striped table-hover',
              headings: {
                invserviceDesc: 'Service',
                invserviceQty: 'Quantity',
                invservicePriceBrutto: 'Price',
                invserviceDiscount: 'Discount',
                actions: ''

              },
              filterable: ['invserviceDesc', 'invserviceQty', 'invservicePriceBrutto', 'invserviceDiscount'],
              sortable: ['invserviceDesc', 'invserviceQty', 'invservicePriceBrutto', 'invserviceDiscount'],
            },
            invoiceAssortmentList: [],
            invoiceAssortmentColumns: ['invassortDesc', 'invassortQty', 'invassortPriceNetto', 'invassortPortion', 'invassortFreq', 'invassortHowLong','invassortDiscount'],
            invoiceAssortmentOptions: {
                rowClassCallback(row) { return  (row.rowActive == true) ? 'activeRow' : 'hoverhighlight'},
                texts: {},
                filterByColumn: true,
                perPage: 10,
                perPageValues: [],
                pagination: { chunk: 10, dropdown: false },
                skin: 'table table-striped table-hover',
                headings: {
                    invassortDesc: 'Assortment',
                    invassortQty: 'Quantity',
                    invassortPriceNetto: 'Price',
                    invassortDiscount: 'Discount',
                    invassortPortion: 'Portion',
                    invassortFreq: 'Frequency ??',
                    invassortHowLong: 'Duration',
                    actions: ''

                },
                filterable: ['invassortDesc', 'invassortQty', 'invassortPriceNetto', 'invassortPortion', 'invassortFreq', 'invassortHowLong','invassortDiscount'],
                sortable: ['invassortDesc', 'invassortQty', 'invassortPriceNetto', 'invassortPortion', 'invassortFreq', 'invassortHowLong','invassortDiscount'],
            },
        };
      },
      computed: {
          ...mapGetters([
              // 'cacheExists',
              // 'getCache',
              // 'cacheItemsCount',
              // 'cacheLastUpdate',
              // 'cacheGetRowById',
              'getActiveOnArrival',
          ]),
      },
      methods: {
          ...mapActions([
          // 'cacheReset',
          // 'cacheFill',
          // 'cacheAddItem',
          // 'cacheDeleteItem',
          // 'cacheUpdateItem',
          // 'cacheSetUpdatedDate',
          // 'cacheUpdate',
          'setActiveOnArrival',
          'resetActiveOnArrival'
    ]),
    // ============================================================= REFRESHING LIST =========================================
          async refreshMainTable() {
                  var result = await this.axiosGetData(this.commonShit.pageMainApi, this.axeman);
                  if (this.axeman.success == true) {
                      this.mainList = this.formatDataForListing(this.axeman.gotFromApi);
                      this.checkOnArrival();
                      if (!this.commonShit.editMode) this.deactivateAllRows(this.mainList);
                  }
              this.axemanReset();
          },
          formatDataForListing(data) {
          // var index;
          
          // for (var i = 0; i < data.length; i++) {
          //     data[i].equipWarrantyData = data[i].equipWarrantyData.split('T')[0];
          //     index = this.equipmentTypes.findIndex(x => x.rowid === data[i].equipTypeId);
          //     data[i].equipTypeWord = this.equipmentTypes[index].key_Code;
          //     data[i].a
          // }
          return data;
      },
    // ============================================================= ACTIVATING =========================================
  editRow(row) {
    // console.log(rowId);
        var mainTableIndex;
        this.filteredList = this.$refs.mainTable.allFilteredData;

        // console.log(this.filteredList);
        this.commonShit.activeRowIndex = this.filteredList.findIndex(x => x.rowId === row.row.rowId); // not supported in IE
        mainTableIndex = this.mainList.findIndex(x => x.rowId === this.filteredList[this.commonShit.activeRowIndex].rowId);
        this.rowIsActivated(this.mainList, mainTableIndex);
        if (this.isClickedTwice(row.row.rowid)) {
            this.execDoubleClick(row, 1); // doubleClickMixin
            return;
        }
        this.rowClicked(row.row.rowid); // doubleClickMixin
    },
  nextRow(table) {
    var mainTableIndex;
    if (this.commonShit.activeRowIndex !== null && this.commonShit.activeRowIndex !== undefined) {
        this.commonShit.activeRowIndex++;
        if (this.commonShit.activeRowIndex >= this.filteredList.length) this.commonShit.activeRowIndex = this.filteredList.length-1;
        else {
            mainTableIndex = table.findIndex(x => x.rowId === this.filteredList[this.commonShit.activeRowIndex].rowId);
            this.rowIsActivated(table, mainTableIndex);
        }
        
    }
  },
  previousRow(table) {
    var mainTableIndex;
    if (this.commonShit.activeRowIndex !== null && this.commonShit.activeRowIndex !== undefined) {
        this.commonShit.activeRowIndex--;
        if (this.commonShit.activeRowIndex < 0) this.commonShit.activeRowIndex = 0;
        {
            mainTableIndex = table.findIndex(x => x.rowId === this.filteredList[this.commonShit.activeRowIndex].rowId);
            this.rowIsActivated(table, mainTableIndex);
        }
    }
  },
  rowIsActivated(table, index) {
    this.clearForm();
    var row = table[index];
    this.commonShit.activeRow = JSON.parse(JSON.stringify(row));
    this.commonShit.editMode = true;
    this.commonShit.activeRowId = row.rowId;
    console.log(row);
    
    this.formFill(row);
    this.formCloneCreate(this.form, 100);
    this.deactivateAllRows(table);
    this.activateRow(table[index]);
    this.setActiveMessage(row);
    this.getInvoiceServices();
    this.getInvoiceAssortment();
    // specific
      
  },
// ============================================================= FORM HANDLING =========================================
      clearForm(e) {
          if (e !== undefined) e.preventDefault();   
          for (var prop in this.form){
              if(this.form.hasOwnProperty(prop)){
                  this.form[prop] = '';
              }
          }
          this.commonShit.editedRowId = '',
          this.commonShit.editMode = false;
          this.commonShit.formClone = "";
          this.$validator.reset();
          // form specific
          this.form.equipTypeId = null;
      },
      formFill(data) {
          for (var prop in this.form){
              if(this.form.hasOwnProperty(prop) && data.hasOwnProperty(prop)){
                  this.form[prop] = data[prop];
              }
          }
      },
// ============================================================= PAGE SPECIFIC =========================================
      saveChangesCondition() {
      return (this.commonShit.editMode && this.formCloneCompare(this.form) == false);
      },
      dontSaveChanges() {
          this.formReverseChanges(this.form);
          //this.formCloneCreate(this.form, 0);
      },
      setActiveMessage(row) {  
          this.commonShit.activeRowMessage = row.invCode; // + ' [' + row.equipTypeWord + ']';
      },
      delCustomMessage(row) {
          return row.equipName;
      },
      async getInvoiceServices() {
          var invoices = {fake: 0};
          if(this.commonShit.activeRowId !== null) {
              var result = await this.axiosGetData('/Invoice/GetInvoiceServices/' + this.commonShit.activeRowId, invoices);
              if (this.axeman.success == true) {
                  this.invoiceServicesList = invoices.gotFromApi;
                  // console.log(this.animalsList);
              } else {
                  console.log(this.axeman.error);
              }
          }   
      },
      async getInvoiceAssortment() {
          var invoices = {fake: 0};
          if(this.commonShit.activeRowId !== null) {
              var result = await this.axiosGetData('/Invoice/GetInvoiceAssortments/' + this.commonShit.activeRowId, invoices);
              if (this.axeman.success == true) {
                  this.invoiceAssortmentList = invoices.gotFromApi;
                  // console.log(this.animalsList);
              } else {
                  console.log(this.axeman.error);
              }
          }   
      },
      goToInvoices() {
          this.$router.push('Sales');
      },
      checkOnArrival() {
      if(this.getActiveOnArrival.list == 'invoice') {
          var invoiceId = this.getActiveOnArrival.id;
          var index = this.mainList.findIndex(x => x.rowId === invoiceId); // not supported in IE
          if (index !== -1) {
              
              this.commonShit.activeRowIndex = index;
              this.rowIsActivated(this.mainList, index);
              var self = this;
              setTimeout(function(){
                  self.activeTab = 1;
                  self.filteredList = self.$refs.mainTable.allFilteredData;
                  // console.log('FAK: ' + self.filteredList);
              }, 100);
          }
          this.resetActiveOnArrival();
      }
  },
// ============================================================= HEADINGS =========================================
          setTableHeadings() {
              this.options.headings.invOfficeId = this.$t("tableHeadings.invOfficeId");
              this.options.headings.invCode = this.$t("tableHeadings.invCode");
              this.options.headings.invCost = this.$t("tableHeadings.invCost");
              this.options.headings.invStatus = this.$t("tableHeadings.invStatus");
              this.options.headings.invEmpIdDesc = this.$t("tableHeadings.invEmpIdDesc");
              this.options.headings.invCustomerAnimalNameCustomer = this.$t("tableHeadings.invCustomerAnimalNameCustomer");
              this.options.headings.invCustomerAnimalNameAnimal = this.$t("tableHeadings.invCustomerAnimalNameAnimal");
              this.options.headings.invMethodPayDesc = this.$t("tableHeadings.invMethodPayDesc");
              this.options.headings.invMethodPayDay = this.$t("tableHeadings.invMethodPayDay");

              this.invoiceServicesOptions.headings.invserviceDesc = this.$t("servic.singular");
              this.invoiceServicesOptions.headings.invserviceQty = this.$t("sales.quantity");
              this.invoiceServicesOptions.headings.invservicePriceBrutto = this.$t("tableHeadings.price");
              this.invoiceServicesOptions.headings.invserviceDiscount = this.$t("sales.discount");
              this.invoiceServicesOptions.texts = this.options.texts;

              this.invoiceAssortmentOptions.headings.invassortDesc = this.$t("assortment.name");
              this.invoiceAssortmentOptions.headings.invassortQty = this.$t("sales.quantity");
              this.invoiceAssortmentOptions.headings.invassortPriceNetto = this.$t("tableHeadings.price");
              this.invoiceAssortmentOptions.headings.invassortDiscount = this.$t("sales.discount");
              this.invoiceAssortmentOptions.headings.invassortPortion = 'Portion';
              this.invoiceAssortmentOptions.headings.invassortFreq = 'Frequency ??';
              this.invoiceAssortmentOptions.headings.invassortHowLong = this.$t("tableHeadings.duration");
              this.invoiceAssortmentOptions.texts = this.options.texts;
          }
      },

      async created () {
          //debugger;
          // console.log('%c====  ' + this.commonShit.pageMainApi + '  ====', 'font-weight: bold; background-color: black; color:lightblue;');
          //   let route = this.$route.params.id;
          
          axios.defaults.headers.common["Authorization"] = "Bearer " + localStorage.token;

          // var result = await this.refreshType("equipmentType");
          // this.equipmentTypes = JSON.parse(JSON.stringify(this.temp));
          
          this.refreshMainTable();
      },

      updated() {
          this.setTableHeadings();
      },
  };
</script>

<style src="./SalesHistory.scss" lang="scss" scoped />