<template>
  <b-modal
      centered
      :title="$t('common.areYouSure')"
      
      v-model="showMe"
      no-close-on-backdrop
      hide-header-close
      content-class="darkModal"
      footer-class="centered"
    >
         <h4 class="my-4 text-center">
            {{ $t('common.deleteSomething')}} <strong>{{ message }}</strong>, ID: {{ rowId }}
        </h4>
        <template slot="modal-footer">
        <b-row>
        <b-col cols="auto">
          <b-button @click="deletionConfirmed" class="btn btn-square btn-danger">{{ delWhat }}</b-button>
        </b-col>
        <b-col cols="auto">
          <b-button variant="secondary" @click="deletionCancelled" >{{ $t('common.dontDeleteSomething') }}</b-button>
        </b-col>
      </b-row>
        </template>
  </b-modal>
</template>

<script>

export default {
    name: "DelConfirmModal",
    
    props: {
        showMe: Boolean,
        delWhat: String,
        message: String,
        rowId: Number
    },
    
    methods: {
        deletionConfirmed() {
            this.$emit('delConfirmed', this.rowId);
        },
        deletionCancelled() {
            this.$emit('delCancelled');
        }
    }
}
</script>

