export const doubleClickMixin = {
    data() {
        return {
            dc: {
                rowClickedOnce: false,
                rowId: null,
            },
            
        }
    },
    methods: {
        isClickedTwice(rowId) {
            return (this.dc.rowClickedOnce && this.dc.rowId == rowId)
        },
        rowClicked(rowId) {
            this.dc.rowClickedOnce = true;
            this.dc.rowId = rowId;
            var self = this;
            setTimeout(function(){ self.dc.rowClickedOnce= false; self.dc.rowId = null}, 300);
        },
        execDoubleClick(row, tabNumber) {
            this.activeTab = tabNumber;
            this.dc.rowClickedOnce= false; 
            this.dc.rowId = null
        },
        
    }
}