<template>
  <div class="actionButtons">
      <span v-b-tooltip.hover :title="$t(showTooltips + '.addNew')" class="actionButtonContainer">
        <b-button @click="actionAddNew" id="actionAdd" type="button" variant="primary" :disabled="!addActive" class="actionButton">
            <span class="glyphicon glyphicon-plus mr-xs mb-xs"></span>
        </b-button>
      </span>
    &nbsp;
    <span v-b-tooltip.hover :title="$t('common.saveChangesYes')" class="actionButtonContainer">
    <b-button @click="actionSave" id="actionSave" type="button" variant="success" :disabled="!saveActive" class="actionButton">
        <span class="glyphicon glyphicon-hdd mr-xs mb-xs"></span>
    </b-button>
    </span>
    &nbsp;
    <span v-b-tooltip.hover :title="$t(showTooltips + '.delete')" class="actionButtonContainer">
    <b-button @click="actionDelete" id="actionDelete" type="button" variant="danger" :disabled="!deleteActive" class="actionButton">
        <span class="glyphicon glyphicon-trash mr-xs mb-xs"></span>
    </b-button>
    </span>
    &nbsp;&nbsp;&nbsp;
    <span v-b-tooltip.hover :title="$t(showTooltips + '.previous')" class="actionButtonContainer">
    <b-button @click="actionPrevious" id="actionPrev" type="button" variant="secondary" :disabled="!previousActive" class="actionButton">
        <span class="glyphicon glyphicon-chevron-left mr-xs mb-xs"></span>
    </b-button>
    </span>
    &nbsp;
    <span v-b-tooltip.hover :title="$t(showTooltips + '.next')" class="actionButtonContainer">
    <b-button @click="actionNext" id="actionNext" type="button" variant="secondary" :disabled="!nextActive" class="actionButton">
        <span class="glyphicon glyphicon-chevron-right mr-xs mb-xs"></span>
    </b-button>
    </span>
    &nbsp;&nbsp;&nbsp;
    <span v-b-tooltip.hover :title="$t(showTooltips + '.print')" class="actionButtonContainer">
    <b-button @click="actionPrint" id="actionPrint" type="button" variant="secondary" :disabled="!printActive" class="actionButton">
        <span class="glyphicon glyphicon-print mr-xs mb-xs"></span>
    </b-button>
    </span>
    <!-- tooltips -->
    <!-- <div>
    <b-tooltip target="actionAdd" triggers="hover">
        {{ $t(showTooltips + '.add') }}
    </b-tooltip>
    <b-tooltip target="actionDelete" triggers="hover">
        {{ $t(showTooltips + '.delete') }}
    </b-tooltip>
    </div> -->
  </div>
</template>

<script>
export default {
    name: "ActionButtons",
    props: {
        addActive: Boolean,
        saveActive: Boolean,
        deleteActive: Boolean,
        nextActive: Boolean,
        previousActive: Boolean,
        printActive: Boolean,
        showTooltips: String,
    },
    data() {
        return {
            addButtonActive: '', 
        }
    },
    methods: {
        updateActiveStates() {
            this.addButtonActive = '';
            console.log('Actions updated');
        },
        actionAddNew() {
            this.$emit('actionAddNew');
            console.log(this.showTooltips);
        },
        actionDelete() {
            this.$emit('actionDelete');
        },
        actionSave() {
            this.$emit('actionSave');
        },
        actionNext() {
            this.$emit('actionNext');
        },
        actionPrevious() {
            this.$emit('actionPrevious');
        },
        actionPrint() {
            this.$emit('actionPrint');
        }
        
    },
    // watch: {
    //     'activeState': function() {
    //         this.updateActiveStates();
    //     }
           
    // }

}
</script>

<style>
 .actionButtons {
     margin-bottom: 1em;
     display: inline-block;
     margin-right: 3em;
 }
 .actionButtonContainer {
     display:inline-block;
     margin: 0;
 }
</style>