<template>
  <div class="gallery-page">
    <b-breadcrumb class="testTop">
      <b-breadcrumb-item>YOU ARE HERE</b-breadcrumb-item>
      <b-breadcrumb-item active>
          {{$t('menuProfilGallery')}}
      </b-breadcrumb-item>
    </b-breadcrumb>

    <div class="d-flex justify-content-between align-items-center">
      <b-button-group class="mb-lg">
        <b-button variant="default" @click="changeCategory(null)"
          :class="{ active: activeCategory === null }"
        >All</b-button>
        <b-button variant="default" @click="changeCategory('nature')"
          :class="{ active: activeCategory === 'nature' }"
        >Nature</b-button>
        <b-button variant="default" @click="changeCategory('people')"
          :class="{ active: activeCategory === 'people' }"
        >People</b-button>
        <b-button variant="default" @click="changeCategory('space')"
          :class="{ active: activeCategory === 'space' }"
        >Space</b-button>
      </b-button-group>
      <b-form-group style="min-width: 350px">
        <b-input-group class="fileinput fileinput-new">
          <input accept="image/*"
            @change="onChangeInputFiles"
            id="fileupload1" ref="imageFileInput"
            type="file" name="file" class="display-none" style="display:none"
          />
          <label for="fileupload1" class="form-control" style="border-top-left-radius: 4px; border-bottom-left-radius: 4px;">
            <div v-if="inputFiles.length > 0">
              <span v-for="file in inputFiles"
                :key="`select-id-${file.name.split('.')[0].toString()}`"
                
              >
                {{file.name}}
              </span>
            </div>
            <span v-else />
          </label>
          <b-input-group-append v-if="inputFiles.length === 0">
            <b-button type="button" variant="default" class="btn-file" @click="$refs.imageFileInput.click()">
              {{ $t('files.select') }}
            </b-button>
          </b-input-group-append>
          <b-input-group-append v-else>
            <b-button type="button" variant="primary">
              {{ $t('files.upload') }}
            </b-button>
            <b-button type="reset" variant="default"
              @click="removeFiles"
            >
              <label>{{ $t('files.remove') }}</label>
            </b-button>
          </b-input-group-append>
        </b-input-group>
      </b-form-group>
      <b-button-group class="mb-lg">
        <b-button variant="default" @click="desc = false"
          :class="{ active: desc === false }"
        ><i class="fa fa-sort-numeric-asc"></i></b-button>
        <b-button variant="default" @click="desc = true"
          :class="{ active: desc }"
        ><i class="fa fa-sort-numeric-desc"></i></b-button>
      </b-button-group>
      
    </div>
    
    <gallery :images="images.map(img => img.src)" :index="index" @close="index = null" />
    <transition-group name="flip-list" class="gallery" tag="ul">
      <li
        v-for="(image, imageIndex) in getImages()"
        :key="image.id"
        @click="index = imageIndex"
        class="picture card"
      >
        <div class="img-wrapper">
          <img class="figure-img width-200" :src="image.src" alt="..." />
        </div>
        <div class="description">
          <h6 class="mt-0 mb-xs">{{image.name}}</h6>
          <ul class="post-links">
            <li><a href="#">{{image.date}}</a></li>
            <li><a href="#"><span class="text-danger">
              <i :class="`fa ${image.like ? 'fa-heart' : 'fa-heart-o'}`" /> Like
            </span></a></li>
            <li><a href="#">Details</a></li>
          </ul>
        </div>
      </li>
    </transition-group>
  </div>
</template>

<script>
import Vue from 'vue';
import Widget from '@/components/Widget/Widget';
import Gallery from 'vue-gallery';

export default {
  name: 'GalleryPage',
  components: { Gallery, Widget },
  data() {
    return {
      activeCategory: null,
      images: [
        {
          id: 0,
          name: 'Dawg',
          groups: [
            'nature',
          ],
          src: require('../../../assets/pictures/1.jpg'), // eslint-disable-line
          date: '10 mins',
        },
        
      ],
      index: null,
      desc: null,
      inputFiles: [],
    };
  },
  methods: {
    onChangeInputFiles(e) {
      const files = [];
      let i = 0;
      for (i; i < e.target.files.length; i += 1) {
        files.push(e.target.files[i]);
      }

      Vue.set(this, 'inputFiles', files);
    },
    removeFiles() {
      Vue.set(this, 'inputFiles', []);
      this.$refs.imageFileInput.value = null;
    },
    changeCategory(category) {
      this.activeCategory = category;
    },
    getImages() {
      const images = this.images.slice();
      images.sort((a, b) => {
        /* eslint-disable */ 
        if (this.desc !== null) {
          if (a.name < b.name) {
            return this.desc ? 1 : -1;
          } else if (a.name > b.name) {
            return this.desc ? -1 : 1;
          } else {
            return 0;
          }
        }
         /* eslint-enable */

        return false;
      });

      if (this.activeCategory) {
        return images
          .filter(img => img.groups.indexOf(this.activeCategory) !== -1);
      }

      return images;
    },
  },
};
</script>

<style src="./Gallery.scss" lang="scss" scoped />
