export const helpersMixin = {
    data() {
        return {

        }
    },
    methods: {
        capitalizeFirstLetter(string) {
            return string.charAt(0).toUpperCase() + string.slice(1);
        },
        formatAddress(street, house, flat, locale) {
            var result = '';
            if (locale == 'pl') {
            result = house;
            if (flat != '' && flat != null)
                    result += ' / ' + flat;
            result = street + ' ' + result;
            }
            return result;
        },

        getDropdownItemLabel(dropdownList, code) {
            var index = dropdownList.findIndex(x => x.code === code);
            if (index >= 0 && index < dropdownList.length)
                return dropdownList[index].label;
            else 
                return code;
        }
    }

}