<template>
<div :class="{root: true, sidebarClose, myLightTheme: (getStyleTheme == 'lightTheme')}" @click="pageClick(sessionSeconds)">
  <Helper />
  <Header />
  <Sidebar />
  <div ref="content" class="content animated fadeInUp">
    <transition name="router-animation">
      <router-view />
    </transition>
  </div>
  <footer class="contentFooter">
    <!-- Light Blue Vue Admin Dashboard Template - Made by <a href="https://flatlogic.com" target="_blank">Flatlogic</a> -->
  </footer>
  <div id="backgroundCover" :class="{myLightTheme:(getStyleTheme == 'lightTheme')}" ></div>
</div>
</template>

<script>
import { mapState, mapActions, mapGetters } from 'vuex';

import Sidebar from '@/components/Sidebar/Sidebar';
import Header from '@/components/Header/Header';
import Helper from '@/components/Helper/Helper';

import './Layout.scss';

export default {
  name: 'Layout',
  components: { Sidebar, Header, Helper },
  data() {
    return {
      timeout: null,
      sessionSeconds: 600,
    }
  },
  methods: {
    ...mapActions('layout',['switchSidebar', 'changeSidebarActive'],),
    ...mapActions('auth', ['setLogoutTimer']),

    pageClick(seconds){
      // console.log('session ends in: ' + seconds);
      
      
      let self = this;
      if (this.timeout) {
        clearTimeout(this.timeout);
        // console.log(this.timeout);
      }
      
      
      if (this.timeout == null) {
          this.timeout = setTimeout(() => {
          //Log out the user
          self.setLogoutTimer(0);
        }, seconds * 1000);
        // console.log(this.timeout);
      }
      
    }
  },
  computed: {
    ...mapState('layout', {
      sidebarClose: state => state.sidebarClose,
    }),
    ...mapGetters([
     'getStyleTheme',
    ]),
  },
  created() {
    this.pageClick(this.sessionSeconds);
  },
  mounted() {
    this.$refs.content.addEventListener('animationend', () => {
      this.$refs.content.classList.remove('animated');
      this.$refs.content.classList.remove('fadeInUp');
    });
  }
};
</script>

<style src="./Layout.scss" lang="scss" />
