<template>
    <div>
        <b-breadcrumb class="testTop">
            <b-breadcrumb-item>{{ $t('common.youAreHere').toUpperCase() }}</b-breadcrumb-item>
            <b-breadcrumb-item active>
                {{$t('menuWarehouseNewItem')}}
            </b-breadcrumb-item>
        </b-breadcrumb>

        <p v-if="$route.params.id">{{ $route.params.id }}</p>
        
        <b-row>
            <b-col lg="12" xs="12">
                
                <Widget customHeader close collapse fullscreen>
                    <b-row>
                    <b-col lg="8" xs="12">
                    <action-buttons
                        :addActive="commonShit.addAvailable"
                        :deleteActive="commonShit.editMode"
                        :saveActive="commonShit.saveAvailable"
                        :nextActive="commonShit.activeRowIndex < (this.filteredList.length - 1)"
                        :previousActive="commonShit.activeRowIndex > 0"
                        showTooltips="assortment"
                        @actionAddNew="showAddNew"
                        @actionDelete="showDelConfirm(commonShit.activeRow)"
                        @actionSave="saveData"
                        @actionNext="nextRowCheck(mainList)"
                        @actionPrevious="previousRowCheck(mainList)"
                    />
                    <span v-if="commonShit.editMode">
                        <em>{{this.$t('common.selected') }}: <span class="text-info">{{ commonShit.activeRowMessage }}</span></em>
                    </span>
                <b-tabs class="mb-lg" v-model="activeTab" no-key-nav>
                    <b-tab :title="$t(commonShit.pageMainApi + '.list')" active>
                        <!-- <b-button @click="showAddNew" type="button" variant="primary">
                            {{ $t('assortment.addNew') }}
                        </b-button> -->
                        <div class="widget-middle-overflow">
                        <span @click="exportFilteredList">
                            <ExportToExcelButton  :theList="exportedList" :columns="columns" :columnNames="options.headings" :listName="commonShit.pageMainApi">
                            </ExportToExcelButton>
                        </span>
                        <v-client-table  :data="mainList" :columns="columns" :options="options"
                                        id="assortmentTable" ref="mainTable"
                                    @row-click="editRow"
                        >
                            <!-- <span slot="actions" slot-scope="{row}">
                                <b-button
                                @click="showDelConfirm(row)"
                                type="button"
                                class="btn btn-square btn-danger"
                                >{{ $t('common.deleteSomething') }}</b-button>
                            </span> -->
                        </v-client-table>
                        </div>
                    </b-tab>
                    <b-tab :title="$t(commonShit.pageMainApi + '.details')" :disabled="!detailsTabEnabled">
                        <button @click="commonShit.validationOn = !commonShit.validationOn">Validation: {{ commonShit.validationOn }}</button>
                <!-- <Widget customHeader close collapse fullscreen> -->
<!-- ============================================================= MAIN FORM ======================================================================== -->
                <b-form>
                    <fieldset>
                        <b-form-group id="input-group-1" :label="$t('assortment.name')" label-for="assortmentName">
                            <b-form-input id="assortmentName" name="assortmentName" placeholder=""
                                        :class="{ 'form-control': true, 'is-invalid': errors.has('assortmentName')}"
                                        v-validate="'required'" data-vv-as="Assortment Name"
                                        v-model="form.assortName"
                            ></b-form-input>
                            <span class="validation-error" v-show="errors.has('assortmentName')">{{ errors.first('assortmentName') }}</span>
                        </b-form-group>
                        <b-form-group id="input-group-2" :label="$t('assortment.description')" label-for="assortmentDescription">
                            <b-form-input id="assortmentDescription" name="assortmentDescription" placeholder=""
                                        :class="{ 'form-control': true, 'is-invalid': errors.has('assortmentDescription')}"
                                        v-validate="'required'" data-vv-as="Assortment Description"
                                        v-model="form.assortDesc"
                            ></b-form-input>
                            <span class="validation-error" v-show="errors.has('assortmentDescription')">{{ errors.first('assortmentDescription') }}</span>
                        </b-form-group>
                        <!-- numer recepty zawiera 22 cyfry -->
                        <!-- <b-form-group id="input-group-3" :label="$t('assortment.prescription')" label-for="prescription">
                            
                            <b-form-input id="prescription" name="prescription" :placeholder="$t('placeholders.prescription')"
                                        :class="{ 'form-control': true, 'is-invalid': errors.has('prescription')}"
                                        v-validate="'numeric'" data-vv-as="Prescription"
                                        v-model="form.assortPrescription"
                            ></b-form-input>
                            <span class="validation-error" v-show="errors.has('prescription')">{{ errors.first('prescription') }}</span>
                        </b-form-group> -->    
                        <b-form-group :label="$t('assortment.type')" label-for="assortmentType">
                            <v-select searchable
                                        id="assortmentType" name="assortmentType"
                                        :class="{'editableSelect' : true, 'invalid-select': errors.has('assortmentType')}"
                                        v-validate="'required'" data-vv-as="Type"
                                        v-model="form.assortTypId"
                                        label="key_Code"
                                        index="rowid"
                                        :options="this.assTypes" 
                            />
                            <b-button @click="showDropdownEditor('assortmentType')" class="editButton btn-outline-warning"> 
                                <span class="glyphicon glyphicon-pencil mr-xs mb-xs"></span>
                            </b-button>
                            <span class="validation-error" v-show="errors.has('assortmentType')">{{ errors.first('assortmentType') }}</span>
                        </b-form-group>
                        <b-form-group class="abc-checkbox abc-checkbox-primary">
                            <input type="checkbox" id="prescription-check"
                                    v-model="form.assortPrescription" />
                            <label for="prescription-check">{{$t('assortment.prescriptionRequired')}}</label>
                        </b-form-group>
                    </fieldset>
                    <!-- <b-form-group id="add-buttons" v-if="!commonShit.editMode">
                        <b-row align-v="center">
                            <b-col lg="12" xs="12">
                            <b-button @click="onSubmit" variant="primary">{{$t('assortment.addNew')}}</b-button>
                            &nbsp;&nbsp;
                            <b-button type="reset" variant="secondary">{{$t('common.clearForm')}}</b-button>
                            </b-col>
                            
                            </b-row>
                        </b-form-group>
                        <b-form-group id="add-buttons" v-else>
                            <b-row align-v="center">
                            <b-col lg="12" xs="12">
                            <b-button @click="onUpdateSubmit" variant="success">{{$t('assortment.update')}}</b-button>
                            &nbsp;&nbsp;
                            <b-button type="reset" variant="secondary">{{$t('common.clearForm')}}</b-button>
                            </b-col>
                        </b-row>
                  </b-form-group> -->
                </b-form>
             <!-- </Widget>  -->
                    </b-tab>
                    <b-tab title="Min-Max" :disabled="!commonShit.editMode">
                        <b-form>
                            <fieldset>
                                <!-- <b-form-group id="input-group-1" :label="$t('storage.singular')" label-for="warehouse">
                                    <b-form-input id="warehouse" name="warehouse" :placeholder="$t('placeholders.empty')"
                                                :class="{ 'form-control': true, 'is-invalid': errors.has('warehouse')}"
                                                v-validate="''" data-vv-as="Warehouse"
                                                v-model="warehouse.name"
                                    ></b-form-input>
                                    <span class="validation-error" v-show="errors.has('warehouse')">{{ errors.first('warehouse') }}</span>
                                </b-form-group> -->
                                <b-form-group :label="$t('storage.singular')" label-for="warehouse">
                                        <v-select searchable
                                            
                                                id="warehouse" name="warehouse"
                                                :class="{'ddl-select' : true, 'invalid-select': errors.has('minMax.warehouse')}"
                                                v-validate="'required'" data-vv-as="Warehouse" data-vv-scope="minMax"
                                                v-model="assortMinMax.magId"
                                                index="code"
                                                label="label"
                                                :options="this.storages"
                                        />
                                        <span class="validation-error" v-show="errors.has('minMax.warehouse')">{{ errors.first('minMax.warehouse') }}</span>
                                  </b-form-group>
                                <!-- <b-form-group id="input-group-2" :label="$t('warehouses.places')" label-for="warehousePlace">
                                    <b-form-input id="warehousePlace" name="warehousePlace" :placeholder="$t('placeholders.empty')"
                                                :class="{ 'form-control': true, 'is-invalid': errors.has('warehousePlace')}"
                                                v-validate="''" data-vv-as="Warehouse place"
                                                v-model="warehouse.place"
                                    ></b-form-input>
                                    <span class="validation-error" v-show="errors.has('warehousePlace')">{{ errors.first('warehousePlace') }}</span>
                                </b-form-group> -->
                                <b-form-group id="input-group-3" :label="$t('common.min')" label-for="warehousePlace">
                                    <b-form-input id="min" name="min" :placeholder="$t('placeholders.empty')"
                                                type="number" min="0"
                                                :class="{ 'form-control': true, 'is-invalid': errors.has('minMax.min')}"
                                                v-validate="''" data-vv-as="Min" data-vv-scope="minMax"
                                                v-model="assortMinMax.min"
                                    ></b-form-input>
                                    <span class="validation-error" v-show="errors.has('minMax.min')">{{ errors.first('minMax.min') }}</span>
                                </b-form-group>
                                <b-form-group id="input-group-4" :label="$t('common.max')" label-for="warehousePlace">
                                    <b-form-input id="max" name="max" :placeholder="$t('placeholders.empty')"
                                                type="number" :min="assortMinMax.min"
                                                :class="{ 'form-control': true, 'is-invalid': errors.has('minMax.max')}"
                                                v-validate="''" data-vv-as="Max" data-vv-scope="minMax"
                                                v-model="assortMinMax.max"
                                    ></b-form-input>
                                    <span class="validation-error" v-show="errors.has('minMax.max')">{{ errors.first('minMax.max') }}</span>
                                </b-form-group>
                                <b-button v-if="minMaxEdit == false" @click="onMinMaxSubmit" variant="primary" :disabled="commonShit.editMode == false">{{$t('common.add')}}</b-button>
                                <template v-else>
                                    <b-button @click="onMinMaxUpdateSubmit" variant="success" :disabled="commonShit.editMode == false">{{$t('common.update')}}</b-button>
                                    &nbsp;&nbsp;
                                    <b-button @click="clearMinMaxForm" variant="primary">+</b-button>
                                </template>
                                
                            </fieldset>
                            <!-- <b-form-group id="add-buttons-minmax">
                                <b-row align-v="center">
                                    <b-col lg="12" xs="12">
                                        <b-button @click="onSubmit" variant="primary">{{$t('common.submit')}}</b-button>
                                        &nbsp;&nbsp;
                                        <b-button type="reset" variant="secondary">{{$t('common.clearForm')}}</b-button>
                                    </b-col>
                                </b-row>
                            </b-form-group> -->
                        </b-form>
                    
                    </b-tab>
                    <b-tab :title="$t(commonShit.pageMainApi + '.storageStates')" :disabled="!commonShit.editMode">

                    </b-tab>
                    <b-tab :title="$t(commonShit.pageMainApi + '.history')" :disabled="!commonShit.editMode">

                    </b-tab>
                    <b-tab :title="$t('common.attachments')" :disabled="!commonShit.editMode">
                    </b-tab>
                </b-tabs>
                </b-col>
                <b-col lg="4" xs="12">
                    <div v-show="activeTab == 2" >
                        <v-client-table  :data="assortMinMaxList" :columns="warehouseColumns" :options="warehouseOptions" @row-click="clickTableRow"
                                            id="warehouseTable" ref="warehouseTable" style="margin-top: 4em;"   
                        >
                        </v-client-table>
                    </div>
                </b-col>
                </b-row>
                </Widget>
            </b-col>
            
        </b-row>
<!-- ============================================================= MODALS ======================================================================== -->
        <del-confirm-modal @delConfirmed="deleteRow" @delCancelled="hideDelConfirm"
            :showMe="this.commonShit.delConfirmShow"
            :delWhat="$t(this.commonShit.pageMainApi + '.delete')"
            :message="this.commonShit.delModalMessage" 
            :rowId="this.commonShit.activeRowId"
        />
        <dropdown-edit-modal @closed="hideDropdownEditor"
            :showMe="this.commonShit.dropdownEditShow" 
            :dropdown="this.commonShit.dropdownToEdit"
        />
        <b-modal ref="saveChangesModal" id="saveChangesModal" content-class="darkModal" footer-class="centered" header-text-variant="light" 
            size="sm" centered ok-variant="success"
            :title="$t('common.saveChanges')" :ok-title="$t('common.saveChangesYes')" :cancel-title="$t('common.saveChangesNo')"
        ><h4 class="my-4 text-center">{{ $t('common.unsavedChanges') }}</h4>
        </b-modal>
    </div>
</template>

<script>
import Vue from 'vue';
import vSelect from 'vue-select';
import Widget from '@/components/Widget/Widget';
import DelConfirmModal from "@/components/Custom/DelConfirmModal.vue"
import DropdownEditModal from "@/components/Custom/DropdownEditModal.vue"
import ActionButtons from "@/components/Custom/ActionButtons.vue"
import ExportToExcelButton from "@/components/Custom/ExportToExcelButton.vue"
import axios from "axios";
import { commonMixin } from "@/core/commonMixin";
import { axiosMixin } from "@/core/axiosMixin";
import { doubleClickMixin } from "@/core/doubleClickMixin";
import { saveChangesMixin } from "@/core/saveChangesMixin";
import { helpersMixin } from "@/core/helpersMixin";
import { mapGetters } from 'vuex';
import { mapActions } from 'vuex';
// import vSelect from '@/components/vue-select';
export default {
    components: { Widget, vSelect, DropdownEditModal, DelConfirmModal, ActionButtons, ExportToExcelButton },
    mixins: [commonMixin, axiosMixin, doubleClickMixin, saveChangesMixin, helpersMixin],
    data: function() {
        return {
            commonShit: {
                pageMainApi: 'assortment',
                // rest in commonMixin
            },
             
            form: {
                assortName: '',
                assortDesc: '',
                assortPrescription: false,
                assortTypId: null,
            },
            assortMinMax: {
                assId: null,
                magId: null,
                min: '',
                max: '',
            },
            minMaxEdit: false,
            assortMinMaxId: null,
            // activeTab: 0,
            assTypes: [],
            storages: [],
            
            columns: [ 'assortName', 'assortDesc', 'assortPrescriptionWord', 'assortTypeWord'],
            options: {
                filterByColumn: true,
                perPage: 10,
                perPageValues: [],
                pagination: { chunk: 10, dropdown: false },
                
                skin: "table table-striped",
                    headings: {
                    assortName: "Assortment",
                    assortDesc: "Description",
                    assortPrescription: "Prescription",
                    assortPrescriptionWord: 'Prescription',
                    assortTypeWord: "Type",
                    actions: "",
                },
            filterable: [ 'assortName', 'assortDesc', 'assortPrescriptionWord', 'assortTypeWord'],
            sortable: [ 'assortName', 'assortDesc', 'assortPrescriptionWord', 'assortTypeWord']
            },

            assortMinMaxList: [],
            warehouseColumns: ['magDesc','min','max'],
            warehouseOptions: {
                rowClassCallback(row) { return  (row.rowActive == true) ? 'activeRow' : 'hoverhighlight'},
                texts: {},
                filterByColumn: true,
                perPage: 10,
                perPageValues: [],
                pagination: { chunk: 10, dropdown: false },
                
                skin: "table table-striped",
                headings: {
                    magDesc: 'Warehouse',
                    min: 'Min',
                    max: 'Max',
                },
            filterable: [ 'magDesc','min','max'],
            sortable: [ 'magDesc','min','max']
            }
        }
    },
// ============================================================= VUEX - CACHE  =========================================
    computed: {
      ...mapGetters([
        'cacheExists',
        'getCache',
        'cacheItemsCount',
        'cacheLastUpdate',
        'cacheGetRowById'
      ])
    },
    
    methods: {
      ...mapActions([
        'cacheReset',
        'cacheFill',
        'cacheAddItem',
        'cacheDeleteItem',
        'cacheUpdateItem',
        'cacheSetUpdatedDate',
        'cacheUpdate'
      ]),
// ============================================================= ACTIVATING =========================================
    editRow(row) {
            var mainTableIndex;
            this.filteredList = this.$refs.mainTable.allFilteredData;
            // console.log(this.filteredList);
            this.commonShit.activeRowIndex = this.filteredList.findIndex(x => x.id === row.row.id); // not supported in IE
            mainTableIndex = this.mainList.findIndex(x => x.id === this.filteredList[this.commonShit.activeRowIndex].id);
            this.rowIsActivated(this.mainList, mainTableIndex);
            if (this.isClickedTwice(row.row.id)) {
                this.execDoubleClick(row, 1); // doubleClickMixin
                return;
            }
            this.rowClicked(row.row.id); // doubleClickMixin
        },
    nextRow(table) {
        var mainTableIndex;
        if (this.commonShit.activeRowIndex !== null && this.commonShit.activeRowIndex !== undefined) {
            this.commonShit.activeRowIndex++;
            if (this.commonShit.activeRowIndex >= this.filteredList.length) this.commonShit.activeRowIndex = this.filteredList.length-1;
            else {
                mainTableIndex = table.findIndex(x => x.id === this.filteredList[this.commonShit.activeRowIndex].id);
                this.rowIsActivated(table, mainTableIndex);
            }
            
        }
    },
    previousRow(table) {
      var mainTableIndex;
      if (this.commonShit.activeRowIndex !== null && this.commonShit.activeRowIndex !== undefined) {
        this.commonShit.activeRowIndex--;
        if (this.commonShit.activeRowIndex < 0) this.commonShit.activeRowIndex = 0;
        {
          mainTableIndex = table.findIndex(x => x.id === this.filteredList[this.commonShit.activeRowIndex].id);
          this.rowIsActivated(table, mainTableIndex);
        }
      }
    },
    rowIsActivated(table, index) {
      this.clearForm();
      var row = table[index];
      this.commonShit.activeRow = JSON.parse(JSON.stringify(row));
      this.commonShit.editMode = true;
      this.commonShit.activeRowId = row.id;
      this.detailsTabEnabled = true;
      
      this.formFill(row);
      this.formCloneCreate(this.form, 100);
      this.deactivateAllRows(table);
      this.activateRow(table[index]);
      this.setActiveMessage(row);
      // specific
      // this.minMaxEdit = false;
      this.getMinMaxList();
    },
// ============================================================= DELETING =========================================
    async deleteRow(rowId) {
        this.commonShit.delConfirmShow = false;
        // debugger;

        let path = this.commonShit.pageMainApi + '/' + rowId;
        // console.log(row.id);
        var result = await this.axiosDeleteData(path);
        if (this.axeman.success) {
            this.cacheDeleteItem({ name: this.commonShit.pageMainApi, id: rowId, updateDate: this.axeman.updated })
            Messenger().post(this.$t(this.commonShit.pageMainApi + ".deleted"))
        } else {
            Messenger().post({ message: this.axeman.error, type: 'error' , showCloseButton: true});
        }
        this.commonShit.editMode = false;
        this.clearForm();
    },
// ============================================================= UPDATING =========================================
      
        onUpdateSubmit() {
            if (this.commonShit.validationOn) {
                // console.log('validating');
                var result = this.$validator.validateAll()
                .then(valid => {
                if (valid) { 
                    this.updateItem(); 
                } else 
                    console.log('%cValidation failed...', 'color:red;');
                return false;
                })
                .catch(() => { return false; });
            }
            else {
                this.updateItem();
            } 
        },
        async updateItem() {
            console.log('ID: ' + this.commonShit.activeRowId);

            var data = this.mapFormData();
            var path = this.commonShit.pageMainApi + "/" + this.commonShit.activeRowId; 

            var result = await this.axiosUpdateData(path, data);
            if (this.axeman.success == true) {
                this.setActiveMessage(data);
                Messenger().post(this.$t(this.commonShit.pageMainApi + '.updated') + ': ' + this.commonShit.activeRowMessage);
                data.id = this.commonShit.activeRowId;
                this.cacheUpdateItem({ name: this.commonShit.pageMainApi, data: data, updateDate: this.axeman.updated });
                this.refreshMainTable();
                this.refreshTablePage(); // double click mixin
                this.formCloneCreate(this.form, 100);
                // this.commonShit.editMode = false;
                // this.commonShit.activeRowId = '';
                // this.clearForm();
            } else {
                console.log(this.axeman.error);
                Messenger().post({ message: this.axeman.error, type: "error" });
            }
        },
        async onMinMaxUpdateSubmit() {
            if (this.commonShit.validationOn) {
                var result = await this.$validator.validate('minMax.*')
                    .then(valid => {
                    if (!valid) {
                        console.log('%cValidation failed...', 'color:red;');
                        
                    } else {
                        console.log('Validation OK');
                        this.updateMinMax();
                        // Add animal function
                    }
                });
            }
        },
        async updateMinMax() {
            var newItem = this.mapMinMaxFormData();
            console.log(newItem);
            var result = await this.axiosUpdateData('assortMinMax/' + this.assortMinMaxId, newItem);
            if (this.axeman.success) {
                Messenger().post(this.$t('storage.updated'));
                this.getMinMaxList();
                // this.clearMinMaxForm();
            }else {
                console.log(this.axeman.error);
                Messenger().post({ message: this.axeman.error, type: "error" });
            }
            this.axemanReset();
        },
// ============================================================= ADDING =========================================
        // showAddNew() {
        //     this.clearForm();
        //     this.activeTab = 1;
        //     // specific
            
        // },
        onSubmit() {
            if (this.commonShit.validationOn) {
                var result = this.$validator.validateAll()
                .then(valid => {
                if (valid) { 
                    this.addNewItem(); 
                }
                return false;
                })
                .catch(() => { return false; });
            }
            else {
                this.addNewItem();
            } 
        },

        async addNewItem() {
            var newItem = this.mapFormData(); // assortment
            var result = await this.axiosAddData(this.commonShit.pageMainApi, newItem);
            if (this.axeman.success) {
                Messenger().post(this.$t(this.commonShit.pageMainApi + '.added'));
                // TODO: Cache (potrzebny zwrot ID z bazy po dodaniu rekordu)
                // this.cacheAddItem({ name: this.commonShit.pageMainApi, data: newItem, updateDate: this.axeman.updated});
                this.refreshMainTable();
                this.clearForm();
            }
            else {
                console.log(this.axeman.error);
                Messenger().post({ message: this.axeman.error, type: "error" });
            }
            this.axemanReset();
        },
        async onMinMaxSubmit() {
            if (this.commonShit.validationOn) {
                var result = await this.$validator.validate('minMax.*')
                    .then(valid => {
                    if (!valid) {
                        console.log('%cValidation failed...', 'color:red;');
                        
                    } else {
                        console.log('Validation OK');
                        this.addMinMax();
                        // Add animal function
                    }
                });
            }
        },
        async addMinMax() {
            var newItem = this.mapMinMaxFormData();
            var result = await this.axiosAddData('assortMinMax', newItem);
            if (this.axeman.success) {
                Messenger().post(this.$t('storage.added'));
                this.getMinMaxList();
                
            }else {
                console.log(this.axeman.error);
                Messenger().post({ message: this.axeman.error, type: "error" });
            }
            this.axemanReset();
        },
        
// ============================================================= REFRESHING LIST =========================================
        async refreshMainTable() {
            console.log('= main list');
            var result = await this.axiosGetLastUpdate(this.commonShit.pageMainApi, this.axeman);
            if (!this.cacheExists(this.commonShit.pageMainApi)) {
                result = await this.axiosGetData(this.commonShit.pageMainApi, this.axeman);
                // caching
                if (this.axeman.success == true)
                this.cacheFill({name: this.commonShit.pageMainApi, data: this.axeman.gotFromApi, updateDate: this.axeman.updated});  
            } else if (this.cacheLastUpdate(this.commonShit.pageMainApi) !=  this.axeman.updated) {
                console.log('Cache(' + this.commonShit.pageMainApi + ') update: ' + this.cacheLastUpdate(this.commonShit.pageMainApi));
                console.log(this.axeman);
                // update cache
                result = await this.axiosGetUpdatedData(this.commonShit.pageMainApi, this.cacheLastUpdate(this.commonShit.pageMainApi), this.axeman);
                if (this.axeman.success == true) {
                // console.log(this.axeman.gotFromApi);
                this.cacheUpdate({name: this.commonShit.pageMainApi, data: this.axeman.gotFromApi, updateDate: this.axeman.updated});
                }
            } else {
                console.log('Cache(' + this.commonShit.pageMainApi + ') update: ' + this.cacheLastUpdate(this.commonShit.pageMainApi));
            }
            this.mainList = this.formatDataForListing(this.getCache(this.commonShit.pageMainApi));
            if (!this.commonShit.editMode) this.deactivateAllRows(this.mainList);
            this.axemanReset();
             
        },
        formatDataForListing(data) {
            var index;
            for (var i = 0; i < data.length; i++) {
                data[i].assortPrescriptionWord = (data[i].assortPrescription == true) ? this.$t('common.yes') : this.$t('common.no');
                 index = this.assTypes.findIndex(x => x.rowid === data[i].assortTypId);
                 if (this.assTypes[index] !== undefined)
                    data[i].assortTypeWord = this.assTypes[index].key_Code;
            }
            return data;
        },
        async refreshType(typeName) {
            var result = await this.axiosGetLastUpdate(typeName, this.axeman);
            if (!this.cacheExists(typeName) || this.cacheLastUpdate(typeName) !=  this.axeman.updated) {
                result = await this.axiosGetData(typeName, this.axeman);
                if (this.axeman.success == true)
                    this.cacheFill({name: typeName, data:  this.axeman.gotFromApi, updateDate: this.axeman.updated});
            } 
            this.temp = this.getCache(typeName);
            this.axemanReset();
        }, 
// ============================================================= FORM HANDLING =========================================
        mapFormData() {
            this.form.assortName = this.capitalizeFirstLetter(this.form.assortName);
            this.form.assortDesc = this.capitalizeFirstLetter(this.form.assortDesc);
            var data = JSON.parse(JSON.stringify(this.form));
            data.assortPrescription = data.assortPrescription ? 1 : 0;
            console.log(data);
            //data.assortTypId = 1;
            return data;
        },

        formFill(data) {
            for (var prop in this.form){
                if(this.form.hasOwnProperty(prop) && data.hasOwnProperty(prop)){
                    this.form[prop] = data[prop];
                }
            }
        },
        
        clearForm(e) {
            if (e !== undefined)
                e.preventDefault();
                
            for (var prop in this.form){
                if(this.form.hasOwnProperty(prop)){
                    this.form[prop] = '';
                }
            }
            this.form.assortTypId = null;

            this.commonShit.activeRowId = null;
            this.commonShit.editMode = false;
            this.commonShit.formClone = "";
            this.deactivateAllRows(this.mainList);
            this.formCloneClear();
            this.$validator.reset();
            // specific
            this.clearMinMaxForm();
        },
        // -------------- specific -------------------
        mapMinMaxFormData() {
            var data = JSON.parse(JSON.stringify(this.assortMinMax));
            data.assId = this.commonShit.activeRowId;
            return data;
        },
        clearMinMaxForm(refreshList = 'no') {
            for (var prop in this.assortMinMax){
                if(this.assortMinMax.hasOwnProperty(prop)){
                    this.assortMinMax[prop] = '';
                }
            }
            
            this.$validator.reset();
            this.assortMinMax.magId = null;
            this.assortMinMaxId = null;
            this.minMaxEdit = false;
            if(refreshList === 'no') {
                this.assortMinMaxList = [];
            } 
            else {
                this.deactivateAllRows(this.assortMinMaxList, 'warehouseTable');
            }
        },
        

        async getMinMaxList() {
            var minMaxTemp = { fake: 0,};
            if(this.commonShit.activeRowId !== null) {
                var result = await this.axiosGetData('assortMinMax/' + this.commonShit.activeRowId, minMaxTemp);
                if (this.axeman.success == true) {
                    this.assortMinMaxList = minMaxTemp.gotFromApi;
                    this.clearMinMaxForm('yes');
                    console.log(this.assortMinMaxList);
                } else {
                    console.log(this.axeman.error);
                }
            }   
        },
        
// ============================================================= MODALS =========================================  
        showDelConfirm(row) {
            this.commonShit.delConfirmShow = true;
            this.commonShit.delModalMessage = this.delCustomMessage(row);
        },
        hideDelConfirm() {
            this.commonShit.delConfirmShow = false;
            this.commonShit.delModalMessage = '';
        },
        showDropdownEditor(dropdown) {
                this.commonShit.dropdownToEdit = dropdown;
                this.commonShit.dropdownEditShow = true;
        },
        hideDropdownEditor() {
            this.commonShit.dropdownEditShow = false;
            this.commonShit.dropdownToEdit = '';
            this.refreshTypesFromCache();
        },
        refreshTypesFromCache() {
            this.temp = this.getCache("assortmentType");
            this.assTypes = JSON.parse(JSON.stringify(this.temp));
            this.formatDataForListing(this.mainList);
            this.refreshTablePage();
        },

        // form specific
        
// ============================================================= HEADINGS =========================================
        setTableHeadings() {
            this.options.headings.assortName = this.$t("assortment.name");
            this.options.headings.assortDesc = this.$t("assortment.description");
            this.options.headings.assortPrescriptionWord = this.$t("assortment.prescription");
            this.options.headings.assortTypeWord = this.$t("tableHeadings.category");
            this.warehouseOptions.headings.magDesc = this.$t('storage.singular');

            this.warehouseOptions.texts = this.options.texts;
        },
// ============================================================= PAGE SPECIFIC =========================================
        saveChangesCondition() {
        return (this.commonShit.editMode && this.formCloneCompare(this.form) == false);
        },
        dontSaveChanges() {
            this.formReverseChanges(this.form);
            //this.formCloneCreate(this.form, 0);
        },
        setActiveMessage(row) {  
            this.commonShit.activeRowMessage = row.assortName; // + ' [' + row.assortTypeWord + ']';
        },
        delCustomMessage(row) {
            return row.assortName;
        },
        fillMinMaxForm(data) {
            
            for (var prop in this.assortMinMax){
                if(this.assortMinMax.hasOwnProperty(prop) && data.hasOwnProperty(prop)){
                    this.assortMinMax[prop] = data[prop];
                }
            }
            this.assortMinMax.assId = this.commonShit.activeRowId;
            console.log(this.assortMinMax);
        },
        clickTableRow(row) {
            console.log(row.row);
            var theTable = this.assortMinMaxList;
            var tableIndex = theTable.findIndex(x => x.id === row.row.id);
            this.fillMinMaxForm(row.row);
            this.minMaxEdit = true;
            this.assortMinMaxId = row.row.id;
            this.tableRowIsActivated(theTable, tableIndex);
        },
        tableRowIsActivated(table, index) {
            var row = table[index];
            // console.log(row);
            this.deactivateAllRows(table);
            this.activateRow(table[index]);
            this.refreshTablePage('warehouseTable');
        },
        
    },
// ============================================================= LIFECYCLE HOOKS ========================================= 
    async created() {
        // console.log('%c====  ' + this.commonShit.pageMainApi + '  ====', 'font-weight: bold; background-color: black; color:lightblue;');
        axios.defaults.headers.common["Authorization"] = "Bearer " + localStorage.token;

        if (!this.cacheExists("assortmentType")) {
            var result = await this.axiosGetData("assortmentType", this.assTypes);
            this.assTypes = this.assTypes.gotFromApi;
            this.cacheFill({name: 'assortmentType', data:  this.assTypes, updateDate: '2019-10-10'});
        }
        else {
            this.assTypes = this.getCache('assortmentType');
        }
        this.refreshMainTable();
        if (!this.cacheExists("DDL/Storage")) {
            var result = await this.axiosGetData("DDL/Storage", this.storages);
            this.storages = this.storages.gotFromApi.model;
            this.cacheFill({name: 'DDL/Storage', data:  this.storages, updateDate: '2019-10-10'});
        }
        else {
            this.storages = this.getCache('DDL/Storage');
        }

    },
    updated() {
        this.setTableHeadings();
    },
    
}
</script>

