                                        {{ $t('common.deleteSomething') }}
                                    </b-button>
                                </span> -->
                                </v-client-table>
                    </div>
                        </b-tab>
                        <b-tab :title="$t(this.commonShit.pageMainApi + '.details')" :disabled="!detailsTabEnabled">
                            <button @click="commonShit.validationOn = !commonShit.validationOn">Validation: {{ commonShit.validationOn }}</button>
        <div>
            
            <!-- ============================================================= MAIN FORM ======================================================================== -->
          <!-- <form-wizard shape="tab"
                             color="#3498db"
                             title=""
                             subtitle=""
                             ref="formWizardCustomer"
                >
                     <b-progress class="progress-xs" variant="gray-light" :value="progress" :max="4" />
                    <b-button slot="prev" variant="primary" @click="progress--">
                        <i class="fa fa-caret-left" /> {{$t('menuCustomerCreatePrevious')}}
                    </b-button>
                    <b-button slot="next" variant="primary" @click.prevent="onNext">
                        {{$t('menuCustomerCreateNext')}} <i class="fa fa-caret-right" />
                    </b-button>
                    <span slot="finish">
                        <b-button v-if="!commonShit.editMode"  variant="success" @click="checkNext">
                            {{$t('menuCustomerCreateFinish')}} <i class="fa fa-check" />
                        </b-button>
                        <b-button v-else variant="success" @click="onSubmitUpdatedCustomer">
                            {{$t('customer.update')}} <i class="fa fa-check" />
                        </b-button>
                    </span> -->
                        <b-form>
                            <b-row align-v="center">
                                <b-col lg="3" xs="12">
                                    <span v-if="selectedCustomer !== null">
