                                            v-model="form.animCustomerId"  
                                    />
                                    <span class="validation-error" v-show="errors.has('step2.ownerID')">{{ errors.first('step2.ownerID') }}</span>
                                </b-col>
                                <b-col style="display:flex; align-items: center; justify-content: left;" lg="9" xs="12">
                                    <!-- <h5 class="text-muted" style="align-self: stretch;">{{ $t('customer.info') }}</h5> -->
                                    <template v-if="selectedCustomer !== null">
                                        <h3 class="client-name m-t-1" style="margin-bottom: -0px;">
                                        <span class="text-muted">{{ $t('animal.owner') }}: </span>{{ selectedCustomer.custFirstname }} {{ selectedCustomer.custLastname}}
                                        </h3>
                                        <address style="display:inline-block; margin: 0px 30px 0 10px;">
                                            E-mail: <a href="mailto:#">{{ selectedCustomer.custMail }}</a><br />
                                            {{$t('tableHeadings.phone')}}: {{ selectedCustomer.custPhone}}<br />
                                        </address>
                                    </template>
                                </b-col>
                            </b-row>
                            <br>
                            <b-row>
                                <b-col lg="3" xs="12">
                              
                                    <b-form-group :label="$t('menuPatientCreateName')" label-for="patientName" description="">
                                        <b-form-input id="patientName" type="text" name="petname" :placeholder="$t('placeholders.petName')"
                                                :class="{'form-control': true, 'is-invalid': errors.has('step2.petname')}"
