                                        />
                                        <!-- <b-button @click="showDropdownEditor('DDL/Plec')" class="editButton btn-outline-warning"> 
                                            <span class="glyphicon glyphicon-pencil mr-xs mb-xs"></span>
                                        </b-button> -->
                                        <span class="validation-error" v-show="errors.has('step2.patientGender')">{{ errors.first('step2.patientGender') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="3" xs="12">
                                    <b-form-group :label="$t('tableHeadings.patientColor')" label-for="patientColor" description="">
                                        <b-form-input id="patientColor" type="text" name="patientColor" :placeholder="$t('common.empty')"
                                                :class="{'form-control': true, 'is-invalid': errors.has('step2.patientColor')}"
                                                v-validate="'required'" data-vv-scope="step2" data-vv-as="Color"
                                                v-model="form.animCoat"  
                                        />
                                        <span class="validation-error" v-show="errors.has('step2.patientColor')">{{ errors.first('step2.patientColor') }}</span>
                                    </b-form-group>
                                    <!-- <b-form-group :label="$t('menuPatientCreateColor')" label-for="patientColor">
                                        <v-select searchable
                                                  id="patientColor" name="patientColor"
                                                  :class="{'ddl-select': true, 'invalid-select': errors.has('step2.patientColor')}"
                                                  v-validate="'required'" data-vv-scope="step2" data-vv-as="Color"
                                                  v-model="form.animCoat"
                                                  :options="this.colors"
                                        />
                                        <b-button @click="showDropdownEditor('DDL/Colors')" class="editButton btn-outline-warning"> 
                                            <span class="glyphicon glyphicon-pencil mr-xs mb-xs"></span>
                                        </b-button>
                                        <span class="validation-error" v-show="errors.has('step2.patientColor')">{{ errors.first('step2.patientColor') }}</span>
                                    </b-form-group> -->
                                </b-col>
                                <b-col lg="3" xs="12">
                                    <b-form-group :label="$t('menuPatientCreateType')" label-for="patientType">
                                        <v-select searchable
                                                  @change="speciesChanged"
                                                  id="patientType" name="patientType"
                                                  :class="{'ddl-select': true, 'invalid-select': errors.has('step2.patientType')}"
                                                  v-validate="'required'" data-vv-scope="step2" data-vv-as="Type"
                                                  v-model="form.animSpeciesId"
                                                  :options="this.types"
                                                  index="code"
                                                label="label"
                                        />
                                        <!-- <b-button @click="showDropdownEditor('DDL/Types')" class="editButton btn-outline-warning"> 
                                            <span class="glyphicon glyphicon-pencil mr-xs mb-xs"></span>
                                        </b-button> -->
                                        <span class="validation-error" v-show="errors.has('step2.patientType')">{{ errors.first('step2.patientType') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="3" xs="12">
                                    <b-form-group :label="$t('menuPatientCreateRace')" label-for="patientRace">
                                        <v-select searchable
                                                  id="patientRace" name="patientRace"
                                                  :class="{'ddl-select': true, 'invalid-select': errors.has('step2.patientRace')}"
                                                  v-validate="'required'" data-vv-scope="step2" data-vv-as="Race"
                                                  v-model="form.animRaceId"
                                                  :options="this.races"
                                                  index="code"
                                                label="label"
                                                  :disabled="this.form.animSpeciesId == null"
                                        />
                                        <span class="validation-error" v-show="errors.has('step2.patientRace')">{{ errors.first('step2.patientRace') }}</span>
                                    </b-form-group>
                                    <!-- <b-form-group> -->
                                        
                                    <!-- </b-form-group> -->
                                </b-col>
                            </b-row>
                            <b-row align-v="center">
                            <b-col lg="3" xs="12">
                                <b-form-group class="abc-checkbox abc-checkbox-primary">
                                    <input type="checkbox"
                                           id="castrated-check"
                                           v-model="form.animCastrated" />
                                    <label for="castrated-check">{{$t('menuPatientCreateCastrated')}}</label>
                                </b-form-group>
                                <b-form-group class="abc-checkbox abc-checkbox-primary">
                                    <input type="checkbox"
                                           id="aggressive-check"
                                           v-model="form.animAggressive" />
                                    <label for="aggressive-check">{{$t(this.commonShit.pageMainApi + '.aggressive')}}</label>
                                </b-form-group>
                            </b-col>
                            <!-- <b-form-group id="add-buttons" v-if="!commonShit.editMode">
                                <b-row align-v="center">
                                    <b-col lg="12" xs="12">
                                        <b-button @click="onSubmit" variant="primary">{{$t(this.commonShit.pageMainApi + '.add')}}</b-button>
                                        &nbsp;&nbsp;
                                        <b-button @click="clearForm" variant="secondary">{{$t('common.clearForm')}}</b-button>
                                    </b-col>
                                </b-row>
                            </b-form-group>
                            <b-form-group id="add-buttons" v-else>
                                <b-row align-v="center">
                                    <b-col lg="12" xs="12">
                                        <b-button @click="onUpdateSubmit" variant="success">{{$t(this.commonShit.pageMainApi + '.update')}}</b-button>
                                        &nbsp;&nbsp;
                                        <b-button @click="clearForm" variant="secondary">{{$t('common.clearForm')}}</b-button>
                                    </b-col>
                                </b-row>
                            </b-form-group> -->
                            
            
            </b-row>
                        </b-form>
                        
        </div>
                        </b-tab>
                        <b-tab :title="$t(this.commonShit.pageMainApi + '.allergiesDiseases')" :disabled="!commonShit.editMode">
                        </b-tab>
                        <b-tab :title="$t(this.commonShit.pageMainApi + '.owners')" :disabled="!commonShit.editMode">
                            <v-client-table :data="ownersList" :columns="ownersColumns" :options="ownersOptions" ref="ownersTable" @row-click="ownerClick">
                            </v-client-table>
                        </b-tab>
                        <b-tab :title="$t(this.commonShit.pageMainApi + '.visitHistory')" :disabled="!commonShit.editMode">
                        </b-tab>
                        <b-tab :title="$t('common.attachments')" :disabled="!commonShit.editMode">
                        </b-tab>
                    </b-tabs>
                </Widget>
            </b-col>
        </b-row>
<!-- ============================================================= MODALS ======================================================================== -->
        <del-confirm-modal @delConfirmed="deleteRow" @delCancelled="hideDelConfirm"
            :showMe="this.commonShit.delConfirmShow"
            :delWhat="$t(this.commonShit.pageMainApi + '.delete')"
            :message="this.commonShit.delModalMessage" 
            :rowId="this.commonShit.activeRowId"
        />
        <dropdown-edit-modal @closed="hideDropdownEditor"
            :showMe="this.commonShit.dropdownEditShow" 
            :dropdown="this.commonShit.dropdownToEdit"
        />
        <b-modal ref="saveChangesModal" id="saveChangesModal" content-class="darkModal" footer-class="centered" header-text-variant="light" 
            size="sm" centered ok-variant="success"
            :title="$t('common.saveChanges')" :ok-title="$t('common.saveChangesYes')" :cancel-title="$t('common.saveChangesNo')"
        ><h4 class="my-4 text-center">{{ $t('common.unsavedChanges') }}</h4>
        </b-modal>
        <b-modal ref="chooseCustomerModal" id="chooseCustomerModal" content-class="darkModal" footer-class="centered" header-text-variant="light" 
            centered hide-header-close size="lg"
            v-model="showCustomerChoice"
            :title="$t('customer.choose')"
        >
        <b-row class="justify-content-md-center" align-v="center" style="background-color: rgba(0,0,0,0.2);">
            <b-col style="display:flex; align-items: center; justify-content: left;">
                <!-- <h5 class="text-muted" style="align-self: stretch;">{{ $t('customer.info') }}</h5> -->
                <template v-if="selectedCustomer !== null">
                    <h3 class="client-name m-t-1" style="margin-bottom: -0px;">
                    <span class="text-muted">{{ $t('animal.owner') }}: </span>{{ selectedCustomer.custFirstname }} {{ selectedCustomer.custLastname}}
                    </h3>
                    <address style="display:inline-block; margin: 0px 30px 0 10px;">
                        E-mail: <a href="mailto:#">{{ selectedCustomer.custMail }}</a><br />
                        {{$t('tableHeadings.phone')}}: {{ selectedCustomer.custPhone}}<br />
                    </address>
                </template>
            </b-col>
        </b-row>
        <br>
        <b-row>
            <b-col>
                <v-client-table :data="customerList" :columns="customerColumns" :options="customerOptions" ref="customerTable" @row-click="clickCustomerRow">        
                </v-client-table>
            </b-col>

        </b-row>
            <div slot="modal-footer">
                <b-button variant="secondary" @click="closeThis" class="closeModalButton">{{ $t('common.close') }}</b-button>
            </div>
        </b-modal>
    </div>
</template>

<script>
import Vue from 'vue';
import Widget from '@/components/Widget/Widget';
// import { vueTableData, vueDatatableData } from './data';
import vSelect from 'vue-select';
import DelConfirmModal from "@/components/Custom/DelConfirmModal.vue"
import DropdownEditModal from "@/components/Custom/DropdownEditModal.vue"
import ActionButtons from "@/components/Custom/ActionButtons.vue"
import ExportToExcelButton from "@/components/Custom/ExportToExcelButton.vue"
import axios from 'axios';
import {i18n} from '@/plugins/i18n';
import { formatAddress } from '@/core/helpers.js';
import { commonMixin } from "@/core/commonMixin";
import { axiosMixin } from "@/core/axiosMixin";
import { doubleClickMixin } from "@/core/doubleClickMixin";
import { saveChangesMixin } from "@/core/saveChangesMixin";
import { helpersMixin } from "@/core/helpersMixin";
import { mapGetters } from 'vuex';
import { mapActions } from 'vuex';

export default {
    // name: 'PatientList',
    components: { Widget, vSelect, DelConfirmModal, DropdownEditModal, ActionButtons, ExportToExcelButton },
    mixins: [commonMixin, axiosMixin, doubleClickMixin, saveChangesMixin, helpersMixin],
    data() {
        return {
            commonShit: {
                pageMainApi: 'animal',
                // rest in commonMixin
            },
            
            columns: ['animName', 'animChip', 'animBirthdate', 'animSpeciesDesc', 'animRaceDesc', 'animSexDesc', 'weight', 'castrated','actions'],
            options: {
                filterByColumn: true,
                perPage: 10,
                perPageValues: [],
                pagination: { chunk: 10, dropdown: false },
                
                skin: 'table table-striped',
                headings: {
                // defaults
                    animName: 'Name',
                    animChip: "Chip",
                    animBirthdate: "Birthdate",
                    weight: "Weight",
                    animSpeciesDesc: "Species",
                    animRaceDesc: "Race",
                    animSexDesc: "Gender",
                    castrated: "Castrated",
                    actions: ''
                },
                filterable: ['animName', 'animChip', 'animBirthdate', 'animSpeciesDesc', 'animRaceDesc', 'animSexDesc', 'weight', 'castrated'],
                sortable: ['animName', 'animChip', 'animBirthdate', 'animSpeciesDesc', 'animRaceDesc', 'animSexDesc', 'weight', 'castrated'],
            },
            form: {
                animCustomerId: '',
                animName: '',
                animChip: '',
                animBirthdate: '',
                weight: '',
                animSexId: null,
                animSexDesc: '',
                animCoat: null,
                animSpeciesId: null,
                animSpeciesDesc: '',
                animRaceId: null,
                animRaceDesc: '',
                animCastrated: false,
                animAggressive: false,
            },
            ownerDC: {
                rowClickedOnce: false,
                rowId: null,
            },
            
            ownersList: [],
            ownersColumns: ['custFirstname', 'custLastname', 'custMail', 'custPhone', 'custAddress', 'custCity'],
            ownersOptions: {
                rowClassCallback(row) { return  (row.rowActive == true) ? 'activeRow' : 'hoverhighlight'},
                texts: {},
                filterByColumn: true,
                columnsDropdown: true,
                perPage: 10,
                perPageValues: [],
                pagination: { chunk: 10, dropdown: false },
                
                skin: 'table table-striped',
                headings: {
                // defaults
                    custFirstname: 'Name',
                    custLastname: "Lastname",
                    custMail: "Email",
                    custPhone: "Phone",
                    custBirthdate: "Date of birth",
                    custAddress: "Address",
                    custCity: "City",
                    custCityCode: "Postal code",
                    custAgree1: 'Aggred1',
                    actions: ''
                },
                filterable: ['custFirstname', 'custLastname', 'custMail', 'custPhone', 'custBirthdate', 'custAddress', 'custCityCode', 'custCity','custAgree1'],
                sortable: ['custFirstname', 'custLastname', 'custMail', 'custPhone', 'custBirthdate', 'custAddress', 'custCityCode', 'custCity','custAgree1'],
            },
            selectedCustomer: null,
            showCustomerChoice: false,
            customerList: [],
            // customerColumns: ['custFirstname', 'custLastname', 'custMail', 'custPhone', 'custAddress', 'custCity'],
            customerColumns: ['custFirstname', 'custLastname', 'custMail', 'custPhone'],
            customerOptions: {
                rowClassCallback(row) { return  (row.rowActive == true) ? 'activeRow' : 'hoverhighlight'},
                texts: {},
                columnsDropdown: true,
                filterByColumn: true,
                
                perPage: 10,
                perPageValues: [],
                pagination: { chunk: 10, dropdown: false },
                
                skin: 'table table-striped',
                headings: {
                // defaults
                    custFirstname: 'Name',
                    custLastname: "Lastname",
                    custMail: "Email",
                    custPhone: "Phone",
                    custBirthdate: "Date of birth",
                    custAddress: "Address",
                    custCity: "City",
                    custCityCode: "Postal code",
                    custAgree1: 'Aggred1',
                    actions: ''
                },
                filterable: ['custFirstname', 'custLastname', 'custMail', 'custPhone', 'custBirthdate', 'custAddress', 'custCityCode', 'custCity','custAgree1'],
                sortable: ['custFirstname', 'custLastname', 'custMail', 'custPhone', 'custBirthdate', 'custAddress', 'custCityCode', 'custCity','custAgree1'],
        },
            
            // patientModalShow: false,
            // validationOn: true,
            
            genders: [],
            // colors: [],
            types: [],
            zones: [],
            races: [],
        };
    },
    computed: {
        ...mapGetters([
            'cacheExists',
            'getCache',
            'cacheItemsCount',
            'cacheLastUpdate',
            'cacheGetRowById',
            'getActiveOnArrival',
        ]),
    },
    methods: {
        ...mapActions([
            'cacheReset',
            'cacheFill',
            'cacheAddItem',
            'cacheDeleteItem',
            'cacheUpdateItem',
            'cacheSetUpdatedDate',
            'cacheUpdate',
            'setActiveOnArrival',
            'resetActiveOnArrival'
      ]),
// ============================================================= ACTIVATING =========================================
    editRow(row) {
            var mainTableIndex;
            this.filteredList = this.$refs.mainTable.allFilteredData;
            // console.log(this.filteredList);
            this.commonShit.activeRowIndex = this.filteredList.findIndex(x => x.id === row.row.id); // not supported in IE
            mainTableIndex = this.mainList.findIndex(x => x.id === this.filteredList[this.commonShit.activeRowIndex].id);
            this.rowIsActivated(this.mainList, mainTableIndex);
            if (this.isClickedTwice(row.row.id)) {
                this.execDoubleClick(row, 1); // doubleClickMixin
                return;
            }
            this.rowClicked(row.row.id); // doubleClickMixin
        },
    nextRow(table) {
        var mainTableIndex;
        if (this.commonShit.activeRowIndex !== null && this.commonShit.activeRowIndex !== undefined) {
            this.commonShit.activeRowIndex++;
            if (this.commonShit.activeRowIndex >= this.filteredList.length) this.commonShit.activeRowIndex = this.filteredList.length-1;
            else {
                mainTableIndex = table.findIndex(x => x.id === this.filteredList[this.commonShit.activeRowIndex].id);
                this.rowIsActivated(table, mainTableIndex);
            }
            
        }
    },
    previousRow(table) {
        var mainTableIndex;
        if (this.commonShit.activeRowIndex !== null && this.commonShit.activeRowIndex !== undefined) {
            this.commonShit.activeRowIndex--;
            if (this.commonShit.activeRowIndex < 0) this.commonShit.activeRowIndex = 0;
            {
                mainTableIndex = table.findIndex(x => x.id === this.filteredList[this.commonShit.activeRowIndex].id);
                this.rowIsActivated(table, mainTableIndex);
            }
        }
    },
    async rowIsActivated(table, index) {
        this.clearForm();
        var row = table[index];
        this.commonShit.activeRow = JSON.parse(JSON.stringify(row));
        this.commonShit.editMode = true;
        this.commonShit.activeRowId = row.id;
        this.detailsTabEnabled = true;
        
        this.formFill(row);
        this.formCloneCreate(this.form, this.formCloneDelay);
        // this.formCloneCreate(this.form, 100);
        this.deactivateAllRows(table);
        this.activateRow(table[index]);
        this.setActiveMessage(row);
        // specific
        this.getOwners();
    },
// ============================================================= ACTIVATING CUSTOMER =========================================
    clickCustomerRow(row) {
        var customerTableIndex = this.customerList.findIndex(x => x.id === row.row.id);
        if (this.isClickedTwice(row.row.id)) {
            this.execCustomerDoubleClick(row);
            return;
        } 
        else {
            this.customerRowIsActivated(this.customerList, customerTableIndex);
            this.rowClicked(row.row.id); // doubleClickMixin
        }
    },
    execCustomerDoubleClick(row) {
        this.dc.rowClickedOnce= false; 
        this.dc.rowId = null;
        this.closeThis();
    },
    customerRowIsActivated(table, index) {
        var row = table[index];
        this.selectedCustomer = row;
        this.form.animCustomerId = this.selectedCustomer.id;
        this.deactivateAllRows(table);
        this.activateRow(table[index]);
        this.refreshTablePage('customerTable');
    },
// ============================================================= CUSTOMER INFO =========================================
        async loadCustomerInfo() {
            var result = await this.axiosGetLastUpdate('customer', this.axeman);
        // debugger;
            if (!this.cacheExists('customer')) {
                // console.log('Cache not created...');
                result = await this.axiosGetData('customer', this.axeman);
                // caching
                if (this.axeman.success == true)
                this.cacheFill({name: 'customer', data: this.axeman.gotFromApi, updateDate: this.axeman.updated});
                
            } else if (this.cacheLastUpdate('customer') !=  this.axeman.updated) {
                console.log('Cache(' + 'customer' + ') update: ' + this.cacheLastUpdate('customer'));
                // update cache
                result = await this.axiosGetUpdatedData('customer', this.cacheLastUpdate('customer'), this.axeman);
                if (this.axeman.success == true) {
                // console.log(this.axeman.gotFromApi);
                this.cacheUpdate({name: 'customer', data: this.axeman.gotFromApi, updateDate: this.axeman.updated});
                }
            } else {
                console.log('Cache(' + 'customer' + ') update: ' + this.cacheLastUpdate('customer'));
                console.log('Using cached data...');
            }
            this.customerList = this.getCache('customer');
            this.axemanReset();
        },
// ============================================================= ADDING =========================================
        // showAddNew() {
        //     this.clearForm();
        //     this.activeTab = 1;
        // },
    onSubmit() {
        if (this.commonShit.validationOn) {
            // console.log('validating');
            var result = this.$validator.validate('step2.*')
            .then(valid => {
            if (valid) { 
                this.addNewItem(); 
            } else 
                console.log('%cValidation failed...', 'color:red;');
            return false;
            })
            .catch(() => { return false; });
        }
        else {
            this.addNewItem();
        } 
    },
    async addNewItem() {
        var newItem = this.mapFormData(); // form
        var result = await this.axiosAddData(this.commonShit.pageMainApi, newItem);
        if (this.axeman.success) {
            Messenger().post(this.$t(this.commonShit.pageMainApi + '.added') + ': ' + newItem.animName);
            // ========== ONLY CACHING, NO SERVER WORKLOAD (ASSUMING NO SIMULTANEOUS EDITING)
            newItem.animSpeciesDesc = this.getDropdownItemLabel(this.types, newItem.animSpeciesId);
            newItem.animRaceDesc = this.getDropdownItemLabel(this.races, newItem.animRaceId);
            newItem.animSexDesc = this.getDropdownItemLabel(this.genders, newItem.animSexId);
            this.cacheAddItem({ name: this.commonShit.pageMainApi, data: newItem, updateDate: this.axeman.updated});
            // TODO: Cache (potrzebny zwrot ID z bazy po dodaniu rekordu)
            this.refreshMainTable();
            // this.clearForm();
            this.rowIsActivated(this.mainList, this.mainList.length - 1);
        }
        else {
            console.log(this.axeman.error);
            Messenger().post({ message: this.axeman.error, type: "error" });
        }
        this.axemanReset();
    },
// ============================================================= UPDATING =========================================
    onUpdateSubmit() {
        if (this.commonShit.validationOn) {
            // console.log('validating');
            var result = this.$validator.validate('step2.*')
            .then(valid => {
            if (valid) { 
                this.updateItem(); 
            } else 
                console.log('%cValidation failed...', 'color:red;');
            return false;
            })
            .catch(() => { return false; });
        }
        else {
            this.updateItem();
        } 
    },
    async updateItem() {
        console.log('ID: ' + this.commonShit.activeRowId);

        var data = this.mapFormData();
        console.log(data);
        var path = this.commonShit.pageMainApi + "/" + this.commonShit.activeRowId; 

        var result = await this.axiosUpdateData(path, data);
        if (this.axeman.success == true) {
            this.setActiveMessage(data);
            Messenger().post(this.$t(this.commonShit.pageMainApi + '.updated') + ': ' + this.commonShit.activeRowMessage);
            data.id = this.commonShit.activeRowId;
            this.cacheUpdateItem({ name: this.commonShit.pageMainApi, data: data, updateDate: this.axeman.updated });
            this.refreshMainTable();
            this.refreshTablePage(); // double click mixin
            this.formCloneCreate(this.form, 100);
            // this.commonShit.editMode = false;
            // this.commonShit.activeRowId = '';
            // this.clearForm();
            // this.$refs.formWizardCustomer.reset();
      } else {
            console.log(this.axeman.error);
            Messenger().post({ message: this.axeman.error, type: "error" });
      }
    },
// ============================================================= DELETING =========================================
        async deleteRow(rowId) {
            this.commonShit.delConfirmShow = false;
            // debugger;

            let path = this.commonShit.pageMainApi + '/' + rowId;
            // console.log(row.id);
            var result = await this.axiosDeleteData(path);
            if (this.axeman.success) {
                this.cacheDeleteItem({ name: this.commonShit.pageMainApi, id: rowId, updateDate: this.axeman.updated});
                Messenger().post(this.$t(this.commonShit.pageMainApi + ".deleted"))
            } else {
                Messenger().post({ message: this.axeman.error, type: 'error' , showCloseButton: true});
            }
                this.commonShit.editMode = false;
                this.clearForm();
        },
// ============================================================= REFRESHING LIST =========================================
    async refreshMainTable() {
        console.log('= main list');
        var result = await this.axiosGetLastUpdate(this.commonShit.pageMainApi, this.axeman);
        if (!this.cacheExists(this.commonShit.pageMainApi)) {
            var result = await this.axiosGetData(this.commonShit.pageMainApi, this.axeman);
            if (this.axeman.success == true)
                this.cacheFill({name: this.commonShit.pageMainApi, data: this.axeman.gotFromApi, updateDate: this.axeman.updated});  
        } else if (this.cacheLastUpdate(this.commonShit.pageMainApi) !=  this.axeman.updated) {
            console.log('Cache(' + this.commonShit.pageMainApi + ') update: ' + this.cacheLastUpdate(this.commonShit.pageMainApi));
            // update cache
            result = await this.axiosGetUpdatedData(this.commonShit.pageMainApi, this.cacheLastUpdate(this.commonShit.pageMainApi), this.axeman);
            if (this.axeman.success == true) {
            console.log(this.axeman.gotFromApi);
            this.cacheUpdate({name: this.commonShit.pageMainApi, data: this.axeman.gotFromApi, updateDate: this.axeman.updated});
            }
        } else {
            console.log('Cache(' + this.commonShit.pageMainApi + ') update: ' + this.cacheLastUpdate(this.commonShit.pageMainApi));
        }
        this.mainList = this.formatDataForListing(this.getCache(this.commonShit.pageMainApi));
        
        // this.mainList = this.formatDataForListing(this.axeman.gotFromApi);
        if (!this.commonShit.editMode) this.deactivateAllRows(this.mainList);
        // console.log(this.mainList);
        this.axemanReset();
        if(this.getActiveOnArrival.list == 'animal') {
            console.log(this.getActiveOnArrival.id);
            // console.log(this.filteredList);
            var index = this.mainList.findIndex(x => x.id === this.getActiveOnArrival.id); // not supported in IE
            if (index !== -1) {
                this.filteredList = this.mainList;
                this.commonShit.activeRowIndex = index;
                this.rowIsActivated(this.mainList, index);
                this.activeTab = 1;
            }
            this.resetActiveOnArrival();
        }
    },
    
    formatDataForListing(data) {
      for (var i = 0; i < data.length; i++) {
          // data[i].emplAdress = this.formatAddress(data[i].emplStreet, data[i].emplHouse, data[i].emplFlat, 'pl');
          // data[i].rowActive = false;
          if (data[i].animBirthdate !== undefined)
            data[i].animBirthdate = data[i].animBirthdate.split('T')[0];
            data[i].animCastrated ? data[i].castrated = this.$t('common.yes') : data[i].castrated = this.$t('common.no');
            data[i].weight = ''; 
        }
        // this.deactivateAllRows(data);
        return data;
    },
// ============================================================= FORM HANDLING =========================================
        mapFormData() {
            this.form.animName = this.capitalizeFirstLetter(this.form.animName);
            var data = JSON.parse(JSON.stringify(this.form));
            //data.assortTypId = 1;
            return data;
        },

        formFill(data) {
            for (var prop in this.form){
                if(this.form.hasOwnProperty(prop) && data.hasOwnProperty(prop)){
                    this.form[prop] = data[prop];
                }
            }
        },
        
        clearForm() {
                this.form.animCustomerId = '';
                this.form.animName = '';
                this.form.animChip = '';
                this.form.animBirthdate = '';
                this.form.weight = '';
                this.form.animSexId = null;
                this.form.animSexDesc = '';
                this.form.animCoat = null;
                this.form.animSpeciesId = null;
                this.form.animSpeciesDesc = '';
                this.form.animRaceId = null;
                this.form.animRaceDesc = '';
                this.form.animCastrated = false;
                this.form.animAggressive = false;

                this.selectedCustomer = null;
            // this.$refs.formWizardCustomer.reset();
            // this.progress = 1;
            this.commonShit.editMode = false;
            this.commonShit.formClone = "";
            this.commonShit.formChangesString = "";
            // this.$validator.reset();
            this.$validator.reset( {scope: 'step2'} );
            this.deactivateAllRows(this.mainList);
            this.deactivateAllRows(this.customerList); 
        },
        // async checkNext() {
        //     // console.log('Before change validation');
        //     var isOK = true;
        //     if (this.commonShit.validationOn) {
        //         var result = await this.$validator.validate('step' + this.progress +'.*').then(valid => {
        //             if (!valid) {
        //                 console.log('Step' + + this.progress + ' not valid');
        //                 isOK = false;
        //             } else {
        //                 if (this.progress == 4 && isOK == true) {
        //                     this.onSubmitNewCustomer();
        //                 } else
        //                     this.progress++;
        //             }
        //         });
        //     }
        //     return isOK;
        // },
// ============================================================= MODALS =========================================      
        showDelConfirm(row) {
            this.commonShit.delConfirmShow = true;
            this.commonShit.delModalMessage = this.delCustomMessage(row);
        },
        hideDelConfirm() {
            this.commonShit.delConfirmShow = false;
            this.commonShit.delModalMessage = '';
        },
        showDropdownEditor(dropdown) {
            this.commonShit.dropdownToEdit = dropdown;
            this.commonShit.dropdownEditShow = true;
        },
        hideDropdownEditor() {
            this.commonShit.dropdownEditShow = false;
            this.commonShit.dropdownToEdit = '';
        },
// ============================================================= HEADINGS =========================================
        setTableHeadings() {
            this.options.headings.animName = this.$t("tableHeadings.patientName");
            this.options.headings.animChip = this.$t("tableHeadings.patientChip");
            this.options.headings.animBirthdate = this.$t("tableHeadings.patientBirthdate");
            this.options.headings.weight = this.$t("tableHeadings.patientWeight");
            this.options.headings.animSexDesc = this.$t("tableHeadings.patientGender");
            this.options.headings.animCoat = this.$t("tableHeadings.patientColor");
            this.options.headings.animSpeciesDesc = this.$t("tableHeadings.patientType");
            this.options.headings.animRaceDesc = this.$t("tableHeadings.patientRace");
            this.options.headings.castrated = this.$t("tableHeadings.patientCastrated");

            this.ownersOptions.headings.custFirstname = this.$t('tableHeadings.name');
            this.ownersOptions.headings.custLastname = this.$t('tableHeadings.lastname');
            this.ownersOptions.headings.custMail = this.$t('tableHeadings.email');
            this.ownersOptions.headings.custPhone = this.$t('tableHeadings.phone');
            this.ownersOptions.headings.custBirthdate = this.$t('tableHeadings.dateOfBirth');
            this.ownersOptions.headings.custAddress = this.$t('tableHeadings.address');
            this.ownersOptions.headings.custCityCode = this.$t('tableHeadings.postalCode');
            this.ownersOptions.headings.custCity = this.$t('tableHeadings.city');
            this.ownersOptions.texts = this.options.texts;
            this.customerOptions.texts = this.options.texts;
        },
    
// ============================================================= PAGE SPECIFIC =========================================
    saveChangesCondition() {
      return (this.commonShit.editMode && this.formCloneCompare(this.form) == false);
    },
    dontSaveChanges() {
        this.formReverseChanges(this.form);
        //this.formCloneCreate(this.form, 0);
    },
    setActiveMessage(row) {  
            this.commonShit.activeRowMessage = row.animName + ' (' + row.animRaceDesc + ')';
    },
    delCustomMessage(row) {
        return row.animName + ' ' + row.animChip;
    },
    async speciesChanged() {
        if (this.form.animSpeciesId == null) {
            this.form.animRaceId = null;
            this.races = [];
            // this.$refs.provinceSelect.disabled = true;
        }
        else {
            if (!this.cacheExists("DDL/AnimalRace_" + this.form.animSpeciesId)) {
                var result = await this.axiosGetDataForId("DDL/AnimalRace", this.form.animSpeciesId, this.races);
                this.races = this.races.gotFromApi.model;
                this.cacheFill({name: 'DDL/AnimalRace_' + this.form.animSpeciesId, data:  this.races, updateDate: '2019-10-10'});
                // this.$refs.provinceSelect.disabled = false;
            }
            else {
                this.races = this.getCache('DDL/AnimalRace_' + this.form.animSpeciesId);
            }
        }
    },
    async getOwners() {
        var owners = { fake: 0,};
        if(this.commonShit.activeRowId !== null) {
            var result = await this.axiosGetData('animal/' + this.commonShit.activeRowId + '/GetOwners', owners);
            if (this.axeman.success == true) {
                this.ownersList = this.formatOwnersForListing(owners.gotFromApi);
                if (this.ownersList.length > 0) {
                    this.selectedCustomer = this.ownersList[0];
                    // this.form.animCustomerId = this.selectedCustomer.id;
                    // this.formCloneCreate(this.form, 100);
                }    
                // console.log(this.ownersList);
            } else {
                console.log(this.axeman.error);
            }
        }   
    },
    formatOwnersForListing(data) {
      for (var i = 0; i < data.length; i++) {
          data[i].custAddress = this.formatAddress(data[i].custStreet, data[i].custHouse, data[i].custFlat, 'pl');
        //   (data[i].custAgree1 == true) ? data[i].custAgree1 = this.$t('common.yes') : data[i].custAgree1 = this.$t('common.no');
          if (data[i].custBirthdate !== undefined)
            data[i].custBirthdate = data[i].custBirthdate.split('T')[0];
        }
        return data;
    },
    ownerClick(row) {
            console.log('click ' + row.row.id);
            if (this.isOwnerClickedTwice(row.row.id)) {
                this.execOwnerDoubleClick(row);
                return;
            }
            this.ownerRowClicked(row.row.id);
        },
        isOwnerClickedTwice(rowId) {
            return (this.ownerDC.rowClickedOnce && this.ownerDC.rowId == rowId)
        },
        ownerRowClicked(rowId) {
            this.ownerDC.rowClickedOnce = true;
            this.ownerDC.rowId = rowId;
            var self = this;
            setTimeout(function(){ self.ownerDC.rowClickedOnce= false; self.ownerDC.rowId = null}, 300);
            var theTable = this.ownersList;
            var tableIndex = theTable.findIndex(x => x.id === rowId);
            this.tableRowIsActivated(theTable, tableIndex);
        },
        execOwnerDoubleClick(row) {
            console.log('Owner: ' + this.ownerDC.rowId);
            this.setActiveOnArrival({list: 'customer', id: this.ownerDC.rowId});
            this.ownerDC.rowClickedOnce = false; 
            this.ownerDC.rowId = null
            // console.log(this.getActiveOnArrival);
            this.$router.push('CustomerList');
        },
        closeThis() {
            this.showCustomerChoice = false;
        },
        onVisitClick() {
            this.setActiveOnArrival({list: 'animal', id: this.commonShit.activeRowId});
            this.$router.push('Sales');
        },
        tableRowIsActivated(dataTable, index) {
            var row = dataTable[index];
            // console.log(row);
            this.deactivateAllRows(dataTable);
            this.activateRow(dataTable[index]);
            this.refreshTablePage('ownersTable');
        },

    },
// ============================================================= LIFECYCLE HOOKS =========================================  
    async created () {
        //debugger;
        // console.log('%c====  ' + this.commonShit.pageMainApi + '  ====', 'font-weight: bold; background-color: black; color:lightblue;');
        //   let route = this.$route.params.id;
        
        axios.defaults.headers.common["Authorization"] = "Bearer " + localStorage.token;
        if (!this.cacheExists("DDL/Plec")) {
            var result = await this.axiosGetData("DDL/Plec", this.genders);
            this.genders = this.genders.gotFromApi.model;
            this.cacheFill({name: 'DDL/Plec', data:  this.genders, updateDate: '2019-10-10'});
        }
        else {
            this.genders = this.getCache('DDL/Plec');
        }
        if (!this.cacheExists("DDL/AnimalSpecies")) {
            var result = await this.axiosGetData("DDL/AnimalSpecies", this.types);
            this.types = this.types.gotFromApi.model;
            this.cacheFill({name: 'DDL/AnimalSpecies', data:  this.types, updateDate: '2019-10-10'});
        }
        else {
            this.types = this.getCache('DDL/AnimalSpecies');
        }
        this.refreshMainTable();
        //var result = await this.refreshType("equipmentType");
        //this.equipmentTypes = JSON.parse(JSON.stringify(this.temp));
        
        //this.refreshMainTable();
        // mockup DDLs
        // this.cacheFill({name: 'DDL/Colors', data:  this.mockup.colors, updateDate: '2019-10-10'});
        // this.cacheFill({name: 'DDL/Types', data:  this.mockup.types, updateDate: '2019-10-10'});
        // this.cacheFill({name: 'DDL/Zones', data:  this.mockup.zones, updateDate: '2019-10-10'});
        // this.colors = this.getCache('DDL/Colors');
        // this.types = this.getCache('DDL/Types');
        // this.zones = this.getCache('DDL/Zones');
    },

    updated() {
        this.setTableHeadings();
    },
};
