<template>
    <div>
        <b-breadcrumb class="testTop">
          <b-breadcrumb-item>{{$t('common.youAreHere').toUpperCase()}}</b-breadcrumb-item>
          <b-breadcrumb-item active>
            {{$t('menuSalesNewInvoice')}}
          </b-breadcrumb-item>   
        </b-breadcrumb>
        <p v-if="$route.params.id">{{ $route.params.id }}</p>
          <b-row>
            <b-col lg="12" xs="12">
              <Widget customHeader close collapse fullscreen>
                  <b-row align-v="center">
                      <b-col lg="7" xs="12">
                  <!-- <b-button-toolbar class="mt-lg justify-content-end d-print-none"> -->
                          <b-button variant="primary" @click="loadList('assortment'); showAssChoice = true">
                          {{ $t('assortment.addNew') }}
                      </b-button>
                      &nbsp;&nbsp;
                      <b-button variant="primary" @click="loadList('servic'); showServiceChoice = true">
                          {{ $t('servic.addNew') }}
                      </b-button>
                      &nbsp;&nbsp;
                      <b-button variant="primary">
                          {{ $t('visit.next') }}
                      </b-button>
                      &nbsp;&nbsp;
                      <span v-if="selectedCustomer !== null">
                      <b-button variant="success" @click="showCustomerChoice = true">
                          {{ $t('customer.change') }}
                      </b-button>
                      </span>
                      <span v-else>
                          <b-button variant="primary" @click="showCustomerChoice = true">
                              {{ $t('customer.choose') }}
                          </b-button>
                      </span>
                      &nbsp;&nbsp;
                      <span v-if="selectedAnimal !== null" >
                      <b-button variant="success" @click="showAnimalChoice = true" >
                          {{ $t('animal.change') }}
                      </b-button>
                      </span>
                      <span v-else>
                          <b-button variant="primary" @click="showAnimalChoice = true" :disabled="this.selectedCustomer == null">
                              {{ $t('animal.choose') }}
                          </b-button>
                      </span>
                      </b-col>
                      <b-col lg="5" xs="12" class="text-right">
                          <strong>{{ $t('sales.discount') }} (%):</strong>
                          <b-form-input type="number" min="0" max="100" name="discount" id="discount" style="max-width: 65px; display: inline-block;  height: 90%;"
                          v-model="discountPercent"/>
                          &nbsp;&nbsp;
                          <b-button variant="danger" @click="generateInvoice">
                              {{ $t('sales.proceedWithPayment') }}
                              &nbsp;
                              <span class="circle bg-white">
                                  <i class="fa fa-arrow-right text-danger" />
                              </span>
                          </b-button>
                          &nbsp;&nbsp;
                          <b-button onClick={this.printInvoice} variant="inverse" class="mr-2">
                              <i class="fa fa-print" />
                              &nbsp;&nbsp;
                              {{ $t('common.print') }}
                          </b-button>
                      </b-col>
                  </b-row>
                          <b-row class="justify-content-md-center" align-v="center" style="background-color: rgba(0,0,0,0.2); margin-top: 5px; margin-bottom: 5px;">
                              <b-col lg="7" xs="12" style="display:flex; align-items: center; justify-content: left;">
                                  <!-- <h5 class="text-muted" style="align-self: stretch;">{{ $t('customer.info') }}</h5> -->
                                  <template v-if="selectedCustomer !== null">
                                      <h3 class="client-name m-t-1" style="margin-bottom: -0px;">
                                      <span class="text-muted">{{ $t('customer.info') }}</span>{{ selectedCustomer.custFirstname }} {{ selectedCustomer.custLastname }}
                                      </h3>
                                      <address style="display:inline-block; margin: 0px 30px 0 10px;">
                                          E-mail: <a href="mailto:#">{{ selectedCustomer.custMail }}</a><br />
                                          {{$t('tableHeadings.phone')}}: {{ selectedCustomer.custPhone}}<br />
                                      </address>
                                  </template>
                              </b-col>
                              <b-col lg="5" xs="12" style="display:flex; align-items: center; justify-content: left;">
                                  <!-- <h5 class="text-muted" style="align-self: stretch;">{{ $t('customer.info') }}</h5> -->
                                  <template v-if="selectedAnimal !== null">
                                      <h3 class="client-name m-t-1" style="margin-bottom: -0px;">
                                      <span class="text-muted">{{ $t('animal.singular') }}: </span><span style="color:wheat;">{{ selectedAnimal.animName }}</span> ({{ selectedAnimal.animRaceDesc }})
                                      </h3>
                                      <!-- <address style="display:inline-block; margin: 0px 30px 0 10px;">
                                          E-mail: <a href="mailto:#">{{ selectedCustomer.custMail }}</a><br />
                                          {{$t('tableHeadings.phone')}}: {{ selectedCustomer.custPhone}}<br />
                                      </address> -->
                                  </template>
                              </b-col>
                          </b-row>
                          
                              <!-- </b-button-toolbar> -->
                  <b-row>
                    <b-col lg="6" xs="12">
                        <legend>{{$t('menuSalesMedicalHistory')}}</legend>
                  <b-form @submit="onSubmit">
                      <fieldset>
                          <b-form-group label=""
                                        label-for="default-textarea"
                                        label-class="text-md-right px-3"
                                        :label-cols="0"
                                        breakpoint="md">
                              <b-form-textarea class="input-transparent" :rows="3" id="default-textarea2" v-model="textAreaContent2" />
                          </b-form-group>

                      </fieldset>
                  </b-form>
                    </b-col>
                    <b-col lg="6" xs="12">   
                  <legend>{{$t('menuSalesMedicalTreatment')}}</legend>
                  <b-form @submit="onSubmit">
                      <fieldset>
                          <b-form-group label=""
                                        label-for="default-textarea"
                                        label-class="text-md-right px-3"
                                        :label-cols="0"
                                        breakpoint="md">
                              <b-form-textarea class="input-transparent" :rows="3" id="default-textarea" v-model="textAreaContent" />
                          </b-form-group>
                      </fieldset>
                  </b-form>
            </b-col>
          </b-row>
        <br>
        <b-row>
            <b-col>
                <v-client-table :data="mainList" :columns="columns" :options="options" ref="mainTable">
                    <div slot="quantity" slot-scope="{row}" :key="editMode">
                        <span v-if="!row.isEditing">{{row.quantity}}</span>
                        <span v-else>
                            <b-form-input type="number" min="1" name="editItemQ" id="editItemQ"
                                        v-model="row.quantity"
                            ></b-form-input>
                        </span>
                    </div>
                    <div slot="pricePerUnit" slot-scope="{row}" :key="editMode">
                        <span v-if="!row.isEditing">{{row.pricePerUnit}}</span>
                        <span v-else>
                            <b-form-input type="text" name="editItemP" id="editItemP"
                                        v-model="row.pricePerUnit"
                            ></b-form-input>  
                        </span>
                    </div>
                    <span slot="actions" slot-scope="{row}" :key="editMode">
                        <span v-if="!row.isEditing">
                        <b-button class="btn btn-square btn-warning button-smaller"
                            @click="inlineEditOn(row)"
                        >{{ $t('common.edit') }}</b-button>&nbsp;&nbsp;
                        <b-button class="btn btn-square btn-danger button-smaller"
                            @click="showDelConfirm(row)" 
                        >{{ $t('common.deleteSomething') }}</b-button>
                        </span>
                        <span v-else>
                            <b-button type="button" variant="primary" class="button-smaller" @click="inlineEditUpdate(row)">{{ $t('common.save') }}</b-button>&nbsp;&nbsp;
                            <b-button type="button" variant="secondary" class="button-smaller" @click="inlineEditCancel(row)">{{ $t('common.back') }}</b-button>
                        </span>
                    </span>
                    <span slot="total" slot-scope="{row}">
                        {{ rowTotal(row) }}
                    </span>
                </v-client-table>
            </b-col>
        </b-row>
        <b-row class="text-right justify-content-end">
          <b-col lg="5" xs="12">
            <legend>{{$t('sales.summary')}}</legend>
            <b-row>
              <b-col sm='6'>
                <p>{{ $t('sales.subtotal') }}</p>
                <p v-if="discountPercent > 0" class="text-success">{{ $t('sales.discount') }} ({{ discountPercent }}%)</p>
                <p>{{ $t('sales.tax') }} ({{this.taxPercent}}%)</p>
                <p class="no-margin"><strong>{{ $t('sales.total') }}</strong></p>
              </b-col>
              <b-col sm='6'>
                <p>{{ summary.subtotal.toFixed(2) }}</p>
                <p v-if="discountPercent > 0" class="text-success">{{ discount.toFixed(2) }}</p>
                <p> {{ tax.toFixed(2) }}</p>
                <p class="no-margin"><strong>{{ totalSum.toFixed(2) }}</strong></p>
              </b-col>
            </b-row>
            <legend></legend>
          </b-col>
        </b-row>
        </Widget>
        </b-col>
        </b-row>
<!-- ============================================================= MODALS ======================================================================== -->
        <del-confirm-modal @delConfirmed="deleteRow" @delCancelled="hideDelConfirm"
            :showMe="this.commonShit.delConfirmShow"
            :delWhat="$t(this.commonShit.pageMainApi + '.delete')"
            :message="this.commonShit.delModalMessage" 
            :rowId="this.commonShit.activeRowId"
        />
        <b-modal ref="chooseCustomerModal" id="chooseCustomerModal" content-class="darkModal" footer-class="centered" header-text-variant="light" 
            centered hide-header-close size="lg"
            v-model="showCustomerChoice"
            :title="$t('customer.choose')"
        >
        <!-- <b-row class="justify-content-md-center" align-v="center" style="background-color: rgba(0,0,0,0.2);">
            <b-col style="display:flex; align-items: center; justify-content: left;"> -->
                <!-- <h5 class="text-muted" style="align-self: stretch;">{{ $t('customer.info') }}</h5> -->
                <!-- <template v-if="selectedCustomer !== null">
                    <h3 class="client-name m-t-1" style="margin-bottom: -0px;">
                    <span class="text-muted">{{ $t('customer.info') }}</span>{{ selectedCustomer.custFirstname }} {{ selectedCustomer.custLastname }}
                    </h3>
                    <address style="display:inline-block; margin: 0px 30px 0 10px;">
                        E-mail: <a href="mailto:#">{{ selectedCustomer.custMail }}</a><br />
                        {{$t('tableHeadings.phone')}}: {{ selectedCustomer.custPhone}}<br />
                    </address>
                </template> -->
            <!-- </b-col>
        </b-row>
        <br> -->
        <b-row>
            <b-col>
                <v-client-table :data="customerList" :columns="customerColumns" :options="customerOptions" ref="customerTable" @row-click="clickTableRow">        
                </v-client-table>
            </b-col>

        </b-row>
            <div slot="modal-footer">
                <b-button variant="secondary" @click="closeThis" class="closeModalButton">{{ $t('common.close') }}</b-button>
            </div>
        </b-modal>
        <!-- chose animal modal  -->
        <b-modal ref="chooseAnimalModal" id="chooseAnimalModal" content-class="darkModal" footer-class="centered" header-text-variant="light" 
            centered hide-header-close size="lg"
            v-model="showAnimalChoice"
            :title="$t('animal.choose')"
        >
        <b-row>
            <b-col>
                <v-client-table :data="ownedAnimalsList" :columns="ownedAnimalsColumns" :options="ownedAnimalsOptions" ref="ownedAnimalsTable" @row-click="clickTableRow">        
                </v-client-table>
            </b-col>

        </b-row>
            <div slot="modal-footer">
                <b-button variant="secondary" @click="closeThis" class="closeModalButton">{{ $t('common.close') }}</b-button>
            </div>
        </b-modal>
        <!-- assortment modal  -->
        <b-modal ref="chooseAssModal" id="chooseAssModal" content-class="darkModal" footer-class="centered" header-text-variant="light" 
            centered hide-header-close size="lg"
            v-model="showAssChoice"
            :title="$t('assortment.addNew')"
        >
        <b-row>
          <b-col>
            <strong>{{ $t('sales.quantity') }}: </strong>
            <b-form-input type="number" min="1" name="quantity" id="quantityAss" style="max-width: 80px; display: inline-block;  height: 90%;"
                          v-model="itemQuantity"/>
          </b-col>
          <b-col xl="8">
            <strong>{{ $t('sales.portion') }}: </strong>
            <b-form-input type="number" min="0" name="dosage" id="dosage" style="max-width: 80px; display: inline-block;  height: 90%;"
                          v-model="dosage.portion"/>
            <v-select searchable style="display: inline-block;  height: 90%; width: 100px"
                      id="massUnit" name="massUnit"
                      v-model="dosage.massUnit"
                      index="code"
                      label="label"
                      :options="this.massUnits"
            />
            &nbsp;&nbsp;
            <b-form-input type="number" min="0" name="freq" id="freq" style="max-width: 60px; display: inline-block;  height: 90%;"
                          v-model="dosage.frequency"/>
            &nbsp;x&nbsp;
            <v-select searchable style="display: inline-block;  height: 90%; width: 115px"
                      id="frequencyUnit" name="frequencyUnit"
                      v-model="dosage.frequencyUnit"
                      index="code"
                      label="label"
                      :options="this.frequencySelectOptions"
                      default="daily"
            />
          </b-col>
        </b-row>
        <b-row>
            <b-col>
                <v-client-table :data="assList" :columns="assColumns" :options="assOptions" ref="assTable" @row-click="clickTableRow">        
                </v-client-table>
            </b-col>

        </b-row>
            <div slot="modal-footer">
                <b-button variant="primary" @click="addAssortmentToMainList(selectedAssortment)" class="closeModalButton" :disabled="selectedAssortment == null">{{ $t('assortment.addNew') }}</b-button>
                &nbsp;&nbsp;
                <b-button variant="secondary" @click="closeThis" class="closeModalButton">{{ $t('common.close') }}</b-button>
            </div>
        </b-modal>
        <!-- service modal  -->
        <b-modal ref="chooseServiceModal" id="chooseServiceModal" content-class="darkModal" footer-class="centered" header-text-variant="light" 
            centered hide-header-close size="lg"
            v-model="showServiceChoice"
            :title="$t('servic.addNew')"
        >
        <strong>{{ $t('sales.quantity') }}: </strong>
        <b-form-input type="number" min="1" name="quantity" id="quantityServ" style="max-width: 80px; display: inline-block;  height: 90%;"
                    v-model="itemQuantity"/>
        <b-row>
            <b-col>
                <v-client-table :data="serviceList" :columns="serviceColumns" :options="serviceOptions" ref="serviceTable" @row-click="clickTableRow">        
                </v-client-table>
            </b-col>

        </b-row>
            <div slot="modal-footer">
                <b-button variant="primary" @click="addServiceToMainList(selectedService)" class="closeModalButton" :disabled="selectedService == null">{{ $t('servic.addNew') }}</b-button>
                &nbsp;&nbsp;
                <b-button variant="secondary" @click="closeThis" class="closeModalButton">{{ $t('common.close') }}</b-button>
            </div>
        </b-modal>
    </div>
</template>

<script>
import Vue from 'vue';
import vSelect from 'vue-select';
import Widget from '@/components/Widget/Widget';
import DelConfirmModal from "@/components/Custom/DelConfirmModal.vue"
import axios from 'axios';
import { commonMixin } from "@/core/commonMixin";
import { axiosMixin } from "@/core/axiosMixin";
import { doubleClickMixin } from "@/core/doubleClickMixin";
import { mapGetters } from 'vuex';
import { mapActions } from 'vuex';
// import vSelect from '@/components/vue-select';
export default {
    components: { Widget, vSelect, DelConfirmModal },
    mixins: [commonMixin, axiosMixin, doubleClickMixin],
    data: function() {
        return {
            commonShit: {
                pageMainApi: 'sales',
                // rest in commonMixin
            },
            showCustomerChoice: false,
            showAnimalChoice: false,
            showAssChoice: false,
            showServiceChoice: false,
            selectedCustomer: null,
            selectedAnimal: null,
            selectedService: null,
            selectedAssortment: null,
            editMode: false,
            tempItem: {
                quantity: 0,
                pricePerUnit: 0,
            },
            itemQuantity: 1,
            dosage: {
              portion: 0,
              massUnit: null,
              frequency: 0,
              frequencyUnit: 1,
            },
            massUnits: [],
            frequencySelectOptions: [{label: 'day', code: 1}, {label: 'week', code: 2}, {label: 'month', code: 3}],
            textAreaContent: '',
            textAreaContent2: '',
            taxPercent: 10,
            discountPercent: 3,
            summary: {
                subtotal: 0,
                total: 0,
                taxValue: 0,
                discountValue: 0,
            },
            invoice: {
                EmpId: null,
                CustomerAnimal: null,
                CustomerId:null,
                MethodPay: null,
                Visitid: null,
                Lines: [],
            },
            invoiceJSON: null,
            animalList: [],
            
            ownersList: [],
            columns: ['id', 'item', 'description', 'quantity', 'pricePerUnit', 'total', 'actions'],
            
            customerList: [],
            // customerColumns: ['custFirstname', 'custLastname', 'custMail', 'custPhone', 'custAddress', 'custCity'],
            customerColumns: ['custFirstname', 'custLastname', 'custMail', 'custPhone'],
            customerOptions: {
                rowClassCallback(row) { return  (row.rowActive == true) ? 'activeRow' : 'hoverhighlight'},
                columnsDropdown: true,
                filterByColumn: true,
                
                perPage: 10,
                perPageValues: [],
                pagination: { chunk: 10, dropdown: false },
                
                skin: 'table table-striped',
                headings: {
                // defaults
                    custFirstname: 'Name',
                    custLastname: "Lastname",
                    custMail: "Email",
                    custPhone: "Phone",
                    custBirthdate: "Date of birth",
                    custAddress: "Address",
                    custCity: "City",
                    custCityCode: "Postal code",
                    custAgree1: 'Aggred1',
                    actions: ''
                },
                filterable: ['custFirstname', 'custLastname', 'custMail', 'custPhone', 'custBirthdate', 'custAddress', 'custCityCode', 'custCity','custAgree1'],
                sortable: ['custFirstname', 'custLastname', 'custMail', 'custPhone', 'custBirthdate', 'custAddress', 'custCityCode', 'custCity','custAgree1'],
        },
            customerDC: {
                rowClickedOnce: false,
                rowId: null,
            },
            ownedAnimalsList: [],
            ownedAnimalsColumns: ['animName', 'animChip', 'animBirthdate', 'animSpeciesDesc', 'animRaceDesc'],
            ownedAnimalsOptions: {
                rowClassCallback(row) { return  (row.rowActive == true) ? 'activeRow' : 'hoverhighlight'},
                texts: {},
                columnsDropdown: true,
                filterByColumn: true,
                perPage: 10,
                perPageValues: [],
                pagination: { chunk: 10, dropdown: false },
                
                skin: 'table table-striped',
                headings: {
                // defaults
                    animName: 'Name',
                    animChip: "Chip",
                    animBirthdate: "Birthdate",
                    weight: "Weight",
                    animSpeciesDesc: "Species",
                    animRaceDesc: "Race",
                    animSexDesc: "Gender",
                    animCastrated: "Castrated",
                    actions: ''
                },
                filterable: ['animName', 'animChip', 'animBirthdate', 'animSpeciesDesc', 'animRaceDesc', 'animSexDesc', 'weight', 'animCastrated'],
                sortable: ['animName', 'animChip', 'animBirthdate', 'animSpeciesDesc', 'animRaceDesc', 'animSexDesc', 'weight', 'animCastrated'],
            },
            assList: [],
            assColumns: [ 'assortName', 'assortDesc', 'assortPrescriptionWord', 'assortTypeWord'],
            assOptions: {
                rowClassCallback(row) { return  (row.rowActive == true) ? 'activeRow' : 'hoverhighlight'},
                texts: {},
                filterByColumn: true,
                perPage: 10,
                perPageValues: [],
                pagination: { chunk: 10, dropdown: false },
                
                skin: "table table-striped",
                    headings: {
                    assortName: "Assortment",
                    assortDesc: "Description",
                    assortPrescription: "Prescription",
                    assortPrescriptionWord: 'Prescription',
                    assortTypeWord: "Type",
                    actions: "",
                },
            filterable: [ 'assortName', 'assortDesc', 'assortPrescriptionWord', 'assortTypeWord'],
            sortable: [ 'assortName', 'assortDesc', 'assortPrescriptionWord', 'assortTypeWord']
            },
            serviceList: [],
            serviceColumns: ['servicName', 'servicDesc', 'servicCost', 'servicDuration'],
            serviceOptions: {
                rowClassCallback(row) { return  (row.rowActive == true) ? 'activeRow' : 'hoverhighlight'},
                texts: {},
                filterByColumn: true,
                perPage: 10,
                perPageValues: [],
                pagination: { chunk: 10, dropdown: false },
                
                skin: 'table table-striped',
                headings: {
                        servicName: 'Name',
                        serviceTypeWord: 'Type',
                        servicDesc: 'Description',
                        servicCost: 'Cost',
                        servicDuration: 'Duration',
                        servicTaxId: 'Tax Id',
                        actions: '',

                    },
                filterable: ['servicName', 'serviceTypeWord', 'servicDesc', 'servicCost', 'servicDuration', 'servicTaxId'],
                sortable: ['servicName', 'serviceTypeWord', 'servicDesc', 'servicCost', 'servicDuration', 'servicTaxId'],
            },
            options: {
                columnsDropdown: false,
                filterByColumn: false,
                perPage: 10,
                perPageValues: [],
                pagination: { chunk: 10, dropdown: false },
                
                skin: 'table table-striped',
                headings: {
                        id: '#',
                        item: 'Item',
                        description: 'Description',
                        quantity: 'Quantity',
                        pricePerUnit: 'Price per Unit',
                        total: 'Total',
                        actions: '',

                    },
                sortable: ['id', 'item', 'description', 'quantity', 'pricePerUnit', 'total'],
                filterable: false,
            },
            
            // fakeData: [
            //     {
            //         id: null,
            //         item: 'Gryzak',
            //         description: 'Dla zbkujcych psw',
            //         quantity: 2,
            //         pricePerUnit: 25,
            //     },
            //     {
            //         id: null,
            //         item: 'Szczepienie podstawowe',
            //         description: 'Dla zdrowia',
            //         quantity: 1,
            //         pricePerUnit: 90,
            //     },
            //     {
            //         id: null,
            //         item: 'Pieczka',
            //         description: 'Zabawka',
            //         quantity: 4,
            //         pricePerUnit: 19.99,
            //     },
            // ],  
        }
    },
    computed: {
        ...mapGetters([
            'cacheExists',
            'getCache',
            'cacheItemsCount',
            'cacheLastUpdate',
            'cacheGetRowById',
            'getActiveOnArrival',
        ]),
       sumOfAllFears() {
           var sum = 0;
           for(var i = 0; i < this.mainList.length; i++) {
               sum += this.mainList[i].quantity * this.mainList[i].pricePerUnit;
           }
            // this.summary.subtotal = sum;
            // this.summary.discountValue = sum * this.discountPercent / 100;
            // this.summary.taxValue = (sum - this.summary.discountValue) * this.taxPercent / 100;
            // this.summary.total = (sum - this.summary.discountValue + this.summary.taxValue);

           return sum;
       },
       discount() {
           return (this.summary.subtotal * this.discountPercent / 100);
       },
       tax() {
           return ((this.summary.subtotal - this.discount) * this.taxPercent / 100);
       },
       totalSum() {
           return (this.summary.subtotal - this.discount + this.tax); 
       },

    },

    methods: {
        ...mapActions([
            'cacheReset',
            'cacheFill',
            'cacheAddItem',
            'cacheDeleteItem',
            'cacheUpdateItem',
            'cacheSetUpdatedDate',
            'cacheUpdate',
            'setActiveOnArrival',
            'resetActiveOnArrival'
      ]),
        onSubmit(e) {
            e.preventDefault();
            // this.$validator.validateAll();
        },
        refreshIndexes() {
            for(var i = 0; i < this.mainList.length; i++)
            this.mainList[i].id = i+1;
        },
        rowTotal(row) {
            // console.log(row);
            return row.quantity * row.pricePerUnit;
        },
// ============================================================= UPDATING =========================================
        inlineEditUpdate(row) {
            // update(row); 
            row.isEditing = false; 
            this.editMode = !this.editMode;
            var index = this.mainList.findIndex(x => x.id === row.id);
            this.mainList[index].quantity = row.quantity;
            this.mainList[index].pricePerUnit = row.pricePerUnit;
            // console.log(row.quantity);
            // console.log(this.mainList[row.id - 1].quantity);
            this.summary.subtotal = this.sumOfAllFears;
        },
        inlineEditCancel(row) {
            row.quantity = this.tempItem.quantity; 
            row.pricePerUnit = this.tempItem.pricePerUnit; 
            row.isEditing = false; 
            this.editMode = !this.editMode;
        },
        inlineEditOn(row) {
            row.isEditing = true; 
            this.editMode = !this.editMode; 
            this.tempItem.quantity = row.quantity; 
            this.tempItem.pricePerUnit = row.pricePerUnit;
        },
// ============================================================= DELETING =========================================
        async deleteRow(rowId) {
        this.commonShit.delConfirmShow = false;
        // debugger;
        //   console.log(rowId);
        var index = this.mainList.findIndex(x => x.id === rowId); // not supported in IE
        this.mainList.splice(index, 1);
        this.refreshIndexes();
        this.summary.subtotal = this.sumOfAllFears;
        //   let path = this.commonShit.pageMainApi + '/' + rowId;
        //   // console.log(row.id);
        //   var result = await this.axiosDeleteData(path);
        //   if (this.axeman.success) {
        //     this.cacheDeleteItem({ name: this.commonShit.pageMainApi, id: rowId, updateDate: this.axeman.updated })
        //     Messenger().post(this.$t(this.commonShit.pageMainApi + ".deleted"))
        //   } else {
        //     Messenger().post({ message: this.axeman.error, type: 'error' , showCloseButton: true});
        //   }
        //       this.commonShit.editMode = false;
        //       this.clearForm();
        },
// ============================================================= CUSTOMER AND ANIMAL =========================================
    async loadList(listName) {
        var result = await this.axiosGetLastUpdate(listName, this.axeman);
    // debugger;
        if (!this.cacheExists(listName)) {
            // console.log('Cache not created...');
            result = await this.axiosGetData(listName, this.axeman);
            // caching
            if (this.axeman.success == true)
            this.cacheFill({name: listName, data: this.axeman.gotFromApi, updateDate: this.axeman.updated});
            // this.employeeList = this.getCache(listName);  
        } else if (this.cacheLastUpdate(listName) !=  this.axeman.updated) {
            console.log('Cache(' + listName + ') update: ' + this.cacheLastUpdate(listName));
            // update cache
            result = await this.axiosGetUpdatedData(listName, this.cacheLastUpdate(listName), this.axeman);
            if (this.axeman.success == true) {
            // console.log(this.axeman.gotFromApi);
            this.cacheUpdate({name: listName, data: this.axeman.gotFromApi, updateDate: this.axeman.updated});
            }
        } else {
            console.log('Cache(' + listName + ') update: ' + this.cacheLastUpdate(listName));
            // this.employeeList = this.getCache(listName);
        }
        if (listName === 'customer')
            this.customerList = this.getCache(listName);
        else if (listName === 'animal')
            this.animalList = this.getCache(listName);
        else if (listName === 'servic')
            this.serviceList = this.getCache(listName);
        else if (listName === 'assortment') {
            this.assList = this.getCache(listName);
            for (var i = 0; i < this.assList.length; i++) 
                this.assList[i].assortPrescriptionWord = (this.assList[i].assortPrescription == true) ? this.$t('common.yes') : this.$t('common.no');
        }
            
        this.axemanReset();
        
    },
    async getOwners(animalId) {
        var owners = { fake: 0,};
        if(animalId !== null) {
            var result = await this.axiosGetData('animal/' + animalId + '/GetOwners', owners);
            if (this.axeman.success == true) {
                // this.ownersList = this.formatOwnersForListing(owners.gotFromApi);
                this.ownersList = owners.gotFromApi;
                if (this.ownersList.length > 0) {
                    this.selectedCustomer = this.ownersList[0];
                    // this.form.animCustomerId = this.selectedCustomer.id;
                    // this.formCloneCreate(this.form, 100);
                }    
                // console.log(this.ownersList);
            } else {
                console.log(this.axeman.error);
            }
        }   
    },
    async getAnimals(customerId) {
        var owners = { fake: 0,};
        if(this.customerId !== null) {
            var result = await this.axiosGetData('customer/' + customerId + '/GetAnimals', owners);
            if (this.axeman.success == true) {
                // this.animalsList = this.formatAnimalsForListing(owners.gotFromApi);
                this.ownedAnimalsList = owners.gotFromApi;
                // console.log(this.animalsList);
            } else {
                console.log(this.axeman.error);
            }
        }   
    },
    async checkOnArrival() {
        if(this.getActiveOnArrival.list == 'customer' || this.getActiveOnArrival.list == 'animal') {
            var animalId;
            var customerId;
            if (this.getActiveOnArrival.list == 'customer')
                customerId = this.getActiveOnArrival.id;
            else if (this.getActiveOnArrival.list == 'animal') {
                animalId = this.getActiveOnArrival.id;
                this.selectedAnimal = this.animalList[this.animalList.findIndex(x => x.id === animalId)];
                var result = await this.getOwners(this.getActiveOnArrival.id);
                customerId = this.selectedCustomer.id;
            }
            // console.log(this.getActiveOnArrival.id);
            // console.log(this.filteredList);
            var index = this.customerList.findIndex(x => x.id === customerId); // not supported in IE
            if (index !== -1) {
                this.tableRowIsActivated(this.customerList, index);
            }
            this.resetActiveOnArrival();
        }
    },

// ============================================================= ACTIVATING CUSTOMER AND ANIMAL =========================================
    clickTableRow(row) {
        var theTable;
        if ( row.row.hasOwnProperty('custFirstname')) {
            theTable = this.customerList;
            if (this.selectedCustomer !== null && row.row.id !== this.selectedCustomer.id)
                this.selectedAnimal = null;
        }    
        else if ( row.row.hasOwnProperty('animName'))
            theTable = this.ownedAnimalsList;
        else if (row.row.hasOwnProperty('assortName')) {
            theTable = this.assList;
        }
        else if (row.row.hasOwnProperty('servicName')) {
            theTable = this.serviceList;
        }
        var tableIndex = theTable.findIndex(x => x.id === row.row.id);
        if (this.isClickedTwice(row.row.id)) {
            this.execTableDoubleClick(row);
            return;
        } 
        else {
            this.tableRowIsActivated(theTable, tableIndex);
            this.rowClicked(row.row.id); // doubleClickMixin
        }
    },
    execTableDoubleClick(row) {
        this.dc.rowClickedOnce = false; 
        this.dc.rowId = null;
        if (row.row.hasOwnProperty('assortName') || row.row.hasOwnProperty('servicName'))
            return;
        if (row.row.hasOwnProperty('custFirstname')) {
            this.selectedCustomer = row.row;
            this.getAnimals(row.row.id);
        }
        else if ( row.row.hasOwnProperty('animName')) {
            this.selectedAnimal = row.row;
        }
        this.closeThis();
    },
    tableRowIsActivated(table, index) {
        var row = table[index];
        // if (row.hasOwnProperty('custFirstname')) {
        //     // this.selectedCustomer = row;
        //     // this.getAnimals(row.id);
        // }
        // else if ( row.hasOwnProperty('animName')) {
        //     this.selectedAnimal = row;
        // }
        if ( row.hasOwnProperty('assortName')) {
            this.selectedAssortment = row;
            this.resetDosage();
            // console.log(this.selectedAssortment);
        }
        else if ( row.hasOwnProperty('servicName')) {
            this.selectedService = row;
        }
        this.deactivateAllRows(table);
        this.activateRow(table[index]);
        this.refreshTablePage('customerTable');
        this.refreshTablePage('ownedAnimalsTable');
        this.refreshTablePage('serviceTable');
        this.refreshTablePage('assTable');

    },
    resetDosage() {
      this.dosage.frequency = 0;
      this.dosage.frequencyUnit = 1;
      this.dosage.portion = 0;
      this.dosage.massUnit = null;
    },
    // {"ID":3, "QTY":"2.4" ,"PRICE":"2.12", "PORTION":1, "PORTIONUOMID":1, "FREQ":1, "FREQUOMID":1 ,"HOWLONG":3,"DISCOUNT":21, "TYP":0},
    addAssortmentToMainList(newAss) {
      // console.log(newAss);
        var tableIndex = this.mainList.findIndex(x => x.item === newAss.assortName);
        if (tableIndex === -1) {
            var newItem =   
            {
                // table show
                item: newAss.assortName,
                description: newAss.assortDesc,
                quantity: this.itemQuantity,
                pricePerUnit: 1,
                // for JSON
                itemId: newAss.id,
                portion: this.dosage.portion,
                portionUnit: this.dosage.massUnit,
                freq: this.dosage.frequency,
                freqUnit: this.dosage.frequencyUnit,
                howlong: null,
                typ: 0,
            }
            console.log(newItem);
            this.mainList.push(newItem);
            this.refreshIndexes();
            this.resetDosage();
        }
        else {
            this.mainList[tableIndex].quantity = parseInt(this.mainList[tableIndex].quantity) + parseInt(this.itemQuantity);
        }
      
        this.summary.subtotal = this.sumOfAllFears;
        this.selectedAssortment = null;
        this.deactivateAllRows(this.assList);
        this.refreshTablePage('assTable');
    },
    // {"ID":9, "QTY":3 ,"PRICE":"1.22", "DISCOUNT":0, "TYP":1},
    addServiceToMainList(newServ) {
        var tableIndex = this.mainList.findIndex(x => x.item === newServ.servicName);
        if (tableIndex === -1) {
            var newItem =   
            {
                // table show
                item: newServ.servicName,
                description: newServ.servicDesc,
                quantity: this.itemQuantity,
                pricePerUnit: newServ.servicCost,
                // for JSON
                itemId: newServ.id,
                typ: 1,
            }
            this.mainList.push(newItem);
            this.refreshIndexes();
        }
        else {
            this.mainList[tableIndex].quantity = parseInt(this.mainList[tableIndex].quantity) + parseInt(this.itemQuantity);
        }

        this.summary.subtotal = this.sumOfAllFears;
        this.selectedService = null;
        this.deactivateAllRows(this.serviceList); 
        this.refreshTablePage('serviceTable');
    },
    // clickAnimalRow() {
    //     console.log('animal click');
    // },
    // nextRow(table) {
    //     var mainTableIndex;
    //     if (this.commonShit.activeRowIndex !== null && this.commonShit.activeRowIndex !== undefined) {
    //         this.commonShit.activeRowIndex++;
    //         if (this.commonShit.activeRowIndex >= this.filteredList.length) this.commonShit.activeRowIndex = this.filteredList.length-1;
    //         else {
    //             mainTableIndex = table.findIndex(x => x.id === this.filteredList[this.commonShit.activeRowIndex].id);
    //             this.rowIsActivated(table, mainTableIndex);
    //         }
            
