    // },
    // previousRow(table) {
    //     var mainTableIndex;
    //     if (this.commonShit.activeRowIndex !== null && this.commonShit.activeRowIndex !== undefined) {
    //         this.commonShit.activeRowIndex--;
    //         if (this.commonShit.activeRowIndex < 0) this.commonShit.activeRowIndex = 0;
    //         {
    //             mainTableIndex = table.findIndex(x => x.id === this.filteredList[this.commonShit.activeRowIndex].id);
    //             this.rowIsActivated(table, mainTableIndex);
    //         }
    //     }
    // },
// ============================================================= INVOICE =========================================
        generateInvoice() {
          if (this.selectedCustomer == null || this.mainList.length == 0) {
            console.log('conditions not met');
            return;
          }
            this.invoice.Lines = [];
            this.invoice.CustomerId = this.selectedCustomer.id;
            if (this.selectedAnimal !== null) {
              this.invoice.CustomerAnimal = this.selectedAnimal.id;
            }
            else {
              this.invoice.CustomerAnimal = null;
            }
            
            this.invoice.EmpId = 176 // temp
            // generate lines
            for (var i = 0; i < this.mainList.length; i++) {
              // {"ID":3, "QTY":"2.4" ,"PRICE":"2.12", "PORTION":1, "PORTIONUOMID":1, "FREQ":1, "FREQUOMID":1 ,"HOWLONG":3,"DISCOUNT":21, "TYP":0},
              // item: newAss.assortName,
              //   description: newAss.assortDesc,
              //   quantity: this.itemQuantity,
              //   pricePerUnit: 1,
              //   // for JSON
              //   itemId: newAss.id,
              //   portion: this.dosage.portion,
              //   portionUnit: this.dosage.massUnit,
              //   freq: this.dosage.frequency,
              //   freqUnit: this.dosage.frequencyUnit,
              //   howlong: null,
              //   typ: 0,
              var line = {
                "ID": this.mainList[i].itemId,
                "QTY": this.mainList[i].quantity,
                "DISCOUNT": this.discountPercent,
                "TYP":this.mainList[i].typ,
                "PRICE": this.mainList[i].pricePerUnit,
              };
              if (this.mainList[i].typ === 0) {
                line["PORTION"] = this.mainList[i].portion;
                line["PORTIONUOMID"] = this.mainList[i].portionUnit;
                line["FREQ"] = this.mainList[i].freq;
                line["FREQUOMID"] = this.mainList[i].freqUnit;
                line["HOWLONG"] = this.mainList[i].howlong;
              }
              this.invoice.Lines.push(line);
            }
            // this.invoiceJSON.Lines = lines;
            this.invoiceJSON = JSON.stringify(this.invoice);
            console.log(this.invoiceJSON);
        if (this.invoiceJSON.length > 2) {
                this.addJSON(this.invoiceJSON, "");
            }
        },
        async addJSON(jsonData, jsonType) {
            // var newItem = {json: jsonData, jsonType: jsonType}
            var newItem = {json: jsonData}
            var result = await this.axiosAddData('invoice', newItem);
            if (this.axeman.success) {
                Messenger().post(this.$t('common.saveSuccessful'));
            }
            else {
                console.log(this.axeman.error);
                Messenger().post({ message: this.axeman.error, type: "error" });
            }
            this.axemanReset();
        },
// ============================================================= MODALS ========================================= 
        showDelConfirm(row) {
            this.commonShit.activeRowId = row.id;
            this.commonShit.delConfirmShow = true;
            this.commonShit.delModalMessage = this.delCustomMessage(row);
        },
        hideDelConfirm() {
            this.commonShit.delConfirmShow = false;
            this.commonShit.delModalMessage = '';
            this.commonShit.activeRowId = null;
        },
        delCustomMessage(row) {
          return row.item + ' (' + row.quantity + ')';
        },
        closeThis() {
          this.showCustomerChoice = false;
          this.showAnimalChoice = false;
          this.showAssChoice = false;
          this.showServiceChoice = false;
          this.itemQuantity = 1;
        },
// ============================================================= HEADINGS =========================================
        setTableHeadings() {
          // this.options.headings.id = this.$t("sales.thingName");
          this.options.headings.item = this.$t("sales.item");
          this.options.headings.description = this.$t("sales.description");
          this.options.headings.quantity = this.$t("sales.quantity");
          this.options.headings.total = this.$t("sales.total");
          this.options.headings.pricePerUnit = this.$t("sales.pricePerUnit");
          // customer list
          this.customerOptions.headings.custFirstname = this.$t('tableHeadings.name');
          this.customerOptions.headings.custLastname = this.$t('tableHeadings.lastname');
          this.customerOptions.headings.custMail = this.$t('tableHeadings.email');
          this.customerOptions.headings.custPhone = this.$t('tableHeadings.phone');
          this.customerOptions.headings.custBirthdate = this.$t('tableHeadings.dateOfBirth');
          this.customerOptions.headings.custAddress = this.$t('tableHeadings.address');
          this.customerOptions.headings.custCityCode = this.$t('tableHeadings.postalCode');
          this.customerOptions.headings.custCity = this.$t('tableHeadings.city');
          this.customerOptions.headings.custAgree1 = this.$t('tableHeadings.kidneyAgreed');
          this.customerOptions.texts = this.options.texts;

          this.ownedAnimalsOptions.headings.animChip = this.$t("tableHeadings.patientChip");
          this.ownedAnimalsOptions.headings.animBirthdate = this.$t("tableHeadings.patientBirthdate");
          this.ownedAnimalsOptions.headings.animName = this.$t("tableHeadings.patientName");
          this.ownedAnimalsOptions.headings.weight = this.$t("tableHeadings.patientWeight");
          this.ownedAnimalsOptions.headings.animSexDesc = this.$t("tableHeadings.patientGender");
          this.ownedAnimalsOptions.headings.animCoat = this.$t("tableHeadings.patientColor");
          this.ownedAnimalsOptions.headings.animSpeciesDesc = this.$t("tableHeadings.patientType");
          this.ownedAnimalsOptions.headings.animRaceDesc = this.$t("tableHeadings.patientRace");
          this.ownedAnimalsOptions.headings.animCastrated = this.$t("tableHeadings.patientCastrated");
          this.ownedAnimalsOptions.texts = this.options.texts;

          this.assOptions.headings.assortName = this.$t("assortment.name");
          this.assOptions.headings.assortDesc = this.$t("assortment.description");
          this.assOptions.headings.assortPrescriptionWord = this.$t("assortment.prescription");
          this.assOptions.headings.assortTypeWord = this.$t("tableHeadings.category");
          this.assOptions.texts = this.options.texts;

          this.serviceOptions.headings.servicName = this.$t("tableHeadings.thingName");
          this.serviceOptions.headings.serviceTypeWord = this.$t("tableHeadings.category");
          this.serviceOptions.headings.servicCost = this.$t("tableHeadings.price");
          this.serviceOptions.headings.servicDesc = this.$t("tableHeadings.description");
          this.serviceOptions.headings.servicDuration = this.$t("tableHeadings.duration");
          this.serviceOptions.headings.servicTaxId = this.$t("tableHeadings.taxId");
          this.serviceOptions.texts = this.options.texts;
        },
    },
    // ============================================================= LIFECYCLE HOOKS ========================================= 
    async created () {
      var result;
      //debugger;
    //   console.log('%c====  ' + this.commonShit.pageMainApi + '  ====', 'font-weight: bold; background-color: black; color:lightblue;');
    //   let route = this.$route.params.id;
      // if (route !== undefined  && route !== undefined ){

      axios.defaults.headers.common["Authorization"] = "Bearer " + localStorage.token;
      // this.mainList = this.fakeData;
      this.refreshIndexes();
      
      // var result = await this.refreshType("serviceType");
      // this.serviceTypes = JSON.parse(JSON.stringify(this.temp));
      this.setTableHeadings();
      this.summary.subtotal = this.sumOfAllFears;
      
      result = await this.loadList('customer');
      result = await this.loadList('animal');
      // for (var i = 0; i < this.customerList.length; i++)
      //     this.customerList[i].label = this.customerList[i].custFirstname + ' ' + this.customerList[i].custLastname;
      this.deactivateAllRows(this.customerList);
      
      this.checkOnArrival();
      if (!this.cacheExists("DDL/UOM")) {
        var result = await this.axiosGetData("DDL/UOM", this.massUnits);
        this.massUnits = this.massUnits.gotFromApi.model;
        this.cacheFill({name: 'DDL/UOM', data:  this.massUnits, updateDate: '2019-10-10'});
      }
      else {
        this.massUnits = this.getCache('DDL/UOM');
      }
      // console.log(this.massUnits);
      this.refreshTablePage('customerTable'); 
    },
    updated() {
      this.setTableHeadings();
    },
    
    
}
