<template>
    <div>
      <b-breadcrumb class="testTop">
            <b-breadcrumb-item>{{ $t('common.youAreHere').toUpperCase() }}</b-breadcrumb-item>
            <b-breadcrumb-item active>
               {{$t('storage.pageName')}}
            </b-breadcrumb-item>
        </b-breadcrumb>
        <b-row>
            <b-col lg="12" xs="12">
                <Widget title="" customHeader close collapse fullscreen>
                    <action-buttons
                        :addActive="commonShit.addAvailable"
                        :deleteActive="commonShit.editMode"
                        :saveActive="commonShit.saveAvailable"
                        :nextActive="commonShit.activeRowIndex < (this.filteredList.length - 1)"
                        :previousActive="commonShit.activeRowIndex > 0"
                        showTooltips="storage"
                        @actionAddNew="showAddNew"
                        @actionDelete="showDelConfirm(commonShit.activeRow)"
                        @actionSave="saveData"
                        @actionNext="nextRowCheck(mainList)"
                        @actionPrevious="previousRowCheck(mainList)"
                    />
                    <span v-if="commonShit.editMode">
                        <em>{{this.$t('common.selected') }}: <span class="text-info">{{ commonShit.activeRowMessage }}</span></em>
                    </span>
                    <b-tabs class="mb-lg" v-model="activeTab" no-key-nav>
                        <b-tab :title="$t(this.commonShit.pageMainApi + '.list')">
                            <div class="widget-middle-overflow">
                                <!-- <b-button @click="showAddNew" type="button" variant="primary">
                                    {{ $t(this.commonShit.pageMainApi + '.addNew') }}
                                </b-button> -->
                                <span @click="exportFilteredList">
                                    <ExportToExcelButton  :theList="exportedList" :columns="columns" :columnNames="options.headings" :listName="commonShit.pageMainApi">
                                    </ExportToExcelButton>
                                </span>
                                <v-client-table 
                                    :data="mainList" 
                                    :columns="columns" 
                                    :options="options"
                                    @row-click="editRow"
                                    ref="mainTable"
                                    :key="tableKey">
                                    <!-- <span slot="actions" slot-scope="{row}">
                                        <b-button
                                        @click="showDelConfirm(row)"
                                        type="button"
                                        class="btn btn-square btn-danger"
                                        >{{ $t('common.deleteSomething') }}</b-button>
                                    </span> -->
                                </v-client-table>
                            </div>
                        </b-tab>
                        <b-tab :title="$t(this.commonShit.pageMainApi + '.details')" :disabled="!detailsTabEnabled">
                            <div class="widget-middle-overflow">
                        <button @click="validationOn = !validationOn">Validation: {{ validationOn }}</button>
                    <b-form>
                        <b-row>
                           <b-col lg="6" xs="12"> 
                        <fieldset>
                            <b-form-group id="input-group-1" :label="$t('storage.name')" label-for="warehouseName">
                                <b-form-input id="warehouseName" name="warehouseName" :placeholder="$t('placeholders.empty')"
                                            :class="{ 'form-control': true, 'is-invalid': errors.has('warehouseName')}"
                                            v-validate="'required'" data-vv-as="Warehouse Name"
                                            v-model="form.magDesc"
                                >
                                </b-form-input>
                                <span class="validation-error" v-show="errors.has('warehouseName')">{{ errors.first('warehouseName') }}</span>
                            </b-form-group>
                            <b-form-group id="input-group-2" :label="$t('storage.code')" label-for="warehouseCode">
                                <b-form-input id="warehouseCode" name="warehouseCode" :placeholder="$t('placeholders.empty')"
                                            :class="{ 'form-control': true, 'is-invalid': errors.has('warehouseCode')}"
                                            v-validate="'required'" data-vv-as="Warehouse Code"
                                            v-model="form.magCode"
                                >
                                </b-form-input>
                                <span class="validation-error" v-show="errors.has('warehouseCode')">{{ errors.first('warehouseCode') }}</span>
                            </b-form-group>
                            <b-form-group id="input-group-3" :label="$t('tableHeadings.email')" label-for="email">
                                <b-form-input id="email" name="email" :placeholder="$t('placeholders.email')"
                                            :class="{ 'form-control': true, 'is-invalid': errors.has('email')}"
                                            v-validate="'required|email'" data-vv-as="Email"
                                            v-model="form.magEmail"      
                                >
                                </b-form-input>
                                <span class="validation-error" v-show="errors.has('email')">{{ errors.first('email') }}</span>
                            </b-form-group>
                            <b-form-group id="input-group-4" :label="$t('tableHeadings.phone')" label-for="phone">
                                <b-form-input id="phone" name="phone" :placeholder="$t('placeholders.phone')"
                                            type="tel" v-mask="'+### ### ### ###'"
                                            :class="{ 'form-control': true, 'is-invalid': errors.has('phone')}"
                                            v-validate="'required'" data-vv-as="Phone"
                                            v-model="form.magPhone"
                                >
                                </b-form-input>
                                <span class="validation-error" v-show="errors.has('phone')">{{ errors.first('phone') }}</span>
                            </b-form-group>
                        </fieldset>
                           </b-col>
                           <b-col lg="6" xs="12">
                               <b-form-group id="input-group-6" :label="$t('tableHeadings.street')" label-for="street">
                                    <b-form-input id="street" name="street" :placeholder="$t('placeholders.street')"
                                                  :class="{ 'form-control': true, 'is-invalid': errors.has('street')}"
                                                  v-validate="''" data-vv-as="Street"
                                                  v-model="form.magStreet" 
                                    >
                                    </b-form-input>
                                    <span class="validation-error" v-show="errors.has('street')">{{ errors.first('street') }}</span>
                                </b-form-group>
                                <b-row>
                                <b-col lg="6" xs="12">
                                    <b-form-group id="input-group-7" :label="$t('tableHeadings.house')" label-for="house">
                                      <b-form-input id="house" name="house" :placeholder="$t('placeholders.house')"
                                                    :class="{ 'form-control': true, 'is-invalid': errors.has('house')}"
                                                    v-validate="'alpha_num'" data-vv-as="House"
                                                    v-model="form.magHouse"
                                      >
                                      </b-form-input>
                                      <span class="validation-error" v-show="errors.has('house')">{{ errors.first('house') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="6" xs="12">
                                  
                                    <b-form-group id="input-group-8" :label="$t('tableHeadings.flat')" label-for="flat">
                                      <b-form-input id="flat" name="flat" :placeholder="$t('placeholders.flat')"
                                                    :class="{ 'form-control': true, 'is-invalid': errors.has('flat')}"
                                                    v-validate="'alpha_num'" data-vv-as="Flat"
                                                    v-model="form.magFlat"
                                      >
                                      </b-form-input>
                                      <span class="validation-error" v-show="errors.has('flat')">{{ errors.first('flat') }}</span>
                                    </b-form-group>
                                </b-col>
                                </b-row>
                                   
                                  <b-form-group id="input-group-9" :label="$t('tableHeadings.postalCode')" label-for="postalCode">
                                    <b-form-input id="postalCode" name="postalCode" :placeholder="$t('placeholders.postalCode')"
                                                  :class="{ 'form-control': true, 'is-invalid': errors.has('postalCode')}"
                                                  v-validate="'alpha_dash'" data-vv-as="Postal Code"
                                                  v-model="form.magCityCode"
                                    >
                                    </b-form-input>
                                    <span class="validation-error" v-show="errors.has('postalCode')">{{ errors.first('postalCode') }}</span>
                                  </b-form-group>
                                
                                
                                <b-form-group id="input-group-10" :label="$t('tableHeadings.city')" label-for="city">
                                    <b-form-input id="city" name="city" :placeholder="$t('placeholders.city')"
                                                  :class="{ 'form-control': true, 'is-invalid': errors.has('city')}"
                                                  v-validate="'alpha_spaces'" data-vv-as="City"
                                                  v-model="form.magCity"
                                    >
                                    </b-form-input>
                                    <span class="validation-error" v-show="errors.has('city')">{{ errors.first('city') }}</span>
                                </b-form-group>
                                <b-form-group :label="$t('headings.country')" label-for="country">
                                        <v-select searchable
                                                @change="countryChanged"
                                                id="country" name="country"
                                                :class="{'ddl-select' : true, 'invalid-select': errors.has('country')}"
                                                v-validate="'required'" data-vv-as="Country"
                                                v-model="form.magCountry"
                                                index="code"
                                                label="label"
                                                :options="this.countries"
                                        />
                                        <span class="validation-error" v-show="errors.has('country')">{{ errors.first('country') }}</span>
                                  </b-form-group>
                                <b-form-group :label="$t('tableHeadings.zone')" label-for="province">
                                    <v-select searchable
                                              id="province" name="province"
                                              :class="{'ddl-select': true, 'invalid-select': errors.has('province')}"
                                              v-validate="'required'" data-vv-as="Province"
                                              v-model="form.magProvince"
                                              index="code"
                                              label="label"
                                              :options="this.zones"
                                              :disabled="this.form.magCountry == null" 
                                    />
                                    <span class="validation-error" v-show="errors.has('province')">{{ errors.first('province') }}</span>
                                </b-form-group>
                           </b-col>
                        </b-row>
                        <!-- <b-form-group id="add-buttons" v-if="!commonShit.editMode">
                            <b-row align-v="center">
                                <b-col lg="12" xs="12">
                                    <b-button @click="onSubmit" variant="primary">{{$t(this.commonShit.pageMainApi + '.addNew')}}</b-button>
                                    &nbsp;&nbsp;
                                    <b-button type="reset" variant="secondary">{{$t('common.clearForm')}}</b-button>
                                </b-col>
                            </b-row>
                        </b-form-group>
                        <b-form-group id="add-buttons" v-if="commonShit.editMode">
                            <b-row align-v="center">
                                <b-col lg="12" xs="12">
                                    <b-button @click="onUpdateSubmit" variant="success">{{$t(this.commonShit.pageMainApi + '.update')}}</b-button>
                                    &nbsp;&nbsp;
                                    <b-button type="reset" variant="secondary">{{$t('common.clearForm')}}</b-button>
                                </b-col>
                            </b-row>
                        </b-form-group> -->
                    </b-form>
                    </div>
                    </b-tab>
                    <b-tab :title="$t(this.commonShit.pageMainApi + '.places')" :disabled="!commonShit.editMode">
                        {{ $t(this.commonShit.pageMainApi + '.place')}}
                    </b-tab>
                    <b-tab :title="$t(this.commonShit.pageMainApi + '.stock')" :disabled="!commonShit.editMode">
                        {{ $t(this.commonShit.pageMainApi + '.stock')}}
                    </b-tab>
                    <b-tab :title="$t(this.commonShit.pageMainApi + '.history')" :disabled="!commonShit.editMode">
                        {{ $t(this.commonShit.pageMainApi + '.history')}}
                    </b-tab>
                    </b-tabs>
                </Widget>
            </b-col>
        </b-row>
<!-- ============================================================= MODALS ======================================================================== -->
        <del-confirm-modal @delConfirmed="deleteRow" @delCancelled="hideDelConfirm"
            :showMe="this.commonShit.delConfirmShow"
            :delWhat="$t(this.commonShit.pageMainApi + '.delete')"
            :message="this.commonShit.delModalMessage" 
            :rowId="this.commonShit.activeRowId"
        />
        <!-- <dropdown-edit-modal @closed="hideDropdownEditor"
            :showMe="this.commonShit.dropdownEditShow" 
            :dropdown="this.commonShit.dropdownToEdit"
        /> -->
        <b-modal ref="saveChangesModal" id="saveChangesModal" content-class="darkModal" footer-class="centered" header-text-variant="light" 
            size="sm" centered ok-variant="success"
            :title="$t('common.saveChanges')" :ok-title="$t('common.saveChangesYes')" :cancel-title="$t('common.saveChangesNo')"
        ><h4 class="my-4 text-center">{{ $t('common.unsavedChanges') }}</h4>
        </b-modal>
  </div>
</template>

<script>
import Vue from 'vue';
import vSelect from 'vue-select';
import Widget from '@/components/Widget/Widget';
import DelConfirmModal from "@/components/Custom/DelConfirmModal.vue"
// import DropdownEditModal from "@/components/Custom/DropdownEditModal.vue"
import ActionButtons from "@/components/Custom/ActionButtons.vue"
import ExportToExcelButton from "@/components/Custom/ExportToExcelButton.vue"
// import { vueTableData, vueDatatableData } from '';
import axios from 'axios';
import { commonMixin } from "@/core/commonMixin";
import { axiosMixin } from "@/core/axiosMixin";
import { doubleClickMixin } from "@/core/doubleClickMixin";
import { saveChangesMixin } from "@/core/saveChangesMixin";
import { helpersMixin } from "@/core/helpersMixin";
import { mapGetters } from 'vuex';
import { mapActions } from 'vuex';

export default {
    components: { Widget, vSelect , DelConfirmModal, ActionButtons, ExportToExcelButton},
    mixins: [commonMixin, axiosMixin, doubleClickMixin, helpersMixin, saveChangesMixin],
    data: function() {
        return {
            commonShit: {
                pageMainApi: 'storage',
                // rest in commonMixin
            },
            
            form: {
                magCode: '',
                magDesc: '',
                magPhone: '',
                magEmail: '',
                magStreet: '',
                magHouse: '',
                magFlat: '',
                magCityCode: '',
                magCity: '',
                magProvince: null,
                magCountry: null,
                
            },
            tableKey: 0,
            temp: [],
            countries: [],
            zones: [],
            validationOn: true,
            activeTab: 0,
            columns: ['magCode','magDesc', 'magPhone' , 'magEmail', 'address', 'magCity', 'countryLabel'],
            warehouseList: [],
            options: {
                filterByColumn: true,
                perPage: 10,
                perPageValues: [],
                pagination: { chunk: 10, dropdown: false },
                skin: 'table table-striped table-hover',
                headings: {
                    magCode: 'Code',
                    magPhone: 'Phone',
                    magDesc: 'Name',
                    magEmail: 'Email',
                    address: 'Address',
                    magCity: 'City',
                    countryLabel: 'Country',
                    actions: ''

                },
                filterable: ['magCode','magDesc', 'magPhone' , 'magEmail', 'address', 'magCity', 'countryLabel'],
                sortable: ['magCode','magDesc', 'magPhone' , 'magEmail', 'address', 'magCity', 'countryLabel'],
        // filterable: ['name', 'lastname'],
            },
            
        }
        
    },

    computed: {
        ...mapGetters([
            'cacheExists',
            'getCache',
            'cacheItemsCount',
            'cacheLastUpdate',
            'cacheGetRowById'
        ])
    },
    
    methods: {
        ...mapActions([
            'cacheReset',
            'cacheFill',
            'cacheAddItem',
            'cacheDeleteItem',
            'cacheUpdateItem',
            'cacheSetUpdatedDate',
            'cacheUpdate'
        ]),
// ============================================================= ACTIVATING =========================================
    editRow(row) {
            var mainTableIndex;
            this.filteredList = this.$refs.mainTable.allFilteredData;
            // console.log(this.filteredList);
            this.commonShit.activeRowIndex = this.filteredList.findIndex(x => x.id === row.row.id); // not supported in IE
            mainTableIndex = this.mainList.findIndex(x => x.id === this.filteredList[this.commonShit.activeRowIndex].id);
            this.rowIsActivated(this.mainList, mainTableIndex);
            if (this.isClickedTwice(row.row.id)) {
                this.execDoubleClick(row, 1); // doubleClickMixin
                return;
            }
            this.rowClicked(row.row.id); // doubleClickMixin
        },
    nextRow(table) {
        var mainTableIndex;
        if (this.commonShit.activeRowIndex !== null && this.commonShit.activeRowIndex !== undefined) {
            this.commonShit.activeRowIndex++;
            if (this.commonShit.activeRowIndex >= this.filteredList.length) this.commonShit.activeRowIndex = this.filteredList.length-1;
            else {
                mainTableIndex = table.findIndex(x => x.id === this.filteredList[this.commonShit.activeRowIndex].id);
                this.rowIsActivated(table, mainTableIndex);
            }
            
        }
    },
    previousRow(table) {
        var mainTableIndex;
        if (this.commonShit.activeRowIndex !== null && this.commonShit.activeRowIndex !== undefined) {
            this.commonShit.activeRowIndex--;
            if (this.commonShit.activeRowIndex < 0) this.commonShit.activeRowIndex = 0;
            {
                mainTableIndex = table.findIndex(x => x.id === this.filteredList[this.commonShit.activeRowIndex].id);
                this.rowIsActivated(table, mainTableIndex);
            }
        }
    },
    rowIsActivated(table, index) {
        this.clearForm();
        var row = table[index];
        this.commonShit.activeRow = JSON.parse(JSON.stringify(row));
        this.commonShit.editMode = true;
        this.commonShit.activeRowId = row.id;
        this.detailsTabEnabled = true;
        
        this.formFill(row);
        this.formCloneCreate(this.form, 100);
        this.deactivateAllRows(table);
        this.activateRow(table[index]);
        this.setActiveMessage(row);
        // specific
        
    },
// ============================================================= REFRESHING LIST =========================================
        async refreshMainTable() {
            console.log('= main list');
            //var result = await this.axiosGetLastUpdate(this.commonShit.pageMainApi, this.axeman);
            //if (!this.cacheExists(this.commonShit.pageMainApi)) {
                var result = await this.axiosGetData(this.commonShit.pageMainApi, this.axeman);
                if (this.axeman.success == true)
                    // this.cacheFill({name: this.commonShit.pageMainApi, data: this.axeman.gotFromApi, updateDate: this.axeman.updated});  
            //} else if (this.cacheLastUpdate(this.commonShit.pageMainApi) !=  this.axeman.updated) {
                // console.log('Cache(' + this.commonShit.pageMainApi + ') update: ' + this.cacheLastUpdate(this.commonShit.pageMainApi));
                // update cache
                //result = await this.axiosGetUpdatedData(this.commonShit.pageMainApi, this.cacheLastUpdate(this.commonShit.pageMainApi), this.axeman);
                //if (this.axeman.success == true) {
                // console.log(this.axeman.gotFromApi);
                // this.cacheUpdate({name: this.commonShit.pageMainApi, data: this.axeman.gotFromApi, updateDate: this.axeman.updated});
            //     }
            // } else {
            //     console.log('Cache(' + this.commonShit.pageMainApi + ') update: ' + this.cacheLastUpdate(this.commonShit.pageMainApi));
            // }
            // this.mainList = this.formatDataForListing(this.getCache(this.commonShit.pageMainApi));
            
            this.mainList = this.formatDataForListing(this.axeman.gotFromApi);
            if (!this.commonShit.editMode) this.deactivateAllRows(this.mainList);
            // console.log(this.mainList);
            this.axemanReset();
             
        },
        formatDataForListing(data) {
            var index;
            
            for (var i = 0; i < data.length; i++) {
                data[i].address = this.formatAddress(data[i].magStreet, data[i].magHouse, data[i].magFlat, 'pl');
                data[i].countryLabel = this.getDropdownItemLabel(this.countries, data[i].magCountry);
                // data[i].equipWarrantyData = data[i].equipWarrantyData.split('T')[0];
                // index = this.serviceTypes.findIndex(x => x.rowid === data[i].servicTypeId);
                // data[i].serviceTypeWord = this.serviceTypes[index].key_Code;
            }
            return data;
        },
// ============================================================= ADDING =========================================
        // showAddNew() {
        //     this.clearForm();
        //     this.activeTab = 1;
        // },
    onSubmit() {
        if (this.commonShit.validationOn) {
            // console.log('validating');
            var result = this.$validator.validateAll()
            .then(valid => {
            if (valid) { 
                this.addNewItem(); 
            } else 
                console.log('%cValidation failed...', 'color:red;');
            return false;
            })
            .catch(() => { return false; });
        }
        else {
            this.addNewItem();
        } 
    },
    async addNewItem() {
      var newItem = this.mapFormData(); // warehouse
      var result = await this.axiosAddData(this.commonShit.pageMainApi, newItem);
      if (this.axeman.success) {
        Messenger().post(this.$t(this.commonShit.pageMainApi + '.added'));
        // ========== ONLY CACHING, NO SERVER WORKLOAD (ASSUMING NO SIMULTANEOUS EDITING)
        //this.cacheAddItem({ name: this.commonShit.pageMainApi, data: newItem, updateDate: this.axeman.updated});
        // TODO: Cache (potrzebny zwrot ID z bazy po dodaniu rekordu)
        this.refreshMainTable();
        this.clearForm();
      }
      else {
        console.log(this.axeman.error);
        Messenger().post({ message: this.axeman.error, type: "error" });
      }
      this.axemanReset();
    },
// ============================================================= UPDATING =========================================
    onUpdateSubmit() {
      if (this.commonShit.validationOn) {
        // console.log('validating');
        var result = this.$validator.validateAll()
        .then(valid => {
        if (valid) { 
          this.updateItem(); 
        } 
        else {
          console.log('%cValidation failed...', 'color:red;');
        } 
        return false;
        })
        .catch(() => { return false; });
      }
      else {
        this.updateItem();
      } 
    },
    async updateItem() {
      console.log('ID: ' + this.commonShit.activeRowId);

      var data = this.mapFormData();
      var path = this.commonShit.pageMainApi + "/" + this.commonShit.activeRowId; 

      var result = await this.axiosUpdateData(path, data);
      if (this.axeman.success == true) {
        this.setActiveMessage(data);
        Messenger().post(this.$t(this.commonShit.pageMainApi + '.updated') + ': ' + this.commonShit.activeRowMessage);
        data.id = this.commonShit.activeRowId;
        //this.cacheUpdateItem({ name: this.commonShit.pageMainApi, data: data, updateDate: this.axeman.updated });
        this.refreshMainTable();
        this.refreshTablePage(); // double click mixin
        this.formCloneCreate(this.form, 100);
        // this.commonShit.editMode = false;
        // this.commonShit.activeRowId = '';
        // this.clearForm();   
      } 
      else {
        console.log(this.axeman.error);
        Messenger().post({ message: this.axeman.error, type: "error" });
      }
    },
// ============================================================= DELETING =========================================
        async deleteRow(rowId) {
            this.commonShit.toDelete = "";
            this.commonShit.modalShow = false;
            // debugger;

            let path = this.commonShit.pageMainApi + '/' + rowId;
            // console.log(row.id);
            var result = await this.axiosDeleteData(path);
            if (this.axeman.success) {
                //this.cacheDeleteItem({ name: this.commonShit.pageMainApi, id: rowId, updateDate: this.axeman.updated});
                Messenger().post(this.$t(this.commonShit.pageMainApi + ".deleted"));
                this.refreshMainTable();
            } else {
                Messenger().post({ message: this.axeman.error, type: 'error' , showCloseButton: true});
            }
                this.commonShit.editMode = false;
                this.clearForm();
        },
// ============================================================= FORM HANDLING =========================================
        clearForm(e) {
            if (e !== undefined) e.preventDefault();   
            for (var prop in this.form){
                if(this.form.hasOwnProperty(prop)){
                    this.form[prop] = '';
                }
            }
            this.commonShit.editedRowId = '',
            this.commonShit.editMode = false;
            this.$validator.reset();
            this.deactivateAllRows(this.mainList);
            this.formCloneClear();
            // form specific
            this.form.magCountry = null;
        },
        formFill(data) {
            for (var prop in this.form){
                if(this.form.hasOwnProperty(prop) && data.hasOwnProperty(prop)){
                    this.form[prop] = data[prop];
                }
            }
        },
        mapFormData() {
            this.form.magDesc = this.capitalizeFirstLetter(this.form.magDesc);
            this.form.magStreet = this.capitalizeFirstLetter(this.form.magStreet);
            this.form.magCity = this.capitalizeFirstLetter(this.form.magCity);
            var data = JSON.parse(JSON.stringify(this.form));
            // form specific
            
            return data;
        },
// ============================================================= MODALS =========================================      
        showDelConfirm(row) {
            this.commonShit.delConfirmShow = true;
            this.commonShit.delModalMessage = this.delCustomMessage(row);
        },
        hideDelConfirm() {
            this.commonShit.delConfirmShow = false;
            this.commonShit.delModalMessage = '';
        },
        showDropdownEditor(dropdown) {
            this.commonShit.dropdownToEdit = dropdown;
            this.commonShit.dropdownEditShow = true;
        },
        hideDropdownEditor() {
            this.commonShit.dropdownEditShow = false;
            this.commonShit.dropdownToEdit = '';
        },
// ============================================================= HEADINGS =========================================
        setTableHeadings() {
            this.options.headings.magCode = this.$t("storage.code");
            this.options.headings.magPhone = this.$t("tableHeadings.phone");
            this.options.headings.magDesc = this.$t("storage.name");
            this.options.headings.magEmail = this.$t("tableHeadings.email");
            this.options.headings.address = this.$t("tableHeadings.address");
            this.options.headings.magCity = this.$t("tableHeadings.city");
            this.options.headings.countryLabel = this.$t("headings.country");      
        },
// ============================================================= PAGE SPECIFIC =========================================
        saveChangesCondition() {
            return (this.commonShit.editMode && this.formCloneCompare(this.form) == false);
        },
        dontSaveChanges() {
            this.formReverseChanges(this.form);
            //this.formCloneCreate(this.form, 0);
        },
        setActiveMessage(row) {  
                this.commonShit.activeRowMessage = row.magDesc + ' [' + row.magCode + ']';
        },
        delCustomMessage(row) {
            return row.magDesc + ' [' + row.magCode + ']';
        },
        async countryChanged() {
            if (this.form.magCountry == null) {
                this.form.magProvince = null
                this.zones = [];
                // this.$refs.provinceSelect.disabled = true;
            }
            else {
                if (!this.cacheExists("DDL/Province_" + this.form.magCountry)) {
                    var result = await this.axiosGetDataForId("DDL/Province", this.form.magCountry, this.zones);
                    this.zones = this.zones.gotFromApi.model;
                    this.cacheFill({name: 'DDL/Province_' + this.form.magCountry, data:  this.zones, updateDate: '2019-10-10'});
                    // this.$refs.provinceSelect.disabled = false;
                }
                else {
                    this.zones = this.getCache('DDL/Province_' + this.form.magCountry);
                }
            }
            // }
        }
    },
// ============================================================= LIFECYCLE HOOKS =========================================  
    async created () {
        //debugger;
        // console.log('%c====  ' + this.commonShit.pageMainApi + '  ====', 'font-weight: bold; background-color: black; color:lightblue;');
        this.setTableHeadings();
        //   let route = this.$route.params.id;
        
        axios.defaults.headers.common["Authorization"] = "Bearer " + localStorage.token;

        // var result = await this.refreshType("equipmentType");
        // this.equipmentTypes = JSON.parse(JSON.stringify(this.temp));
        
        
        if (!this.cacheExists("DDL/Country")) {
            var result = await this.axiosGetData("DDL/Country", this.countries);
            this.countries = this.countries.gotFromApi.model;
            this.cacheFill({name: 'DDL/Country', data:  this.countries, updateDate: '2019-10-10'});
        }
        else {
            this.countries = this.getCache('DDL/Country');
        }
        this.refreshMainTable();
    },

    updated() {
        this.setTableHeadings();
    },
}
</script>

