<script>
import Vue from 'vue';
import vSelect from 'vue-select';
import Widget from '@/components/Widget/Widget';
// import { vueTableData, vueDatatableData } from '';
import axios from 'axios';
import { axiosMixin } from "@/core/axiosMixin";
// import vSelect from '@/components/vue-select';
export default {
    components: { Widget, vSelect },
    mixins: [axiosMixin],
    data: function() {
        return {
            commonShit: {
                pageMainApi: 'openHour',
                // rest in commonMixin
            },
            days: [
                { from: [], to: [], count: 1, },
                { from: [], to: [], count: 1, },
                { from: [], to: [], count: 1, },
                { from: [], to: [], count: 1, },
                { from: [], to: [], count: 1, },
                { from: [], to: [], count: 1, },
                { from: [], to: [], count: 1, },
            ],
            openHours: [],
            
        }
    },
    
    methods: {
        onSubmit(e) {
            e.preventDefault();
            // this.$validator.validateAll();
            //this.addNewEmployee();
            var voorhees = [];
            var dayNumber = 0;
            for (dayNumber = 0; dayNumber < this.days.length; dayNumber++) {
                // console.log(this.days[dayNumber]);
                if (this.days[dayNumber].from.length > this.days[dayNumber].to.length)
                    this.days[dayNumber].from.length = this.days[dayNumber].to.length
                for (var i = 1; i < this.days[dayNumber].from.length; i++) {
                    if (this.days[dayNumber].from[i] !== "" && this.days[dayNumber].to[i] !== "" && this.days[dayNumber].from[i] !== undefined && this.days[dayNumber].to[i] !== undefined) {
                        voorhees.push({"WeekDayId" : dayNumber + 1, "DateFrom": "1900-01-01 " + this.days[dayNumber].from[i] + ":00", "DateTo": "1900-01-01 " + this.days[dayNumber].to[i] + ":00"});
                    }
                }
            }
            voorhees = JSON.stringify(voorhees);
            console.log(voorhees);
            if (voorhees.length > 2) {
                this.addJSON(voorhees, "OfficeOpenHour");
            }
        },
        async addJSON(jsonData, jsonType) {
            // var newItem = {json: jsonData, jsonType: jsonType}
            var newItem = {json: jsonData}
            var result = await this.axiosAddData(this.commonShit.pageMainApi, newItem);
            if (this.axeman.success) {
                Messenger().post(this.$t('common.saveSuccessful'));
            }
            else {
                console.log(this.axeman.error);
                Messenger().post({ message: this.axeman.error, type: "error" });
            }
            this.axemanReset();
        },
        resetOpenHours() {
            for (var i = 0; i < this.days.length; i++) {
                this.days[i].from = [];
                this.days[i].to = [];
                this.days[i].count = 1;
            }
        },
        async refreshOpenHours(officeID) {
            var result = await this.axiosGetData(this.commonShit.pageMainApi + '/1', this.axeman);
                if (this.axeman.success == true) {
                    this.openHours = this.axeman.gotFromApi;
                    this.resetOpenHours();
                    // console.log(this.openHours);
                    for (var i = 0; i < this.openHours.length; i++) {
                        if (this.openHours[i].officeId !== officeID)
                            continue;
                        var dayIndex = this.openHours[i].weekday - 1;
                        
                        // this.days[dayIndex].from[this.days[dayIndex].count - 1] = "12:23";
                        this.days[dayIndex].from[this.days[dayIndex].count] = this.openHours[i].beginDate.toString().split('T')[1];
                        this.days[dayIndex].to[this.days[dayIndex].count] = this.openHours[i].endDate.toString().split('T')[1];
                        this.days[dayIndex].count++;
                        // this.days[dayIndex].from.push(this.openHours[i].beginDate.toString().split('T')[1]);
                        // this.days[dayIndex].to.push(this.openHours[i].endDate.toString().split('T')[1]);
                        
                        
                        
                        // console.log(this.openHours[i].beginDate.toString().split('T')[1]);
                    }
                }
                // console.log(this.days);
        }
    },

    created () {
      //debugger;
    //   let route = this.$route.params.id;
      // if (route !== undefined  && route !== undefined ){

        let config = { Headers:{ Authorization: "Bearer " + localStorage.token}}
        console.log('%c====  ' + this.commonShit.pageMainApi + '  ====', 'font-weight: bold; background-color: black; color:lightblue;');
        // console.log(JSON.stringify(this.jSon));
        this.refreshOpenHours(1);
    },   
}
</script>

