<template>
    <div>
        <b-breadcrumb class="testTop">
            <b-breadcrumb-item>YOU ARE HERE</b-breadcrumb-item>
            <b-breadcrumb-item active>
                {{$t('menuSettingOfficeProfile')}}
            </b-breadcrumb-item>
        </b-breadcrumb>
      
        <p v-if="$route.params.id">{{ $route.params.id }}</p>
        <b-row>
            <b-col lg="8" xs="12">
                <Widget title=""
                        customHeader close collapse fullscreen>
                    <b-form @submit="onSubmit">
                        <fieldset>
                            <b-form-group id="input-group-1" :label="$t('menuSettingOfficeProfileName')" label-for="companyName">
                                <b-form-input id="companyName" name="companyName"
                                              :class="{'form-control': true, 'is-invalid': errors.has('companyName')}"
                                                v-validate="'required'" data-vv-as="Company Name"
                                                v-model="form.companyName" 
                                />
                                <span class="validation-error" v-show="errors.has('companyName')">{{ errors.first('companyName') }}</span> 
                            </b-form-group>
                            <b-form-group id="input-group-2" :label="$t('menuSettingOfficeProfileOfficeName')" label-for="clinicName">
                                <b-form-input id="clinicName" name="clinicName"
                                              :class="{'form-control': true, 'is-invalid': errors.has('clinicName')}"
                                                v-validate="''" data-vv-as="Clinic Name"
                                                v-model="form.clinicName"
                                />
                                <span class="validation-error" v-show="errors.has('clinicName')">{{ errors.first('clinicName') }}</span>
                            </b-form-group>
                            <b-row>
                                <b-col xs='4' md='6' class="b-col-print-6">
                                    <b-form-group id="input-group-3" :label="$t('menuSettingOfficeProfilePhone')" label-for="phone">
                                        <b-form-input id="phone" name="phone" placeholder=""
                                              :class="{'form-control': true, 'is-invalid': errors.has('phone')}"
                                                v-validate="'required'" v-mask="'+### ### ### ###'" data-vv-as="Phone number"
                                                v-model="form.phone"
                                        />
                                        <span class="validation-error" v-show="errors.has('phone')">{{ errors.first('phone') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col xs='4' md='6' class="b-col-print-6">
                                    <b-form-group id="input-group-4" :label="$t('tableHeadings.email')" label-for="email">
                                        <b-form-input id="email" name="email"
                                              :class="{'form-control': true, 'is-invalid': errors.has('email')}"
                                                v-validate="'required|email'" data-vv-as="Email"
                                                v-model="form.email"
                                        />
                                        <span class="validation-error" v-show="errors.has('email')">{{ errors.first('email') }}</span>
                                    </b-form-group>
                                </b-col>
                            </b-row>
                            <b-form-group id="input-group-5" :label="$t('headings.website')" label-for="website">
                                <b-form-input id="website" name="website"
                                              :class="{'form-control': true, 'is-invalid': errors.has('website')}"
                                                v-validate="''" data-vv-as="Website"
                                                v-model="form.website"
                                />
                                <span class="validation-error" v-show="errors.has('website')">{{ errors.first('website') }}</span>
                            </b-form-group>

                            <b-form-group id="input-group-6" :label="$t('headings.facebook')" label-for="facebook">
                                <b-form-input id="facebook" name="facebook"
                                              :class="{'form-control': true, 'is-invalid': errors.has('facebook')}"
                                                v-validate="''" data-vv-as="Facebook"
                                                v-model="form.facebook"
                                />
                                <span class="validation-error" v-show="errors.has('facebook')">{{ errors.first('facebook') }}</span>
                            </b-form-group>

                            <b-form-group id="input-group-7" :label="$t('headings.instagram')" label-for="instagram">
                                <b-form-input id="instagram" name="instagram"
                                              :class="{'form-control': true, 'is-invalid': errors.has('instagram')}"
                                                v-validate="''" data-vv-as="Instagram"
                                                v-model="form.instagram"
                                />
                                <span class="validation-error" v-show="errors.has('instagram')">{{ errors.first('instagram') }}</span>
                            </b-form-group>
                            <b-form-group id="input-group-8" :label="$t('menuSettingOfficeProfilDescription')" label-for="description">
                                <b-form-input id="description" name="description"
                                              :class="{'form-control': true, 'is-invalid': errors.has('description')}"
                                                v-validate="''" data-vv-as="Description"
                                                v-model="form.description"
                                />
                                <span class="validation-error" v-show="errors.has('description')">{{ errors.first('description') }}</span>
                            </b-form-group>
                        </fieldset>
                        <fieldset>
                            <b-row>
                                <b-col lg="6" xs="12">
                                    <b-form-group :label="$t('menuCustomerCreateAddress')" label-for="street" description="">
                                        <b-form-input id="street" name="street" placeholder=""
                                                type="text"
                                                :class="{'form-control': true, 'is-invalid': errors.has('street')}"
                                                v-validate="'required'" data-vv-as="Street" 
                                                v-model="form.street" 
                                        />
                                        <span class="validation-error" v-show="errors.has('street')">{{ errors.first('street') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="3" xs="12">
                                    <b-form-group :label="$t('menuCustomerCreateAddressHouse')" label-for="house" description="">
                                        <b-form-input id="house" name="house" placeholder=""
                                                type="text"
                                                :class="{'form-control': true, 'is-invalid': errors.has('house')}"
                                                v-validate="'required'" data-vv-as="House"  
                                                v-model="form.house" 
                                        />
                                        <span class="validation-error" v-show="errors.has('house')">{{ errors.first('house') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="3" xs="12">
                                    <b-form-group :label="$t('menuCustomerCreateAddressFlat')" label-for="flat" description="">
                                        <b-form-input id="flat" name="flat" placeholder=""
                                                type="text" 
                                                :class="{'form-control': true, 'is-invalid': errors.has('flat')}"
                                                v-validate="'required'" data-vv-as="Flat"
                                                v-model="form.flat" 
                                        />
                                        <span class="validation-error" v-show="errors.has('flat')">{{ errors.first('flat') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="5" xs="12">
                                    <b-form-group :label="$t('tableHeadings.postalCode')" label-for="postal" description="">
                                        <b-form-input id="postal" name="postal" placeholder=""
                                                type="text"
                                                :class="{'form-control': true, 'is-invalid': errors.has('postal')}"
                                                v-validate="'required'" data-vv-as="Postal code" 
                                                v-model="form.postal" 
                                        />
                                        <span class="validation-error" v-show="errors.has('postal')">{{ errors.first('postal') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="7" xs="12">
                                    <b-form-group :label="$t('tableHeadings.city')" label-for="city" description="">
                                        <b-form-input id="city" name="city" placeholder=""
                                                type="text"
                                                :class="{'form-control': true, 'is-invalid': errors.has('city')}"
                                                v-validate="'required'" data-vv-as="City" 
                                                v-model="form.city" 
                                        />
                                        <span class="validation-error" v-show="errors.has('city')">{{ errors.first('city') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="6" xs="12">
                                <b-form-group :label="$t('headings.country')" label-for="country">
                                        <v-select searchable
                                                @change="countryChanged"
                                                id="country" name="country"
                                                :class="{'ddl-select' : true, 'invalid-select': errors.has('country')}"
                                                v-validate="'required'" data-vv-as="Country"
                                                v-model="form.country"
                                                index="code"
                                                label="label"
                                                :options="this.countries"
                                        />
                                        <span class="validation-error" v-show="errors.has('country')">{{ errors.first('country') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="6" xs="12">
                                    <b-form-group :label="$t('headings.province')" label-for="province">
                                        <v-select searchable
                                                id="province" name="province" ref="provinceSelect"
                                                :class="{'ddl-select' : true, 'invalid-select': errors.has('province')}"
                                                v-validate="'required'" data-vv-as="Province"
                                                v-model="form.province"
                                                index="code"
                                                label="label"
                                                :options="this.zones"
                                                :disabled="form.country == null"
                                        />
                                        <!-- <b-button @click="showDropdownEditor('DDL/Zones')" class="editButton btn-outline-warning"> 
                                            <span class="glyphicon glyphicon-pencil mr-xs mb-xs"></span>
                                        </b-button> -->
                                        <span class="validation-error" v-show="errors.has('province')">{{ errors.first('province') }}</span>
                                    </b-form-group>
                                </b-col>
                            </b-row>
                        </fieldset>
                    </b-form>
                </Widget>
            </b-col>
            <b-col lg="4" xs="12">

                <Widget title=""
                        customHeader close collapse fullscreen>
                    <b-form @submit="onSubmit">
                        <fieldset>
                            <b-row align-v="center">
                                <b-col lg="12" xs="12">
                                    <b-form-checkbox-group id="checkboxes-4"  name="profileOptions" 
                                                            v-model="form.profiles" v-validate="'required'" data-vv-as="Profiles">
                                        <b-form-group class="abc-checkbox abc-checkbox-primary">
                                            <b-form-checkbox value="VET">{{$t('menuSettingOfficeProfilProfile1')}}</b-form-checkbox><br> 
                                            <b-form-checkbox value="HOTEL">{{$t('menuSettingOfficeProfilProfile2')}}</b-form-checkbox><br>
                                            <b-form-checkbox value="HAIRDRESSER">{{$t('menuSettingOfficeProfilProfile3')}}</b-form-checkbox><br>
                                            <b-form-checkbox value="TRAINER">{{$t('menuSettingOfficeProfilProfile4')}}</b-form-checkbox><br>
                                            <b-form-checkbox value="SPA">{{$t('menuSettingOfficeProfilProfile5')}}</b-form-checkbox><br>
                                            <b-form-checkbox value="BEHAVIOURIST">{{$t('menuSettingOfficeProfilProfile6')}}</b-form-checkbox><br>
                                            <b-form-checkbox value="CLINIC">{{$t('menuSettingOfficeProfilProfile7')}}</b-form-checkbox>   
                                        </b-form-group>
                                        <span class="validation-error" v-show="errors.has('profileOptions')">{{ errors.first('profileOptions') }}</span>
                                    </b-form-checkbox-group>
                                </b-col>
                                <b-col lg="12" xs="12">
                                    <b-form-group v-if="this.moreProfiles == true" :label="$t('menuSettingOfficeProfilMainProfile')" label-for="mainProfile">
                                        <v-select id="mainProfile" name="mainProfile" class="mt-xs"
                                                    v-model="form.mainProfile" v-validate="'required'" data-vv-as="Main profile"
                                                    label="text"
                                                    :options="form.profiles"
                                        />
                                        <span class="validation-error" v-show="errors.has('mainProfile')">{{ errors.first('mainProfile') }}</span>
                                    </b-form-group>
                                </b-col>
                            </b-row>
                            <b-button @click="onSubmit" variant="primary">{{$t('common.save')}}</b-button>
                        </fieldset>
                    </b-form>
                </Widget>
                <Widget close collapse>
                    <b-form>
                        <b-form-group>
                            <input
                                accept="image/*" @change="onChangeInputImage"
                                id="imageUpload" name="file" ref="imageFileInput"
                                type="file"  style="display:none"
                            />
                            <div class="fileinput fileinput-new fileinput-fix">
                                <div class="fileinput-new thumbnail">
                                <div v-if="imageFiles.length > 0">
                                    <div
                                    class="image-preview"
                                    v-for="image in imageFiles"
                                    :key="`img-id-${image.toString()}`"
                                    :style="{ backgroundImage: `url(${image.preview})`}"
                                    />
                                </div>
                                <div v-else class="image-preview-empty" @click="$refs.imageFileInput.click()">
                                    {{ $t('files.clickHereToChooseImage') }}
                                </div>
                                </div>
                            </div>
                            <b-button type="button" variant="primary" @click="imageFileUpload" :disabled="imageFiles.length <= 0">
                                {{ $t('files.upload') }}
                                </b-button>
                                &nbsp;
                                <b-button v-if="imageFiles.length > 0" type="button" variant="default" @click="imageFiles.pop(); $refs.imageFileInput.value = null;">
                                {{ $t('files.clear') }}
                                </b-button>
                        </b-form-group>
                    </b-form>
                </Widget>
            </b-col>
        </b-row>

    </div>
</template>

<script>
import Vue from 'vue';
import vSelect from 'vue-select';
import Widget from '@/components/Widget/Widget';
import axios from 'axios';
import { commonMixin } from "@/core/commonMixin";
import { axiosMixin } from "@/core/axiosMixin";
import { mapGetters } from 'vuex';
import { mapActions } from 'vuex';
// import vSelect from '@/components/vue-select';
export default {
    components: { Widget, vSelect },
    mixins: [commonMixin, axiosMixin],
    data: function() {
        return {
            form: {
                companyName: '',
                clinicName: '',
                phone: '',
                email: '',
                website: '',
                facebook: '',
                instagram: '',
                description: '',
                // address
                street: '',
                house: '',
                flat: '',
                postal: '',
                city: '',
                country: null,
                province: null,
                profiles: [],
                mainProfile: null,

            },
            profileOptions: [
                { text: 'Weterynarz', value: 'VET' },
                { text: 'Hotel', value: 'HOTEL' },
                { text: 'SPA', value: 'SPA' },
                { text: 'Behawiorysta', value: 'BEHAVIOURIST' }
            ],
            imageFiles: [],
            zones: [],
            countries: []
        }
    },
    
    methods: {
        ...mapActions([
            'cacheReset',
            'cacheFill',
            'cacheAddItem',
            'cacheDeleteItem',
            'cacheUpdateItem',
            'cacheSetUpdatedDate',
            'cacheUpdate'
      ]),
        onSubmit(e) {
            e.preventDefault();
            // --------------------------------------------------------------------------------------------------
            // Jak zapisujemy Profil firmy to du liter pola:
            // Nazwa, nazwa gabinetu, opis ulica, miasto
            // --------------------------------------------------------------------------------------------------
            let result = this.$validator.validateAll()
                .then(valid => {
                if (valid) {
                    this.saveProfile();
                }
                return false;
                })
                .catch(() => { return false; });
        },
        onChangeInputImage(event) {
            console.log('Image file change');
            const files = [];
            const reader = new FileReader();
            files.push(event.target.files[0]);
            reader.onloadend = () => {
                files[0].preview = reader.result;
                files[0].toUpload = true;
                Vue.set(this, 'imageFiles', files);
            };
            reader.readAsDataURL(event.target.files[0]);
        },
        imageFileUpload() {
            console.log ('TODO: Image file upload');
        },
        saveProfile() {
            console.log('TODO: Save profile');
        },
        async countryChanged() {
        // console.log(this.customer.custCountry);
        if (this.form.country == null) {
            this.form.province = null
            this.zones = [];
            // this.$refs.provinceSelect.disabled = true;
        }
        else {
            if (!this.cacheExists("DDL/Province_" + this.form.country)) {
                var result = await this.axiosGetDataForId("DDL/Province", this.form.country, this.zones);
                this.zones = this.zones.gotFromApi.model;
                this.cacheFill({name: 'DDL/Province_' + this.form.country, data:  this.zones, updateDate: '2019-10-10'});
                // this.$refs.provinceSelect.disabled = false;
            }
            else {
                this.zones = this.getCache('DDL/Province_' + this.form.country);
            }
        }
        // }
    } 
    },
    computed: {
        moreProfiles() {
            return (this.form.profiles.length > 1) ? true : false;
        },
        ...mapGetters([
            'cacheExists',
            'getCache',
            'cacheItemsCount',
            'cacheLastUpdate',
            'cacheGetRowById'
        ]),
    },
    async created () {
    // console.log('%c====  ' + this.commonShit.pageMainApi + '  ====', 'font-weight: bold; background-color: black; color:lightblue;');
        axios.defaults.headers.common["Authorization"] = "Bearer " + localStorage.token;

        if (!this.cacheExists("DDL/Country")) {
            var result = await this.axiosGetData("DDL/Country", this.countries);
            this.countries = this.countries.gotFromApi.model;
            this.cacheFill({name: 'DDL/Country', data:  this.countries, updateDate: '2019-10-10'});
        }
        else {
            this.countries = this.getCache('DDL/Country');
        }
    
    }
    
    
}
</script>

<style>
    .image-preview, .image-preview-empty {
    width: 200px;
    height: 150px;
    background-size: cover;
    background-position: center;
    border: 1px solid wheat;
    padding: 1px;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  .image-preview-empty {
      display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
  .image-preview-empty:hover {
      background-color: rgba(0,0,0,0.1);
  }
</style>