<template>
    <div>
        <b-breadcrumb class="testTop">
            <b-breadcrumb-item>{{$t('common.youAreHere').toUpperCase()}}</b-breadcrumb-item>
            <b-breadcrumb-item active>
                {{$t('menuSettingEquipment')}}
            </b-breadcrumb-item>
        </b-breadcrumb>
        <p v-if="$route.params.id">{{ $route.params.id }}</p>
        
        <b-row>
            <b-col lg="12" xs="12">
                <Widget title="" customHeader close collapse fullscreen>
                    <action-buttons
                        :addActive="commonShit.addAvailable"
                        :deleteActive="commonShit.editMode"
                        :saveActive="commonShit.saveAvailable"
                        :nextActive="commonShit.activeRowIndex < (this.filteredList.length - 1)"
                        :previousActive="commonShit.activeRowIndex > 0"
                        showTooltips="equipment"
                        @actionAddNew="showAddNew"
                        @actionDelete="showDelConfirm(commonShit.activeRow)"
                        @actionSave="saveData"
                        @actionNext="nextRowCheck(mainList)"
                        @actionPrevious="previousRowCheck(mainList)"
                    />
                    <span v-if="commonShit.editMode">
                        <em>{{this.$t('common.selected') }}: <span class="text-info">{{ commonShit.activeRowMessage }}</span></em>
                    </span>
                    <b-tabs class="mb-lg" v-model="activeTab" no-key-nav>
                        <b-tab :title="$t(this.commonShit.pageMainApi + '.list')">
                            <div class="widget-middle-overflow">
                        <!-- <b-button @click="showAddNew" type="button" variant="primary">
                            {{ $t(this.commonShit.pageMainApi + '.addNew') }}
                        </b-button> -->
                        <span @click="exportFilteredList">
                            <ExportToExcelButton  :theList="exportedList" :columns="columns" :columnNames="options.headings" :listName="commonShit.pageMainApi">
                            </ExportToExcelButton>
                        </span>
                        <v-client-table 
                            :data="mainList" 
                            :columns="columns" 
                            :options="options"
                            @row-click="editRow"
                            ref="mainTable"
                            :key="tableKey">
                            <!-- <span slot="actions" slot-scope="{row}">
                                <b-button
                                @click="showDelConfirm(row)"
                                type="button"
                                class="btn btn-square btn-danger"
                                >{{ $t('common.deleteSomething') }}</b-button>
                            </span> -->
                        </v-client-table>
                    </div>
                        </b-tab>
                        <b-tab :title="$t(this.commonShit.pageMainApi + '.details')" :disabled="!detailsTabEnabled">
                            <div class="widget-middle-overflow">
                        <button @click="commonShit.validationOn = !commonShit.validationOn">Validation: {{ commonShit.validationOn }}</button>
<!-- ============================================================= MAIN FORM ======================================================================== -->
                    <b-form>
                        <fieldset>
                            <b-form-group id="input-group-1" :label="$t('menuSettingEquipmentName')" label-for="equipmentName">
                                <b-form-input id="equipmentName" name="equipmentName" placeholder=""
                                            :class="{ 'form-control': true, 'is-invalid': errors.has('equipmentName')}"
                                            v-validate="'required'" data-vv-as="Equipment Name"
                                            v-model="form.equipName"
                                >
                                </b-form-input>
                                <span class="validation-error" v-show="errors.has('equipmentName')">{{ errors.first('equipmentName') }}</span>
                            </b-form-group>
                            <b-form-group id="input-group-2" :label="$t('menuSettingEquipmentDescription')" label-for="equipmentDescription">
                                <b-form-input id="equipmentDescription" name="equipmentDescription" placeholder=""
                                            :class="{ 'form-control': true, 'is-invalid': errors.has('equipmentDescription')}"
                                            v-validate="'required'" data-vv-as="Equipment Description"
                                            v-model="form.equipDesc"
                                >
                                </b-form-input>
                                <span class="validation-error" v-show="errors.has('equipmentDescription')">{{ errors.first('equipmentDescription') }}</span>
                            </b-form-group>
                            <b-form-group id="input-group-3" :label="$t('equipment.serial')" label-for="equipmentSerial">
                                <b-form-input id="equipmentSerial" name="equipmentSerial" placeholder=""
                                            :class="{ 'form-control': true, 'is-invalid': errors.has('equipmentSerial')}"
                                            v-validate="'required'" data-vv-as="Serial number"
                                            v-model="form.equipSerialNum"      
                                >
                                </b-form-input>
                                <span class="validation-error" v-show="errors.has('equipmentSerial')">{{ errors.first('equipmentSerial') }}</span>
                            </b-form-group>
                            <b-form-group id="input-group-4" :label="$t('equipment.warranty')" label-for="equipmentWarranty">
                                <b-form-input id="equipmentWarranty" name="equipmentWarranty"
                                            type="date"
                                            :class="{ 'form-control': true, 'is-invalid': errors.has('equipmentWarranty')}"
                                            v-validate="'required'" data-vv-as="Warranty date"
                                            v-model="form.equipWarrantyData"
                                >
                                </b-form-input>
                                <span class="validation-error" v-show="errors.has('equipmentWarranty')">{{ errors.first('equipmentWarranty') }}</span>
                            </b-form-group>
                            <b-form-group id="input-group-5" :label="$t('menuSettingEquipmentCategory')" label-for="equipmentCategory">
                                <v-select searchable
                                        id="equipmentCategory" name="equipmentCategory"
                                        :class="{'editableSelect' : true, 'invalid-select': errors.has('equipmentCategory')}"
                                        v-validate="'required'" data-vv-as="Category"
                                        v-model="form.equipTypeId"
                                        label="key_Code"
                                        index="rowid"
                                        :options="this.equipmentTypes" 
                                />
                                <b-button @click="showDropdownEditor('equipmentType')" class="editButton btn-outline-warning"> 
                                    <span class="glyphicon glyphicon-pencil mr-xs mb-xs"></span>
                                </b-button>
                                <span class="validation-error" v-show="errors.has('equipmentCategory')">{{ errors.first('equipmentCategory') }}</span>
                            </b-form-group>
                            <b-row class="mb-lg">
                                <b-col sm='6' class="b-col-print-6">
                                    <b-form-group id="input-group-6" :label="$t('menuSettingEquipmentPrice')" label-for="equipmentCost">
                                        <b-form-input id="equipmentCost" name="equipmentCost" placeholder=""
                                                    :class="{ 'form-control': true, 'is-invalid': errors.has('equipmentCost')}"
                                                    v-validate="{ required:true, decimal: [2, ',']}" data-vv-as="Cost"
                                                    v-model="form.equipPrice"
                                        >
                                        </b-form-input>
                                        <span class="validation-error" v-show="errors.has('equipmentCost')">{{ errors.first('equipmentCost') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col sm='6' class="b-col-print-6">

                                </b-col>
                            </b-row>

                        </fieldset>
                        <!-- <b-form-group id="add-buttons" v-if="!commonShit.editMode">
                            <b-row align-v="center">
                                <b-col lg="12" xs="12">
                                    <b-button @click="onSubmit" variant="primary">{{$t(this.commonShit.pageMainApi + '.addNew')}}</b-button>
                                    &nbsp;&nbsp;
                                    <b-button @click="clearForm" variant="secondary">{{$t('common.clearForm')}}</b-button>
                                </b-col>
                            </b-row>
                        </b-form-group>
                        <b-form-group id="add-buttons" v-if="commonShit.editMode">
                            <b-row align-v="center">
                                <b-col lg="12" xs="12">
                                    <b-button @click="onUpdateSubmit" variant="success">{{$t(this.commonShit.pageMainApi + '.update')}}</b-button>
                                    &nbsp;&nbsp;
                                    <b-button @click="clearForm" variant="secondary">{{$t('common.clearForm')}}</b-button>
                                </b-col>
                            </b-row>
                        </b-form-group> -->
                    </b-form>
                    </div>
                </b-tab>
                <b-tab :title="$t('common.attachments')" :disabled="!commonShit.editMode">
                </b-tab>
                    </b-tabs>
                </Widget>
            </b-col>
        </b-row>
<!-- ============================================================= MODALS ======================================================================== -->
        <del-confirm-modal @delConfirmed="deleteRow" @delCancelled="hideDelConfirm"
            :showMe="this.commonShit.delConfirmShow"
            :delWhat="$t(this.commonShit.pageMainApi + '.delete')"
            :message="this.commonShit.delModalMessage" 
            :rowId="this.commonShit.activeRowId"
        />
        <dropdown-edit-modal @closed="hideDropdownEditor"
            :showMe="this.commonShit.dropdownEditShow" 
            :dropdown="this.commonShit.dropdownToEdit"
        />
        <b-modal ref="saveChangesModal" id="saveChangesModal" content-class="darkModal" footer-class="centered" header-text-variant="light" 
            size="sm" centered ok-variant="success"
            :title="$t('common.saveChanges')" :ok-title="$t('common.saveChangesYes')" :cancel-title="$t('common.saveChangesNo')"
        >
          <h4 class="my-4 text-center">{{ $t('common.unsavedChanges') }}</h4>
        </b-modal>
    </div>
</template>

<script>
import Vue from 'vue';
import vSelect from 'vue-select';
import Widget from '@/components/Widget/Widget';
import DelConfirmModal from "@/components/Custom/DelConfirmModal.vue"
import DropdownEditModal from "@/components/Custom/DropdownEditModal.vue"
import ActionButtons from "@/components/Custom/ActionButtons.vue"
import ExportToExcelButton from "@/components/Custom/ExportToExcelButton.vue"
// import { vueTableData, vueDatatableData } from '';
import axios from 'axios';
import { commonMixin } from "@/core/commonMixin";
import { axiosMixin } from "@/core/axiosMixin";
import { doubleClickMixin } from "@/core/doubleClickMixin";
import { saveChangesMixin } from "@/core/saveChangesMixin";
import { helpersMixin } from "@/core/helpersMixin";
import { mapGetters } from 'vuex';
import { mapActions } from 'vuex';
// import vSelect from '@/components/vue-select';
export default {
    components: { Widget, vSelect , DelConfirmModal, DropdownEditModal, ActionButtons, ExportToExcelButton},
    mixins: [commonMixin, axiosMixin, doubleClickMixin, saveChangesMixin, helpersMixin],
    data: function() {
        return {
            commonShit: {
                pageMainApi: 'equipment',
                // rest in commonMixin
            },
            
            form: {
                equipName: '',
                equipSerialNum: '',
                equipWarrantyData: '',
                equipDesc: '',
                equipTypeId: null,
                equipPrice: ''
            },
            tableKey: 0,
            equipmentTypes: [],
            temp: [],
            // validationOn: true,
            // activeTab: 0,
            columns: ['equipName', 'equipSerialNum', 'equipWarrantyData', 'equipDesc', 'equipTypeWord'],
           
            options: {
                filterByColumn: true,
                perPage: 10,
                perPageValues: [],
                pagination: { chunk: 10, dropdown: false },
                skin: 'table table-striped table-hover',
                headings: {
                    equipName: 'Name',
                    equipSerialNum: 'Serial number',
                    equipWarrantyData: 'Warranty',
                    equipDesc: 'Description',
                    equipTypeWord: 'Type',
                    actions: ''

                },
                filterable: ['equipName', 'equipSerialNum', 'equipWarrantyData', 'equipDesc', 'equipTypeWord'],
                sortable: ['equipName', 'equipSerialNum', 'equipWarrantyData', 'equipDesc', 'equipTypeWord'],
        // filterable: ['name', 'lastname'],
            },
            
        }
        
    },
// ============================================================= VUEX - CACHE  =========================================
    computed: {
        ...mapGetters([
            'cacheExists',
            'getCache',
            'cacheItemsCount',
            'cacheLastUpdate',
            'cacheGetRowById'
        ])
    },
    
    methods: {
        ...mapActions([
            'cacheReset',
            'cacheFill',
            'cacheAddItem',
            'cacheDeleteItem',
            'cacheUpdateItem',
            'cacheSetUpdatedDate',
            'cacheUpdate'
      ]),
// ============================================================= ACTIVATING =========================================
    editRow(row) {
            var mainTableIndex;
            this.filteredList = this.$refs.mainTable.allFilteredData;
            // console.log(this.filteredList);
            this.commonShit.activeRowIndex = this.filteredList.findIndex(x => x.id === row.row.id); // not supported in IE
            mainTableIndex = this.mainList.findIndex(x => x.id === this.filteredList[this.commonShit.activeRowIndex].id);
            this.rowIsActivated(this.mainList, mainTableIndex);
            if (this.isClickedTwice(row.row.id)) {
                this.execDoubleClick(row, 1); // doubleClickMixin
                return;
            }
            this.rowClicked(row.row.id); // doubleClickMixin
        },
    nextRow(table) {
        var mainTableIndex;
        if (this.commonShit.activeRowIndex !== null && this.commonShit.activeRowIndex !== undefined) {
            this.commonShit.activeRowIndex++;
            if (this.commonShit.activeRowIndex >= this.filteredList.length) this.commonShit.activeRowIndex = this.filteredList.length-1;
            else {
                mainTableIndex = table.findIndex(x => x.id === this.filteredList[this.commonShit.activeRowIndex].id);
                this.rowIsActivated(table, mainTableIndex);
            }
            
        }
    },
    previousRow(table) {
        var mainTableIndex;
        if (this.commonShit.activeRowIndex !== null && this.commonShit.activeRowIndex !== undefined) {
            this.commonShit.activeRowIndex--;
            if (this.commonShit.activeRowIndex < 0) this.commonShit.activeRowIndex = 0;
            {
                mainTableIndex = table.findIndex(x => x.id === this.filteredList[this.commonShit.activeRowIndex].id);
                this.rowIsActivated(table, mainTableIndex);
            }
        }
    },
    rowIsActivated(table, index) {
        this.clearForm();
        var row = table[index];
        this.commonShit.activeRow = JSON.parse(JSON.stringify(row));
        this.commonShit.editMode = true;
        this.commonShit.activeRowId = row.id;
        this.detailsTabEnabled = true;
        
        this.formFill(row);
        this.formCloneCreate(this.form, 100);
        this.deactivateAllRows(table);
        this.activateRow(table[index]);
        this.setActiveMessage(row);
        // specific
        
    },
// ============================================================= DELETING =========================================
    async deleteRow(rowId) {
        this.commonShit.delConfirmShow = false;
        // debugger;

        let path = this.commonShit.pageMainApi + '/' + rowId;
        // console.log(row.id);
        var result = await this.axiosDeleteData(path);
        if (this.axeman.success) {
            this.cacheDeleteItem({ name: this.commonShit.pageMainApi, id: rowId, updateDate: this.axeman.updated })
            Messenger().post(this.$t(this.commonShit.pageMainApi + ".deleted"))
        } else {
            Messenger().post({ message: this.axeman.error, type: 'error' , showCloseButton: true});
        }
        this.commonShit.editMode = false;
        this.clearForm();
    },
// ============================================================= UPDATING =========================================
      
        onUpdateSubmit() {
            if (this.commonShit.validationOn) {
                // console.log('validating');
                var result = this.$validator.validateAll()
                .then(valid => {
                if (valid) { 
                    this.updateItem(); 
                } else 
                    console.log('%cValidation failed...', 'color:red;');
                return false;
                })
                .catch(() => { return false; });
            }
            else {
                this.updateItem();
            } 
        },
        async updateItem() {
        console.log('ID: ' + this.commonShit.activeRowId);

        var data = this.mapFormData();
        var path = this.commonShit.pageMainApi + "/" + this.commonShit.activeRowId; 

        var result = await this.axiosUpdateData(path, data);
        if (this.axeman.success == true) {
            this.setActiveMessage(data);
            Messenger().post(this.$t(this.commonShit.pageMainApi + '.updated') + ': ' + this.commonShit.activeRowMessage);
            data.id = this.commonShit.activeRowId;
            this.cacheUpdateItem({ name: this.commonShit.pageMainApi, data: data, updateDate: this.axeman.updated });
            this.refreshMainTable();
            this.refreshTablePage(); // double click mixin
            this.formCloneCreate(this.form, 100);
            // this.commonShit.editMode = false;
            // this.commonShit.activeRowId = '';
            // this.clearForm();
      } else {
            console.log(this.axeman.error);
            Messenger().post({ message: this.axeman.error, type: "error" });
      }
    },
// ============================================================= ADDING =========================================
        // showAddNew() {
        //     this.clearForm();
        //     this.activeTab = 1;
        // },
        onSubmit() {
            if (this.commonShit.validationOn) {
                var result = this.$validator.validateAll()
                .then(valid => {
                if (valid) { 
                    this.addNewItem(); 
                }
                return false;
                })
                .catch(() => { return false; });
            }
            else {
                this.addNewItem();
            } 
        },

        async addNewItem() {
            var newItem = this.mapFormData(); // equipment
            var result = await this.axiosAddData(this.commonShit.pageMainApi, newItem);
            if (this.axeman.success) {
                Messenger().post(this.$t(this.commonShit.pageMainApi + '.added'));
                // TODO: Cache (potrzebny zwrot ID z bazy po dodaniu rekordu)
                // this.cacheAddItem({ name: this.commonShit.pageMainApi, data: newItem, updateDate: this.axeman.updated});
                this.refreshMainTable();
                this.clearForm();
            }
            else {
                console.log(this.axeman.error);
                Messenger().post({ message: this.axeman.error, type: "error" });
            }
            this.axemanReset();
        },
// ============================================================= REFRESHING LIST =========================================
        async refreshMainTable() {
            var result = await this.axiosGetLastUpdate(this.commonShit.pageMainApi, this.axeman);
            if (!this.cacheExists(this.commonShit.pageMainApi)) {
                // console.log('Cache not created...');
                result = await this.axiosGetData(this.commonShit.pageMainApi, this.axeman);
                // caching
                if (this.axeman.success == true)
                    this.cacheFill({name: this.commonShit.pageMainApi, data: this.axeman.gotFromApi, updateDate: this.axeman.updated});
                // this.employeeList = this.getCache(this.commonShit.pageMainApi);  
            } else if (this.cacheLastUpdate(this.commonShit.pageMainApi) !=  this.axeman.updated) {
                console.log('Cache updated: ' + this.cacheLastUpdate(this.commonShit.pageMainApi));
                // update cache
                result = await this.axiosGetUpdatedData(this.commonShit.pageMainApi, this.cacheLastUpdate(this.commonShit.pageMainApi), this.axeman);
                if (this.axeman.success == true) {
                // console.log(this.axeman.gotFromApi);
                this.cacheUpdate({name: this.commonShit.pageMainApi, data: this.axeman.gotFromApi, updateDate: this.axeman.updated});
                }
            } else {
                console.log('Cache updated: ' + this.cacheLastUpdate(this.commonShit.pageMainApi));
                console.log('Using cached data...');
                // this.employeeList = this.getCache(this.commonShit.pageMainApi);
            }
            this.mainList = this.formatDataForListing(this.getCache(this.commonShit.pageMainApi));
            if (!this.commonShit.editMode) this.deactivateAllRows(this.mainList);
            this.axemanReset();
        },
        
        
        // async refreshMainTable() {
        //     console.log('= main list');
        //     var result = await this.axiosGetLastUpdate(this.commonShit.pageMainApi, this.axeman);
        //     if (!this.cacheExists(this.commonShit.pageMainApi)) {
        //         result = await this.axiosGetData(this.commonShit.pageMainApi, this.axeman);
        //         // caching
        //         if (this.axeman.success == true)
        //             this.cacheFill({name: this.commonShit.pageMainApi, data: this.axeman.gotFromApi, updateDate: this.axeman.updated});  
        //     } else if (this.cacheLastUpdate(this.commonShit.pageMainApi) !=  this.axeman.updated) {
        //         console.log('Cache(' + this.commonShit.pageMainApi + ') update: ' + this.cacheLastUpdate(this.commonShit.pageMainApi));
        //         // update cache
        //         result = await this.axiosGetUpdatedData(this.commonShit.pageMainApi, this.cacheLastUpdate(this.commonShit.pageMainApi), this.axeman);
        //         if (this.axeman.success == true) {
        //         // console.log(this.axeman.gotFromApi);
        //         this.cacheUpdate({name: this.commonShit.pageMainApi, data: this.axeman.gotFromApi, updateDate: this.axeman.updated});
        //         }
        //     } else {
        //         console.log('Cache(' + this.commonShit.pageMainApi + ') update: ' + this.cacheLastUpdate(this.commonShit.pageMainApi));
        //     }
        //     this.mainList = this.formatDataForListing(this.getCache(this.commonShit.pageMainApi));
        //     this.axemanReset();
             
        // },
        formatDataForListing(data) {
            var index;
            
            for (var i = 0; i < data.length; i++) {
                data[i].equipWarrantyData = data[i].equipWarrantyData.split('T')[0];
                index = this.equipmentTypes.findIndex(x => x.rowid === data[i].equipTypeId);
                data[i].equipTypeWord = this.equipmentTypes[index].key_Code;
                data[i].a
            }
            return data;
        },
        async refreshType(typeName) {
            var result = await this.axiosGetLastUpdate(typeName, this.axeman);
            if (!this.cacheExists(typeName) || this.cacheLastUpdate(typeName) !=  this.axeman.updated) {
                result = await this.axiosGetData(typeName, this.axeman);
                if (this.axeman.success == true)
                    this.cacheFill({name: typeName, data:  this.axeman.gotFromApi, updateDate: this.axeman.updated});
            } 
            this.temp = this.getCache(typeName);
            this.axemanReset();
        },     
// ============================================================= FORM HANDLING =========================================
        clearForm(e) {
            if (e !== undefined) e.preventDefault();   
            for (var prop in this.form){
                if(this.form.hasOwnProperty(prop)){
                    this.form[prop] = '';
                }
            }
            this.commonShit.editedRowId = '',
            this.commonShit.editMode = false;
            this.deactivateAllRows(this.mainList);
            this.formCloneClear();
            this.$validator.reset();
            // form specific
            this.form.equipTypeId = null;
        },
        formFill(data) {
            for (var prop in this.form){
                if(this.form.hasOwnProperty(prop) && data.hasOwnProperty(prop)){
                    this.form[prop] = data[prop];
                }
            }
        },
        mapFormData() {
            this.form.equipName = this.capitalizeFirstLetter(this.form.equipName);
            this.form.equipDesc = this.capitalizeFirstLetter(this.form.equipDesc);
            var data = JSON.parse(JSON.stringify(this.form));
            // form specific
            //delete data.cost;
            data.equipWarrantyData += 'T00:00:00';
            return data;
        },
        // formCloneCreate(sourceForm) {
        //     this.formCloneClear();
        //     this.commonShit.formClone = JSON.parse(JSON.stringify(sourceForm));
        // },
        // formCloneClear() {
        //    this.commonShit.formClone = null;
        //    this.commonShit.formChangesString = ''; 
        // },
        // formCloneCompare(sourceForm) {
        //     var noChanges = true;
        //     for (var attr in sourceForm) {
        //         if (this.commonShit.formClone[attr] != sourceForm[attr]) {
        //             this.commonShit.formChangesString += this.commonShit.formClone[attr] + " / " + sourceForm[attr] + "\n";
        //             noChanges = false;
        //         }
        //     }
        //     return noChanges;
        // },
// ============================================================= MODALS =========================================      
        showDelConfirm(row) {
            this.commonShit.delConfirmShow = true;
            this.commonShit.delModalMessage = this.delCustomMessage(row);
        },
        hideDelConfirm() {
            this.commonShit.delConfirmShow = false;
            this.commonShit.delModalMessage = '';
        },
        showDropdownEditor(dropdown) {
                this.commonShit.dropdownToEdit = dropdown;
                this.commonShit.dropdownEditShow = true;
        },
        hideDropdownEditor() {
            this.commonShit.dropdownEditShow = false;
            this.commonShit.dropdownToEdit = '';
            this.refreshTypesFromCache();
        },
        refreshTypesFromCache() {
            this.temp = this.getCache("equipmentType");
            this.equipmentTypes = JSON.parse(JSON.stringify(this.temp));
            this.formatDataForListing(this.mainList);
            this.refreshTablePage();
        },

        // form specific
        
// ============================================================= HEADINGS =========================================
        setTableHeadings() {
            this.options.headings.equipName = this.$t("tableHeadings.thingName");
            this.options.headings.equipSerialNum = this.$t("tableHeadings.serial");
            this.options.headings.equipWarrantyData = this.$t("tableHeadings.warranty");
            this.options.headings.equipDesc = this.$t("tableHeadings.description");
            this.options.headings.equipTypeWord = this.$t("tableHeadings.category");
        },
// ============================================================= PAGE SPECIFIC =========================================
        saveChangesCondition() {
        return (this.commonShit.editMode && this.formCloneCompare(this.form) == false);
        },
        dontSaveChanges() {
            this.formReverseChanges(this.form);
            //this.formCloneCreate(this.form, 0);
        },
        setActiveMessage(row) {  
            this.commonShit.activeRowMessage = row.equipName; // + ' [' + row.equipTypeWord + ']';
        },
        delCustomMessage(row) {
            return row.equipName;
        },
    },
// ============================================================= LIFECYCLE HOOKS =========================================  
    async created () {
        //debugger;
        // console.log('%c====  ' + this.commonShit.pageMainApi + '  ====', 'font-weight: bold; background-color: black; color:lightblue;');
        //   let route = this.$route.params.id;
        
        axios.defaults.headers.common["Authorization"] = "Bearer " + localStorage.token;

        var result = await this.refreshType("equipmentType");
        this.equipmentTypes = JSON.parse(JSON.stringify(this.temp));
        
        this.refreshMainTable();
    },

    updated() {
        this.setTableHeadings();
    },
}
</script>

