<template>
    <div>
        <b-breadcrumb class="testTop">
            <b-breadcrumb-item>{{ $t('common.youAreHere').toUpperCase() }}</b-breadcrumb-item>
            <b-breadcrumb-item active>
                {{$t('menuWarehouseItemList')}}
            </b-breadcrumb-item>
        </b-breadcrumb>
        <!-- <p v-if="$route.params.id">{{ $route.params.id }}</p> -->
        <b-row>
            <b-col lg="12" xs="12">
                <Widget title=""
                        collapse close customHeader>

                    <div class="widget-middle-overflow">
                        <v-client-table id="assortmentTable"
                        :data="assortmentList"
                        :columns="columns"
                        :options="options"
                        @row-click="edit"
                        ref="myTable"
                        >
                            <span slot="actions" slot-scope="{row}">
                                <b-button
                                @click="showDelConfirm(row)"
                                type="button"
                                class="btn btn-square btn-danger"
                                >{{ $t('common.deleteSomething') }}</b-button>
                            </span>
                        </v-client-table>
                    </div>
                </Widget>
            </b-col>
        </b-row>
        <del-confirm-modal @delConfirmed="remove" @delCancelled="hideDelConfirm"
            :showMe="this.commonShit.modalShow"
            :delWhat="$t('assortment.delete')"
            :message="this.commonShit.modalMessage" 
            :rowId="this.commonShit.toDelete.id"
        />
    </div>
</template>

<script>
    import Widget from '@/components/Widget/Widget';
    import DelConfirmModal from "@/components/Custom/DelConfirmModal.vue"
    // import { vueTableData, vueDatatableData } from './data';
    import axios from "axios";
    import { axiosMixin } from "@/core/axiosMixin";
    import { mapGetters } from 'vuex';
    import { mapActions } from 'vuex';

    export default {
        name: 'WarehouseList',
        mixins: [axiosMixin],
        components: { Widget, DelConfirmModal },
        data() {
            return {
                commonShit: {
                    pageMainApi: 'assortment',
                    editMode: false,
                    editedRowId: '',
                    toDelete: '',
                    modalShow: false,
                    modalMessage: '',
                    formClone: '',
                    formChangesString: "",
                },
                assortmentList: [],
                columns: [ 'assortName', 'assortDesc', 'assortPrescription', 'assortTypId', 'actions'],
                options: {
                    filterByColumn: true,
                    perPage: 10,
                    perPageValues: [],
                    pagination: { chunk: 10, dropdown: false },
                    skin: "table table-striped",
                    headings: {
                    assortName: "Assortment",
                    assortDesc: "Description",
                    assortPrescription: "Prescription",
                    assortTypId: "Type",
                    actions: ""
                    },
                filterable: [ 'assortName', 'assortDesc', 'assortPrescription', 'assortTypId'],
                sortable: [ 'assortName', 'assortDesc', 'assortPrescription', 'assortTypId']
                }
            };
        },
        methods: {
            edit(e) {

            },
            async remove(row) {
                this.commonShit.toDelete = "";
                this.commonShit.modalShow = false;
                // debugger;

                let path = this.commonShit.pageMainApi + '/' + row.id;
                // console.log(row.id);
                var result = await this.axiosDeleteData(path);
                if (this.axeman.success) {
                    this.cacheDeleteItem({ name: this.commonShit.pageMainApi, id: rowId, updateDate: this.axeman.updated});
                    Messenger().post(this.$t("assortment.deleted"))
                } else {
                    Messenger().post({ message: this.axeman.error, type: 'error' , showCloseButton: true});
                }
                    this.commonShit.editMode = false;
                    this.onClear();
            },
            async refreshAssortmentTable() {
                var result = await this.axiosGetData(this.commonShit.pageMainApi, this.axeman);
                if (this.axeman.success == true) {
                    // for (var i = 0; i < this.axeman.gotFromApi.length; i++) {
                    //     this.axeman.gotFromApi[i].equipWarrantyData = this.axeman.gotFromApi[i].equipWarrantyData.split('T')[0];
                    // }
                    this.assortmentList = this.axeman.gotFromApi;
                }
            },
            showDelConfirm(row) {
                this.commonShit.toDelete = row;
                this.commonShit.modalShow = true;
                this.commonShit.modalMessage = row.assortName;
            },
            hideDelConfirm() {
                this.commonShit.modalShow = false;
                this.commonShit.modalMessage = '';
            },
            setTableHeadings() {
                this.options.headings.assortName = this.$t("assortment.name");
                this.options.headings.assortDesc = this.$t("assortment.description");
                this.options.headings.assortPrescription = this.$t("assortment.prescription");
                this.options.headings.assortTypId = this.$t("assortment.type");
            },
        },
        created() {
            console.log('=== ' + this.commonShit.pageMainApi + ' ===');
            // let route = this.$route.params.id;
            // if (route !== undefined  && route !== undefined ){

            axios.defaults.headers.common["Authorization"] = "Bearer " + localStorage.token;
            this.refreshAssortmentTable();
        },
        updated() {
            this.setTableHeadings();
        },
    };
</script>

<style src="./WarehouseList.scss" lang="scss" scoped />