                                  </ExportToExcelButton>
                                </span>
                                <v-client-table :data="mainList" :columns="columns" :options="options" ref="mainTable" @row-click="editRow">
                                <!-- <span slot="actions" slot-scope="{row}">
                                    <b-button @click="editRow({row})"
                                    type="button" class="btn btn-square btn-warning">
                                        {{ $t('common.edit') }}
                                    </b-button>&nbsp;
                                    <b-button @click="showDelConfirm(row)"
                                    type="button" class="btn btn-square btn-danger">
                                        {{ $t('common.deleteSomething') }}
                                    </b-button>
                                </span> -->
                                </v-client-table>
                            </div>
                        </b-tab>
                        <b-tab :title="$t(this.commonShit.pageMainApi + '.details')" :disabled="!detailsTabEnabled ">
                            <button @click="commonShit.validationOn = !commonShit.validationOn">Validation: {{ commonShit.validationOn }}</button>
<!-- ============================================================= MAIN FORM ======================================================================== -->
                        <b-form>
                            <b-row>
                                <b-col lg="6" xs="12">
                                    <b-form-group :label="$t('menuCustomerCreateFirstName')"
                                                  label-for="firstname"
                                                  description="">
                                            <b-form-input id="firstname" name="firstname" type="text" 
