                                                v-model="customer.custCountry"
                                                index="code"
                                                label="label"
                                                :options="this.countries"
                                        />
                                        <span class="validation-error" v-show="errors.has('country')">{{ errors.first('country') }}</span>
                                    </b-form-group>
                                    <b-form-group :label="$t('headings.province')" label-for="province">
