                                <b-button @click="editRow({row})"
                                type="button" class="btn btn-square btn-warning">
                                    {{ $t('common.edit') }}
                                </b-button>&nbsp;
                                <b-button @click="showDelConfirm(row)"
                                type="button" class="btn btn-square btn-danger">
                                    {{ $t('common.deleteSomething') }}
                                </b-button>
                            </span> -->
                        </v-client-table>
                    </b-tab>
                    <b-tab :title="$t(this.commonShit.pageMainApi + '.reservationHistory')" :disabled="!commonShit.editMode">
                        <v-client-table :data="mockupList" :columns="mockupColumns" :options="mockupOptions" ref="mockupTable1">
                        </v-client-table>
                    </b-tab>
