                        <v-client-table :data="invoicesList" :columns="invoicesColumns" :options="invoicesOptions" ref="invoicesTable" @row-click="invoiceClick">
                        </v-client-table>
                    </b-tab> 
                    <b-tab :title="$t(this.commonShit.pageMainApi + '.permissions')" :disabled="!commonShit.editMode">
                    <p>
                        <!-- <em>{{ $t('customer.singular') }}: <span class="text-info">{{customerName}}</span></em> -->
                    </p>
                <!-- <tab-content :title="$t('menuCustomerCreatePermitTab')"> -->
                    <b-form>
                    <fieldset>
                        <b-form-group class="abc-checkbox abc-checkbox-primary">
                            <div class="validation-error" v-show="errors.has('agree1')">{{ errors.first('agree1') }}</div>
                            <input type="checkbox" name="agree1" id="checkbox-primary"
                                    v-model="customer.custAgree1" 
                                    v-validate="''" data-vv-as="Agreement"
                            />
                            <label for="checkbox-primary" style="font-size: 120%;">{{$t('customer.agreeTitle1')}}</label>
                            &nbsp;
                            <span class="primary readMore" variant="outline-info" @click="agreeNumber = 1; agreeDetailsShow = true;">{{$t('common.details')}}</span> 
                        </b-form-group>
                        <b-form-group class="abc-checkbox abc-checkbox-primary">
                            <input type="checkbox"
                                id="checkbox-primary1"
                                v-model="customer.custAgree2" />
                            <label for="checkbox-primary1">{{$t('customer.agreeTitle2')}}</label>
                            &nbsp;
                            <span class="primary readMore" variant="outline-info" @click="agreeNumber = 2; agreeDetailsShow = true;">{{$t('common.details')}}</span> 
                        </b-form-group>
                        <b-form-group class="abc-checkbox abc-checkbox-primary">
                            <input type="checkbox"
                                id="checkbox-primary2"
                                v-model="customer.custAgree3" />
                            <label for="checkbox-primary2">{{$t('customer.agreeTitle3')}}</label>
                            &nbsp;
                            <span class="primary readMore" variant="outline-info" @click="agreeNumber = 3; agreeDetailsShow = true;">{{$t('common.details')}}</span>
                        </b-form-group>
                        <b-form-group class="abc-checkbox abc-checkbox-primary">
                            <input type="checkbox"
                                id="checkbox-primary3"
                                v-model="customer.custAgree4" />
                            <label for="checkbox-primary3">{{$t('customer.agreeTitle4')}}</label>
                            &nbsp;
                            <span class="primary readMore" variant="outline-info" @click="agreeNumber = 4; agreeDetailsShow = true;">{{$t('common.details')}}</span>
                        </b-form-group>
                        <b-form-group class="abc-checkbox abc-checkbox-primary">
                            <input type="checkbox"
                                id="checkbox-primary4"
                                v-model="customer.custAgree5" />
                            <label for="checkbox-primary4">{{$t('customer.agreeTitle5')}}</label>
                            &nbsp;
                            <span class="primary readMore" variant="outline-info" @click="agreeNumber = 5; agreeDetailsShow = true;">{{$t('common.details')}}</span>
                        </b-form-group>
                    </fieldset>
                    </b-form>
                <!-- </tab-content> -->
            <!-- </form-wizard> -->
                        </b-tab>
                        <b-tab :title="$t('common.attachments')" :disabled="!commonShit.editMode">
                        </b-tab>
                    </b-tabs>
                </Widget>
            </b-col>
        </b-row>
<!-- ============================================================= MODALS ======================================================================== -->
        <del-confirm-modal @delConfirmed="deleteRow" @delCancelled="hideDelConfirm"
            :showMe="this.commonShit.delConfirmShow"
            :delWhat="$t(this.commonShit.pageMainApi + '.delete')"
            :message="this.commonShit.delModalMessage" 
            :rowId="this.commonShit.activeRowId"
        />
        <dropdown-edit-modal @closed="hideDropdownEditor"
            :showMe="this.commonShit.dropdownEditShow" 
            :dropdown="this.commonShit.dropdownToEdit"
        />
        <b-modal ref="saveChangesModal" id="saveChangesModal" content-class="darkModal" footer-class="centered" header-text-variant="light" 
            size="sm" centered ok-variant="success"
            :title="$t('common.saveChanges')" :ok-title="$t('common.saveChangesYes')" :cancel-title="$t('common.saveChangesNo')"
        >
          <h4 class="my-4 text-center">{{ $t('common.unsavedChanges') }}</h4>
        </b-modal>
        <b-modal ref="addAnimalModal" id="addAnimalModal" 
            content-class="darkModal" footer-class="centered" header-text-variant="light" 
            size="lg" centered ok-variant="success"
            :title="$t('customer.addAnimal')" no-close-on-backdrop hide-header-close
            v-model="addAnimalModalShow"
        >
        <template slot="modal-footer">
        <b-row>
        <b-col cols="auto">
          <b-button @click="onAnimalSubmit" variant="primary">{{$t('customer.addAnimal')}}</b-button>
        </b-col>
        <b-col cols="auto">
            <b-button @click="addAnimalClose" variant="secondary">{{$t('common.close')}}</b-button>
        </b-col>
      </b-row>
        </template>
        <h5 class="my-2 text-center" style="background-color: rgba(0,0,0,0.2); padding: 0.5em;">{{this.$t('customer.singular') }}: <span class="text-info">{{ commonShit.activeRowMessage }}</span></h5>
          <br>
          <b-form>
                <b-row>
                    <b-col lg="3" xs="12">
                    
                        <b-form-group :label="$t('menuPatientCreateName')" label-for="patientName" description="">
                            <b-form-input id="patientName" type="text" name="petname" :placeholder="$t('placeholders.petName')"
                                    :class="{'form-control': true, 'is-invalid': errors.has('step2.petname')}"
                                    v-validate="'required'" data-vv-scope="step2" data-vv-as="Petname"
                                    v-model="animal.animName"  
                            />
                            <span class="validation-error" v-show="errors.has('step2.petname')">{{ errors.first('step2.petname') }}</span>
                        </b-form-group>
                    </b-col>
                    <b-col lg="3" xs="12">
                        <b-form-group :label="$t('menuPatientCreateChip')" label-for="patientChip" description="">
                            <b-form-input id="patientChip" name="patientChip" :placeholder="$t('placeholders.chip')"
                                    type="text"
                                    :class="{'form-control': true, 'is-invalid': errors.has('step2.patientChip')}"
                                    v-validate="'numeric'" data-vv-scope="step2" data-vv-as="Chip"
                                    v-model="animal.animChip" 
                            />
                            <span class="validation-error" v-show="errors.has('step2.patientChip')">{{ errors.first('step2.patientChip') }}</span>
                        </b-form-group>
                    </b-col>
                    <b-col lg="3" xs="12">
                        <b-form-group :label="$t('tableHeadings.dateOfBirth')" label-for="patientDateOfBirth" description="">
                            <b-form-input id="patientDateOfBirth" name="patientDateOfBirth" 
                                    type="date"
                                    :class="{'form-control': true, 'is-invalid': errors.has('step2.patientDateOfBirth')}"
                                    v-validate="''" data-vv-scope="step2" data-vv-as="Birthdate"
                                    v-model="animal.animBirthdate" 
                            />
                            <span class="validation-error" v-show="errors.has('step2.patientDateOfBirth')">{{ errors.first('step2.patientDateOfBirth') }}</span>
                        </b-form-group>
                    </b-col>
                    <b-col lg="3" xs="12">
                        <b-form-group :label="$t('menuPatientCreateWeight')" label-for="patientWeight" description="">
                            <b-form-input id="patientWeight" name="patientWeight" :placeholder="$t('placeholders.weight')" 
                                    type="text"
                                    :class="{'form-control': true, 'is-invalid': errors.has('step2.patientWeight')}"
                                    v-validate="{ decimal: [3, ',']}" data-vv-scope="step2" data-vv-as="Weight"
                                    v-model="animal.weight" 
                            />
                            <span class="validation-error" v-show="errors.has('step2.patientWeight')">{{ errors.first('step2.patientWeight') }}</span>
                        </b-form-group>
                    </b-col>
                </b-row>
                <b-row>
                    <b-col lg="3" xs="12">
                        <b-form-group :label="$t('menuPatientCreateSex')" label-for="patientGender">
                            <v-select searchable
                                        id="patientGender" name="patientGender"
                                        :class="{'ddl-select': true, 'invalid-select': errors.has('step2.patientGender')}"
                                        v-validate="'required'" data-vv-scope="step2" data-vv-as="Gender"
                                        v-model="animal.animSexId"
                                        :options="this.genders"
                                        index="code"
                                        label="label" 
                            />
                            
                            <span class="validation-error" v-show="errors.has('step2.patientGender')">{{ errors.first('step2.patientGender') }}</span>
                        </b-form-group>
                    </b-col>
                    <b-col lg="3" xs="12">
                        <b-form-group :label="$t('tableHeadings.patientColor')" label-for="patientColor" description="">
                            <b-form-input id="patientColor" type="text" name="patientColor" :placeholder="$t('common.empty')"
                                    :class="{'form-control': true, 'is-invalid': errors.has('step2.patientColor')}"
                                    v-validate="'required'" data-vv-scope="step2" data-vv-as="Color"
                                    v-model="animal.animCoat"  
                            />
                            <span class="validation-error" v-show="errors.has('step2.patientColor')">{{ errors.first('step2.patientColor') }}</span>
                        </b-form-group>
                        
                    </b-col>
                    <b-col lg="3" xs="12">
                        <b-form-group :label="$t('menuPatientCreateType')" label-for="patientType">
                            <v-select searchable
                                        @change="speciesChanged"
                                        id="patientType" name="patientType"
                                        :class="{'ddl-select': true, 'invalid-select': errors.has('step2.patientType')}"
                                        v-validate="'required'" data-vv-scope="step2" data-vv-as="Type"
                                        v-model="animal.animSpeciesId"
                                        :options="this.types"
                                        index="code"
                                    label="label"
                            />
                            <!-- <b-button @click="showDropdownEditor('DDL/Types')" class="editButton btn-outline-warning"> 
                                <span class="glyphicon glyphicon-pencil mr-xs mb-xs"></span>
                            </b-button> -->
                            <span class="validation-error" v-show="errors.has('step2.patientType')">{{ errors.first('step2.patientType') }}</span>
                        </b-form-group>
                    </b-col>
                    <b-col lg="3" xs="12">
                        <b-form-group :label="$t('menuPatientCreateRace')" label-for="patientRace">
                            <v-select searchable
                                        id="patientRace" name="patientRace"
                                        :class="{'ddl-select': true, 'invalid-select': errors.has('step2.patientRace')}"
                                        v-validate="'required'" data-vv-scope="step2" data-vv-as="Race"
                                        v-model="animal.animRaceId"
                                        :options="this.races"
                                        index="code"
                                    label="label"
                                        :disabled="this.animal.animSpeciesId == null"
                            />
