  },
// ============================================================= VUEX - CACHE  =========================================
    computed: {
        ...mapGetters([
            'cacheExists',
            'getCache',
            'cacheItemsCount',
            'cacheLastUpdate',
            'cacheGetRowById',
            'getActiveOnArrival',
        ]),
        ...mapState('auth', { lang : state => state.lang, }),
    },

