// ============================================================= REFRESHING LIST =========================================
    async refreshMainTable() {
      var result = await this.axiosGetLastUpdate(this.commonShit.pageMainApi, this.axeman);
// debugger;
    if (!this.cacheExists(this.commonShit.pageMainApi)) {
        // console.log('Cache not created...');
        result = await this.axiosGetData(this.commonShit.pageMainApi, this.axeman);
        // caching
        if (this.axeman.success == true)
          this.cacheFill({name: this.commonShit.pageMainApi, data: this.axeman.gotFromApi, updateDate: this.axeman.updated});
        // this.employeeList = this.getCache(this.commonShit.pageMainApi);  
    } else if (this.cacheLastUpdate(this.commonShit.pageMainApi) !=  this.axeman.updated) {
        console.log('Cache(' + this.commonShit.pageMainApi + ') update: ' + this.cacheLastUpdate(this.commonShit.pageMainApi));
        // update cache
        result = await this.axiosGetUpdatedData(this.commonShit.pageMainApi, this.cacheLastUpdate(this.commonShit.pageMainApi), this.axeman);
        if (this.axeman.success == true) {
          // console.log(this.axeman.gotFromApi);
          this.cacheUpdate({name: this.commonShit.pageMainApi, data: this.axeman.gotFromApi, updateDate: this.axeman.updated});
        }
      } else {
        console.log('Cache(' + this.commonShit.pageMainApi + ') update: ' + this.cacheLastUpdate(this.commonShit.pageMainApi));
        // console.log('Using cached data...');
          // this.employeeList = this.getCache(this.commonShit.pageMainApi);
      }
      this.mainList = this.formatDataForListing(this.getCache(this.commonShit.pageMainApi));
      if (!this.commonShit.editMode) this.deactivateAllRows(this.mainList);
      this.axemanReset();
      if(this.getActiveOnArrival.list == 'customer') {
            console.log(this.getActiveOnArrival.id);
            // console.log(this.filteredList);
            var index = this.mainList.findIndex(x => x.id === this.getActiveOnArrival.id); // not supported in IE
            if (index !== -1) {
                this.filteredList = this.mainList;
                this.commonShit.activeRowIndex = index;
                this.rowIsActivated(this.mainList, index);
                this.activeTab = 1;
            }
            this.resetActiveOnArrival();
        }
    },

    formatDataForListing(data) {
      for (var i = 0; i < data.length; i++) {
          data[i].custAddress = this.formatAddress(data[i].custStreet, data[i].custHouse, data[i].custFlat, 'pl');
        //   (data[i].custAgree1 == true) ? data[i].custAgree1 = this.$t('common.yes') : data[i].custAgree1 = this.$t('common.no');
          if (data[i].custBirthdate !== undefined)
            data[i].custBirthdate = data[i].custBirthdate.split('T')[0];
        }
        return data;
    },
// ============================================================= FORM HANDLING =========================================
    formFill(data) {
        // console.log(this.customer);
        for (var prop in this.customer){
            if(this.customer.hasOwnProperty(prop) && data.hasOwnProperty(prop)){
                this.customer[prop] = data[prop];
            }
        }
    },
    clearForm() {
        // form specific
            this.customer.custFirstname = '';
            this.customer.custLastname = '';
            this.customer.custMail = '';
            this.customer.custPhone = '';
            this.customer.custBirthdate = '';
            this.customer.custStreet = '';
            this.customer.custHouse = '';
            this.customer.custFlat = '';
            this.customer.custCityCode = '';
            this.customer.custCity = '';
            this.customer.custProvince = null;
