        mapFormData() {
            this.customer.custFirstname = this.capitalizeFirstLetter(this.customer.custFirstname);
            this.customer.custLastname = this.capitalizeFirstLetter(this.customer.custLastname);
            this.customer.custStreet = this.capitalizeFirstLetter(this.customer.custStreet);
            this.customer.custCity = this.capitalizeFirstLetter(this.customer.custCity);
            
            this.customer.custAgree1 = true;
            var data = JSON.parse(JSON.stringify(this.customer));
            // console.log(data);
            return data;
        },
        // formCloneCreate(sourceForm, delayMili) {
