      this.options.headings.custCityCode = this.$t('tableHeadings.postalCode');
      this.options.headings.custCity = this.$t('tableHeadings.city');
      this.options.headings.custAgree1 = this.$t('tableHeadings.kidneyAgreed');

        this.animalsOptions.headings.animChip = this.$t("tableHeadings.patientChip");
        this.animalsOptions.headings.animBirthdate = this.$t("tableHeadings.patientBirthdate");
        this.animalsOptions.headings.animName = this.$t("tableHeadings.patientName");
