        this.invoicesOptions.headings.invoiceCompany = this.$t("tableHeadings.patientCastrated");
        this.invoicesOptions.headings.invoiceNumber = this.$t("tableHeadings.invCode");
        this.invoicesOptions.headings.invoiceTotalCost = this.$t("tableHeadings.invCost");
        this.invoicesOptions.headings.invoiceDate = this.$t("tableHeadings.invMethodPayDay");
        this.invoicesOptions.headings.invoiceAnmial = this.$t("tableHeadings.invCustomerAnimalNameAnimal");
        this.invoicesOptions.texts = this.options.texts;
