// ============================================================= PAGE SPECIFIC =========================================
    saveChangesCondition() {
      return (this.commonShit.editMode && this.formCloneCompare(this.customer) == false);
    },
    dontSaveChanges() {
        this.formReverseChanges(this.customer);
        //this.formCloneCreate(this.customer, 0);
    },
    setActiveMessage(row) {  
            this.commonShit.activeRowMessage = row.custFirstname + ' ' + row.custLastname;
    },
    delCustomMessage(row) {
        return row.custFirstname + ' ' + row.custLastname;
    },
    addAnimalShow() {
        this.addAnimalModalShow = true;
    },
    addAnimalClose() {
        this.addAnimalModalShow = false;
        this.animal.animCustomerId = '';
        this.animal.animName = '';
        this.animal.animChip = '';
        this.animal.animBirthdate = '';
        this.animal.weight = '';
        this.animal.animSexId = null;
        this.animal.animSexDesc = '';
        this.animal.animCoat = null;
        this.animal.animSpeciesId = null;
        this.animal.animSpeciesDesc = '';
        this.animal.animRaceId = null;
        this.animal.animRaceDesc = '';
        this.animal.animCastrated = false;
        this.$validator.reset( {scope: 'step2'} );

    },
    mapAnimalData() {
            var data = JSON.parse(JSON.stringify(this.animal));
            data.animCustomerId = this.commonShit.activeRowId;
            return data;
    },
    async onAnimalSubmit() {
        if (this.commonShit.validationOn) {
            var result = await this.$validator.validate('step2.*')
                .then(valid => {
                if (!valid) {
                    console.log('%cValidation failed...', 'color:red;');
                    
                } else {
                    console.log('Validation OK');
                    this.addNewAnimal();
                    // Add animal function
                }
            });
        }
    },
    async addNewAnimal() {
        var newItem = this.mapAnimalData(); // animal
        var result = await this.axiosAddData('animal', newItem);
        if (this.axeman.success) {
            Messenger().post(this.$t('animal' + '.added'));
            // ========== ONLY CACHING, NO SERVER WORKLOAD (ASSUMING NO SIMULTANEOUS EDITING)
            //this.cacheAddItem({ name: 'animal', data: newItem, updateDate: this.axeman.updated});
            // TODO: Cache (potrzebny zwrot ID z bazy po dodaniu rekordu)
            // this.refreshMainTable();
            this.getAnimals();
            this.addAnimalClose();
        }
        else {
            console.log(this.axeman.error);
            Messenger().post({ message: this.axeman.error, type: "error" });
        }
        this.axemanReset();
    },
    async countryChanged() {
        // console.log(this.customer.custCountry);
        if (this.customer.custCountry == null) {
            this.customer.custProvince = null
            this.zones = [];
            // this.$refs.provinceSelect.disabled = true;
        }
        else {
            if (!this.cacheExists("DDL/Province_" + this.customer.custCountry)) {
                var result = await this.axiosGetDataForId("DDL/Province", this.customer.custCountry, this.zones);
                this.zones = this.zones.gotFromApi.model;
                this.cacheFill({name: 'DDL/Province_' + this.customer.custCountry, data:  this.zones, updateDate: '2019-10-10'});
                // this.$refs.provinceSelect.disabled = false;
            }
            else {
                this.zones = this.getCache('DDL/Province_' + this.customer.custCountry);
            }
        }
        // }
    },
    async speciesChanged() {
        if (this.animal.animSpeciesId == null) {
            this.animal.animRaceId = null;
            this.races = [];
            // this.$refs.provinceSelect.disabled = true;
        }
        else {
            if (!this.cacheExists("DDL/AnimalRace_" + this.animal.animSpeciesId)) {
                var result = await this.axiosGetDataForId("DDL/AnimalRace", this.animal.animSpeciesId, this.races);
                this.races = this.races.gotFromApi.model;
                this.cacheFill({name: 'DDL/AnimalRace_' + this.animal.animSpeciesId, data:  this.races, updateDate: '2019-10-10'});
                // this.$refs.provinceSelect.disabled = false;
            }
            else {
                this.races = this.getCache('DDL/AnimalRace_' + this.animal.animSpeciesId);
            }
        }
    },
    async getInvoices() {
        var invoices = {fake: 0};
          if(this.commonShit.activeRowId !== null) {
            var result = await this.axiosGetData('/Invoice/GetInvoiceCustomer/' + this.commonShit.activeRowId, invoices);
            if (this.axeman.success == true) {
                this.invoicesList = invoices.gotFromApi;
                // console.log(this.animalsList);
            } else {
                console.log(this.axeman.error);
            }
        }   
    },
    async getAnimals() {
        var owners = { fake: 0,};
        if(this.commonShit.activeRowId !== null) {
            var result = await this.axiosGetData('customer/' + this.commonShit.activeRowId + '/GetAnimals', owners);
            if (this.axeman.success == true) {
                this.animalsList = this.formatAnimalsForListing(owners.gotFromApi);
                // console.log(this.animalsList);
            } else {
                console.log(this.axeman.error);
            }
        }   
    },
    formatAnimalsForListing(data) {
        // console.log(data);
      for (var i = 0; i < data.length; i++) {
          if (data[i].animBirthdate !== undefined)
            data[i].animBirthdate = data[i].animBirthdate.split('T')[0];
            data[i].animSexDesc = this.getDropdownItemLabel(this.genders, data[i].animSexId);
            data[i].animCastrated ? data[i].animCastrated = this.$t('common.yes') : data[i].animCastrated = this.$t('common.no'); 
        }
        // this.deactivateAllRows(data);
        return data;
    },
        animalClick(row) {
            var theTable = this.animalsList;
            var tableIndex = theTable.findIndex(x => x.id === row.row.id);
            this.tableRowIsActivated(theTable, tableIndex);
            // console.log('click ' + row.row.id);
            if (this.isAnimalClickedTwice(row.row.id)) {
                this.execAnimalDoubleClick(row);
                return;
            }
            
            this.animalRowClicked(row.row.id);
        },
        isAnimalClickedTwice(rowId) {
            return (this.animalDC.rowClickedOnce && this.animalDC.rowId == rowId)
        },
        animalRowClicked(rowId) {
            this.animalDC.rowClickedOnce = true;
            this.animalDC.rowId = rowId;
            var self = this;
            setTimeout(function(){ self.animalDC.rowClickedOnce= false; self.animalDC.rowId = null}, 300);
        },
        execAnimalDoubleClick(row) {
            console.log('Animal: ' + this.animalDC.rowId);
            this.setActiveOnArrival({list: 'animal', id: this.animalDC.rowId});
            this.animalDC.rowClickedOnce = false; 
            this.animalDC.rowId = null
            // console.log(this.getActiveOnArrival);
            this.$router.push('PatientList');
        },
        onVisitClick() {
            this.setActiveOnArrival({list: 'customer', id: this.commonShit.activeRowId});
            this.$router.push('Sales');
        },
        invoiceClick(row) {
            // console.log(row.row);
            var theTable = this.invoicesList;
            var tableIndex = theTable.findIndex(x => x.invoiceId === row.row.invoiceId);
            // console.log(tableIndex);
            this.tableRowIsActivated(theTable, tableIndex);
            // console.log('click ' + row.row.id);
            if (this.isInvoiceClickedTwice(row.row.invoiceId)) {
                this.execInvoiceDoubleClick(row);
                return;
            }
            
            this.invoiceRowClicked(row.row.invoiceId);
        },
        isInvoiceClickedTwice(rowId) {
            return (this.invoiceDC.rowClickedOnce && this.invoiceDC.rowId == rowId)
        },
        invoiceRowClicked(rowId) {
            // console.log('Clicked: ' + rowId);
            this.invoiceDC.rowClickedOnce = true;
            this.invoiceDC.rowId = rowId;
            var self = this;
            setTimeout(function(){ self.invoiceDC.rowClickedOnce= false; self.invoiceDC.rowId = null}, 300);
        },
        execInvoiceDoubleClick(row) {
            // console.log('Invoice: ' + this.invoiceDC.rowId);
            this.setActiveOnArrival({list: 'invoice', id: this.invoiceDC.rowId});
            this.invoiceDC.rowClickedOnce = false; 
            this.invoiceDC.rowId = null
            // console.log(this.getActiveOnArrival);
            this.$router.push('SalesHistory');
        },
        tableRowIsActivated(dataTable, index) {
            var row = dataTable[index];
            // console.log(row);
            this.deactivateAllRows(dataTable);
            this.activateRow(dataTable[index]);
            this.refreshTablePage('animalsTable');
            this.refreshTablePage('invoicesTable');
            
        },
        
  },
// ============================================================= LIFECYCLE HOOKS =========================================  
  async created () {
    // console.log('%c====  ' + this.commonShit.pageMainApi + '  ====', 'font-weight: bold; background-color: black; color:lightblue;');
    axios.defaults.headers.common["Authorization"] = "Bearer " + localStorage.token;
    
    var result = await this.refreshMainTable();
    
    

    // mockup DDLs
    // this.cacheFill({name: 'DDL/Colors', data:  this.mockup.colors, updateDate: '2019-10-10'});
    // this.cacheFill({name: 'DDL/Types', data:  this.mockup.types, updateDate: '2019-10-10'});
    // this.cacheFill({name: 'DDL/Zones', data:  this.mockup.zones, updateDate: '2019-10-10'});
    // this.cacheFill({name: 'DDL/Countries', data:  this.mockup.countries, updateDate: '2019-10-10'});
    // this.colors = this.getCache('DDL/Colors');
    // this.types = this.getCache('DDL/Types');
    // this.zones = this.getCache('DDL/Zones');
    // this.countries = this.getCache('DDL/Countries');

    
    if (!this.cacheExists("DDL/Country")) {
      result = await this.axiosGetData("DDL/Country", this.countries);
      this.countries = this.countries.gotFromApi.model;
      this.cacheFill({name: 'DDL/Country', data:  this.countries, updateDate: '2019-10-10'});
    }
    else {
      this.countries = this.getCache('DDL/Country');
    }
    if (!this.cacheExists("DDL/Plec")) {
      result = await this.axiosGetData("DDL/Plec", this.genders);
      this.genders = this.genders.gotFromApi.model;
      this.cacheFill({name: 'DDL/Plec', data:  this.genders, updateDate: '2019-10-10'});
    }
    else {
      this.genders = this.getCache('DDL/Plec');
    }
    if (!this.cacheExists("DDL/AnimalSpecies")) {
            result = await this.axiosGetData("DDL/AnimalSpecies", this.types);
            this.types = this.types.gotFromApi.model;
            this.cacheFill({name: 'DDL/AnimalSpecies', data:  this.types, updateDate: '2019-10-10'});
        }
        else {
            this.types = this.getCache('DDL/AnimalSpecies');
        }
    
  },

  updated() {
    this.setTableHeadings();
    this.setAnimalTableTexts();
  }
};
