export const saveChangesMixin = {
    methods: {
        showLeaveModal() { this.$refs.saveChangesModal.show() },

        async showSaveChangesModal(table, isNext) {
          
            this.showLeaveModal();
            this.$nextTick(() => {
              // Set up hide handler.
              // Done in next tick to ensure Modal is in DOM
              this.$refs.saveChangesModal.$once('hide', (bvEvt) => {
                if (bvEvt.trigger === 'ok') {
                  // User clicked on built in OK button
                  // So we set up a handler to listen for modal 'hidden' event
                  // and advance to next route once hidden
                  this.$refs.saveChangesModal.$once('hidden', () => { this.saveChanges(table, isNext)});
                } else if (bvEvt.trigger === 'cancel') {
                  // User clicked cancel 
                    this.$refs.saveChangesModal.$once('hidden', () => { this.dontSaveChanges(); (isNext) ? this.nextRow(table) : this.previousRow(table);});
                } else { // or the modal 'x' close button 
                  // (isNext) ? this.nextRow(table) : this.previousRow(table);
                }
                
              })
            })
        },

        async saveChanges(table, isNext) {
          var result = await this.updateItem().then(() => {
            (isNext) ? this.nextRow(table) : this.previousRow(table);
          }); 
          
        },

        nextRowCheck(table) {
          if (this.saveChangesCondition()) {
            this.showSaveChangesModal(table, true);
          }
          else
            this.nextRow(table);
        },
        previousRowCheck(table) {
          if (this.saveChangesCondition()) {
            this.showSaveChangesModal(table, false);
          }
          else
            this.previousRow(table);
        },

        formCloneCreate(sourceForm, delayMili) {
          this.formCloneClear();
          var self = this;
          setTimeout(function(){
              self.commonShit.formClone = JSON.parse(JSON.stringify(sourceForm));
          }, delayMili);
        },
        formReverseChanges(targetForm) {
          for (var attr in targetForm) {
             if (targetForm.hasOwnProperty(attr) && this.commonShit.formClone.hasOwnProperty(attr))
              targetForm[attr] = this.commonShit.formClone[attr];
            }
        },
        formCloneClear() {
          this.commonShit.formClone = null;
          this.commonShit.formChangesString = ''; 
        },
        formCloneCompare(sourceForm) {
            var noChanges = true;
            if (JSON.stringify(sourceForm) !== JSON.stringify(this.commonShit.formClone))
            noChanges = false;
            return noChanges;
        },
    },

    watch: {
      'activeTab'() {
        if (this.activeTab !== 1 && this.saveChangesCondition() ) {
          this.showLeaveModal();
          this.$nextTick(() => {
            // Set up hide handler.
            // Done in next tick to ensure Modal is in DOM
            this.$refs.saveChangesModal.$once('hide', (bvEvt) => {
              if (bvEvt.trigger === 'ok') {
                // User clicked on built in OK button
                // So we set up a handler to listen for modal 'hidden' event
                // and advance to next route once hidden
                this.$refs.saveChangesModal.$once('hidden', () => { this.onUpdateSubmit() })
              } else if (bvEvt.trigger === 'cancel') {
                // User clicked cancel 
                  this.$refs.saveChangesModal.$once('hidden', () => { this.dontSaveChanges()})
              } else // or the modal 'x' close button
                this.activeTab = 1;
            })
          })
        }
      },  
    },

    beforeRouteLeave (to, from, next) {
        if (this.saveChangesCondition())
        {
          this.showLeaveModal();
          this.$nextTick(() => {
            // Set up hide handler.
            // Done in next tick to ensure Modal is in DOM
            this.$refs.saveChangesModal.$once('hide', (bvEvt) => {
              if (bvEvt.trigger === 'ok') {
                // User clicked on built in OK button
                // So we set up a handler to listen for modal 'hidden' event
                // and advance to next route once hidden
                this.$refs.saveChangesModal.$once('hidden', () => { this.onUpdateSubmit(); next() })
              } else if (bvEvt.trigger === 'cancel') {
                // User clicked cancel 
                  this.$refs.saveChangesModal.$once('hidden', () => { next() })
              } else // or the modal 'x' close button
                next(false);
            })
          })
        } else next();
        
      },

}