<template>
  <b-nav-item-dropdown no-caret>
    <template slot="button-content">
      <span class="cursor-pointer flex items-center">
        <img style="max-width: 22px" :src="i18n_locale_img" :alt="$i18n.locale" />
        <span class="ml-2 my-header-nav-item">{{ getCurrentLocaleData.lang }}</span>
      </span>
    </template>
      <b-dropdown-item-button @click="updateLocale('en')"><img  style="max-width: 22px" class="" src="@/assets/flags/en.png" alt="en" /> &nbsp;English</b-dropdown-item-button>
      <b-dropdown-item-button @click="updateLocale('pl')"><img  style="max-width: 22px" src="@/assets/flags/pl.png" alt="pl" /> &nbsp;polski</b-dropdown-item-button>
      <!-- <vs-dropdown-item @click="updateLocale('fr')"><img class="h-4 w-5 mr-1" src="@/assets/images/flags/fr.png" alt="fr" /> &nbsp;French</vs-dropdown-item>
      <vs-dropdown-item @click="updateLocale('de')"><img class="h-4 w-5 mr-1" src="@/assets/images/flags/de.png" alt="de" /> &nbsp;German</vs-dropdown-item>
      <vs-dropdown-item @click="updateLocale('pt')"><img class="h-4 w-5 mr-1" src="@/assets/images/flags/pt.png" alt="pt" /> &nbsp;Portuguese</vs-dropdown-item> -->
  </b-nav-item-dropdown>
</template>

<script>
import { mapActions } from 'vuex';
export default {
  computed: {
    i18n_locale_img () {
      return require(`@/assets/flags/${this.$i18n.locale}.png`)
    },
    getCurrentLocaleData () {
      const locale = this.$i18n.locale
      if (locale === 'en')      return { flag: 'us', lang: 'English'    }
      else if (locale === 'pl') return { flag: 'pl', lang: 'polski'     }
      else if (locale === 'pt') return { flag: 'br', lang: 'Portuguese' }
      else if (locale === 'fr') return { flag: 'fr', lang: 'French'     }
      else if (locale === 'de') return { flag: 'de', lang: 'German'     }
    }
  },
  methods: {
    ...mapActions('auth', ['logout','changeUserLang']),
    updateLocale (locale) {
      this.$i18n.locale = locale
      this.changeUserLang(locale) // our shit
    }
  }
}
</script>
