<template>
  <span v-b-tooltip.hover.top :title="$t('common.exportToExcel')" id="exportToExcel" class="actionButtonContainer" style="position:absolute; bottom: 18px; right: -1px; z-index: 1;">
  <b-button  type="button" v-on:click="delayedExport" variant="outline-info" class="btn-sm">
    <i class="fa fa-file-excel-o" style="font-size: 110%"></i>
  </b-button>
  </span>
</template>

<script>
import Vue from "vue";
import XLSX from 'xlsx'

export default {
  // data: () => ({
    props: {
      theList: Array,
      columns: Array,
      columnNames: Object,
      listName: String,
    },
    data() {
      return {
        Datas: {
          // We will make a Workbook contains 2 Worksheets
          // 'animals': [
          //             {"name": "cat", "category": "animal1"}
          //             ,{"name": "dog", "category": "animal2"}
          //             ,{"name": "pig", "category": "animal3"}
          //           ],
        }
      }
  },
  methods: {
    delayedExport() {
      var self = this;
            setTimeout(self.onexport, 100);
    },
    async onexport () { // On Click Excel download button
      // var keyNames = Object.keys(this.theList[0]);
     
      // var list = this.theList.map(obj => {
      //     var rObj = {}
      //     for (var i = 0;  i < this.columns.length; i++)
      //       rObj[this.columnNames[this.columns[i]]] = obj[this.columns[i]];
      //     return rObj
      //   });

      var headings = [];
      for (var i = 0; i < this.columns.length; i++)
        headings.push(this.columnNames[this.columns[i]]);
      headings = [ headings ];
      
      var list = JSON.stringify(this.theList, this.columns);
      list = JSON.parse(list); 

      var WS = XLSX.utils.aoa_to_sheet(headings);
      XLSX.utils.sheet_add_json(WS, list, {skipHeader: true, origin: "A2"});
      // WS['!cols'] = [];
      // WS['!cols'][1] = { hidden: true };

      // export json to Worksheet of Excel
      // only array possible
      //var WS = XLSX.utils.json_to_sheet(list);
      
      //  console.log(WS);
      // var pokemonWS = XLSX.utils.json_to_sheet(this.Datas.pokemons) 

      // A workbook is the name given to an Excel file
      var wb = XLSX.utils.book_new() // make Workbook of Excel

      // add Worksheet to Workbook
      // Workbook contains one or more worksheets
      XLSX.utils.book_append_sheet(wb, WS, this.listName) // sheetAName is name of Worksheet
      // XLSX.utils.book_append_sheet(wb, pokemonWS, 'pokemons')   

      // export Excel file
      XLSX.writeFile(wb, 'Lista.xlsx') // name of the file is 'book.xlsx'
    }
  }
}
</script>